/* eslint-disable no-nested-ternary */
import React, { useCallback, useContext, useEffect, useMemo, PropsWithChildren } from 'react';
import { SelectNormal } from '~/components/icons';
import { SelectOptionWrap } from './styled';
import { ISelectOptionProps, ISelectValue, TOptionTitle } from './interface';
import { classnames } from '~/utils';
import { Tooltip } from '~/components/tooltip';
import { createSelectContext } from './SelectContext';
import { IPlacement } from '../trigger';

const noopWrapperStyle = {};

export const getTitleContent = (title: TOptionTitle | undefined): string | undefined => {
  if (typeof title === 'string' || !title) {
    return title;
  }
  return title.title;
};

export const getTitlePlacement = (title: TOptionTitle | undefined): IPlacement => {
  if (typeof title === 'string' || !title) {
    return 'right';
  }
  return title.placement;
};

// eslint-disable-next-line max-lines-per-function
const SelectOptionFC = <
VT extends ISelectValue = ISelectValue
>(props: PropsWithChildren<ISelectOptionProps<VT>>) => {
  const {
    children,
    style,
    disabled,
    disabledTip,
    value,
    onSelect,
    testid,
    className,
    title,
  } = props;

  const SelectContext = createSelectContext<VT>();

  const context = useContext(SelectContext);

  if (!context) {
    throw new Error('[we-ui] Select.Option 必须为 Select 的子组件');
  }

  useEffect(() => {
    if (value) {
      context.onUpdateOption(value, children);
    }
    return () => {
      if (value) {
        context.onRemoveOption(value);
      }
    };
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [value]);

  useEffect(() => {
    if (value) {
      context.onCacheOptionDisabled(value, !!disabled);
    }
    return () => {
      if (value) {
        context.onUncacheOptionDisabled(value);
      }
    };
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [value, disabled]);

  // 是否选中
  const selected = context.value === value && value !== undefined;

  // 容器样式
  const optionWrapperStyle = context.optionWrapperStyle || noopWrapperStyle;

  // 输入过程中是否隐藏
  const hidden = context.isSearching && context.showOptionValues.indexOf(value!) === -1;

  // 键盘事件中高亮
  const hovered = context && context.keyboardAt === value && value !== undefined;

  /**
   * 点击选中
   *
   * @return {void}
   */
  const handleClick = useCallback((): void => {
    if (value !== undefined) {
      context.onClickOption({
        value: value!,
        disabled: !!disabled,
      });
    } else if (onSelect) {
      onSelect();
    }
  }, [value, disabled, context, onSelect]);

  // 样式
  const styleCombined = useMemo(() => ({
    ...optionWrapperStyle,
    ...style,
  }), [optionWrapperStyle, style]);

  return hidden ? null : (
    <Tooltip
      title={disabled && disabledTip ? disabledTip : getTitleContent(title)}
      placement={disabled && disabledTip ? 'top' : getTitlePlacement(title)}
    >
      <SelectOptionWrap
        style={styleCombined}
        className={classnames(className, {
          selected,
          disabled,
          hovered,
        })}
        onClick={handleClick}
        data-testid={testid && `option-${testid}`}
      >
        {!!context.selectable && !!selected && <SelectNormal className="check-icon" />}
        {children}
      </SelectOptionWrap>
    </Tooltip>
  );
};

(SelectOptionFC as React.FC).defaultProps = {
  style: {},
};

(SelectOptionFC as React.FC).displayName = 'SelectOption';

const SelectOption = React.memo(SelectOptionFC) as typeof SelectOptionFC;

export { SelectOption };
