/* eslint-disable @typescript-eslint/no-explicit-any */
/* eslint-disable @typescript-eslint/explicit-function-return-type */
import styled from 'styled-components';
import { withTheme } from '../theme';

export const imgPadding = 1;
export const imgMargin = 1;
export const imgBorder = 1;
export const menuImgMargin = { upDown: 4, leftRight: 6 };
export const smallMode = { upper: 26, down: 38 };
export const normalMode = { upper: 38, down: 38 };
export const btnWidth = 12;

export const IconWrapper = withTheme(styled.div<any>`
width: ${(p) => (p.isDown ? '26px' : `${btnWidth}px`)};
height: ${(p) => (p.isDown ? '8px' : `${btnWidth}px`)};
cursor: pointer;
display: flex;
justify-content: center;
align-items: center;
border-radius: ${(p) => (p.isDown ? '2px 2px 0 0' : '50%')};
background-color: ${p => (p.active ? '#eee' : '')};
&:hover {
  background-color: #eee;
}
&:active {
  background-color: #ddd;
}
.weicon {
  color: ${(p) => p.theme.ui.commonBaseColor3};

  &.disabled {
    color: ${(p) => p.theme.ui.commonBaseColor4};
    cursor: not-allowed;
  }
}
`);

export const TableStyleItemWrap = withTheme(styled.div<any>`
  padding: ${(p) => (p.size === 'small' ? `${imgPadding - 1}px` : `${imgPadding}px`)} ;
  margin:${(p) => {
    if (p.footer) {
      return `${menuImgMargin.upDown}px ${menuImgMargin.leftRight}px`;
    } else {
      return `0px ${imgMargin}px`;
    }
  }};
  border: ${imgBorder}px solid transparent;
  width:${(p) => {
    if (p.footer === true) {
      return `${p.mode.down}px`;
    } else {
      return `${p.mode.upper}px`;
    }
  }};
  border-radius:2px;
  &:hover {
   border: ${(p) => `${imgBorder}px solid ${p.theme.ui.commonBaseColor4}`};
  }
  &.actived {
    border: ${imgBorder}px solid #0089FF;
  }

`);
export const TableStyleItem = withTheme(styled.img<any>`
  width:100%;
  cursor: pointer;
  display:block;
`);
export const TableStyleSet = withTheme(styled.div<any>`
display: flex;
justify-content: flex-start;
`);

export const TableStyleDown = styled.div`
display: flex;
justify-content: center;
`;

export const TableStyleWrapper = styled.div<any>`
width: ${p => `${p.width}px`};
padding-top: ${p => (p.mode === 'normal' ? '8px' : '')};
border: ${p => (p.mode === 'normal' ? '1px solid rgba(17,31,44,0.08)' : '')};
border-radius: ${p => (p.mode === 'normal' ? '4px' : '')};
`;

export const TableStyleUpper = styled.div`
display:flex;
align-items:center;
justify-content:space-between;
`;
export const OverlayWrapper = styled.div`
padding:10px 10px;
`;
