import styled, { css } from 'styled-components';
import { resetComponent, optionWrapStyle } from '~/utils/styled.mixin';
import { withTheme } from '../theme';

const directionCss = css<{ direction: 'rtl' | 'ltr' }>`
  ${({ direction }) => {
    if (direction === 'rtl') {
      return 'flex-direction: row-reverse;';
    }
    return '';
  }}
`;

const avatarWidth = css<any>`
  width: ${({ theme, size }) => {
    let sizeString = '';
    if (size === 'small') sizeString = 'Small';
    if (size === 'large') sizeString = 'Large';
    return theme.ui[`elementHeight${sizeString}`];
  }};
`;

const avatarHeight = css<any>`
  height: ${({ theme, size }) => {
    let sizeString = '';
    if (size === 'small') sizeString = 'Small';
    if (size === 'large') sizeString = 'Large';
    return theme.ui[`elementHeight${sizeString}`];
  }};
`;

export const AvatarWrap: any = withTheme(styled.div<any>`
  ${resetComponent}
  display: inline-block;
  position: relative;
  ${avatarWidth};
  ${avatarHeight};
  cursor: pointer;
`);

export const AvatarInner = withTheme(styled.div<any>`
  box-sizing: border-box;
  position: relative;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  overflow: hidden;
  border: 1px solid ${({ theme }) => theme.ui.colorGray1};

  &:hover, &:active{
    border: 1px solid ${({ color, theme }) => (typeof color === 'string' ? color : theme.ui.colorGray1)};
  }
`);

const divider = css`
  content: '';
  display: block;
  position: absolute;
  background-color: ${({ theme }) => theme.ui.colorGray1};
`;

const leftDivider = css`
  ${divider}
  left: 0;
  top: 0;
  bottom: 0;
  width: 1px;
`;

const rightDivider = css`
  ${divider}
  right: 0;
  top: 0;
  bottom: 0;
  width: 1px;
`;

const topDivider = css`
  ${divider}
  right: 0;
  left: 0;
  top: 0;
  height: 1px;
`;

export const AvatarImage = withTheme(styled.div<any>`
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-image: url(${({ url }) => url});
  background-size: cover;
  background-position: center center;

  &.avatar_1_1 {
    border-radius: 50%;
  }
  &.avatar_2_1, &.avatar_3_1 {
    transform: translate(-50%, 0);
  }
  &.avatar_2_2 {
    transform: translate(50%, 0);
    &:after {
      ${leftDivider}
    }
  }
  &.avatar_3_2 {
    transform: translate(50%, -50%);
    &:after {
      ${leftDivider}
    }
  }
  &.avatar_3_3 {
    transform: translate(50%, 50%);
    &:after {
      ${leftDivider}
    }
    &:before {
      ${topDivider}
    }
  }
  &.avatar_4_1 {
    transform: translate(-50%, -50%);
    &:after {
      ${rightDivider}
    }
  }
  &.avatar_4_2 {
    transform: translate(-50%, 50%);
    &:after {
      ${rightDivider}
    }
    &:before {
      ${topDivider}
    }
  }
  &.avatar_4_3 {
    transform: translate(50%, -50%);
  }
  &.avatar_4_4 {
    transform: translate(50%, 50%);
    &:after {
      ${topDivider}
    }
  }
`);

export const AvatarCorner = withTheme(styled.div<any>`
  position: absolute;
  bottom: 0;
  right: 0;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  overflow: hidden;
   background-color: ${({ theme }) => theme.ui.colorGray1};
  border: 1px solid ${({ theme }) => theme.ui.colorGray1};
  box-sizing: border-box;
`);

export const AvatarColor = withTheme(styled.div<any>`
  width: 100%;
  height: 100%;
  background-color: ${({ backgroundColor }) => backgroundColor};
`);

export const NameWrap = withTheme(styled.div<any>`
  width: 100%;
  height: 100%;
  background-color: ${({ theme }) => theme.ui.primaryColor};
  color: ${({ theme }) => theme.ui.colorGray1};
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 14px;
`);

export const TooltipWrap = styled.div`
  display: flex;
  align-items: center;

  // 设计稿上是高宽 7px，这里是按调整后 path 大约 7 * 7 来取的 font-size，可能需要再确认
  .status-icon {
    font-size: 12px;
  }
`;

export const TextSpan = styled.span`
  margin: 0 3px 0 7px;
`;


export const AvatarGroupWrap = withTheme(styled.div<any>`
  display: inline-block;
`);

export const GroupAvatarList = withTheme(styled.ul<any>`
  ${resetComponent};
  ${directionCss};
  width: 100%;
  ${avatarHeight};
  display: flex;
  position: relative;
`);


export const GroupAvatarItem = withTheme(styled.li<any>`
  ${resetComponent};
  ${avatarHeight};
  position: relative;
  flex: 1 auto;
  &.more {
    border: 0;
  }

  &.more:hover .more-item-span {
    background-color: #C6C9CD;
  }
`);

export const GroupMoreItem = withTheme(styled.div<any>`
  box-sizing: border-box;
  ${avatarHeight};
  ${avatarWidth};
  background-color: #E2E4E5;
  border-radius: 50%;
  border: 1px solid ${({ theme }) => theme.ui.colorGray1};
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  i {
    display: block;
    background-color: ${({ theme }) => theme.ui.textColor};
    width: 2px;
    height: 2px;
    margin-right: 2px;
    border-radius: 50%;
    &:last-child {
      margin-right: 0;
    }
  }
`);

export const GroupMoreItemSpan = withTheme(styled.span`
  font-size: 12px;
  transform: scale(.75);
`);

export const OverlayWrap = withTheme(styled.div<any>`
  padding: 10px 0;
  min-width: 100px;
`);

export const CountWrap = withTheme(styled.div<any>`
  font-size: ${({ theme }) => theme.ui.fontSizeSmall};
  color: ${({ theme }) => theme.ui.commonBaseColor3};
  // 行高为 24px / 12px
  line-height: 2;
  padding-left: 6px;
`);

export const OverlayAvatarItem = withTheme(styled.div`
  ${optionWrapStyle}
  justify-content: space-between;
  margin: 0 6px;
  padding: 6px;
  // 同上
  .status-icon {
    font-size: 14px;
  }
`);

export const OverlayAvatarName = withTheme(styled.div<any>`
  margin-left: 7px;
  padding-right: 16px;
`);


export const ListWrap = withTheme(styled.div<any>`
  width: 100%;
  overflow-y: auto;
`);

export const OverlayAvatarWrap = withTheme(styled.div<any>`
  display: flex;
  align-items: center;
`);

export const OverlayAvatarStatus = withTheme(styled.span`
  margin-left: auto;
  color: ${({ theme }) => theme.ui.commonBaseColor3};
`);
