import { useContext, useMemo, useRef, useLayoutEffect, useState, useCallback, useEffect } from 'react';
import { uuid, debounce } from '~/utils';
import { ControllerContext, MenuKeyContext, PathContext, HotkeyPositionContext } from './contexts';
import { IUseMenuKeyContext, ISimpleTreeNode } from './interface';

/**
 * 获取 Controller
 *
 * @param {String} name Component's name
 * @return {Controller | never}
 */
export const useController = (name: string) => {
  const ctrl = useContext(ControllerContext);
  if (!ctrl) {
    throw new Error(`${name} 组件必须为 Menu 组件的子组件`);
  }
  return ctrl;
};


export const useHotkeyController = (name: string) => {
  const ctrl = useContext(HotkeyPositionContext);
  if (!ctrl) {
    throw new Error(`${name} 组件必须为 Menu 组件的子组件`);
  }
  return ctrl;
};

/**
 * 获取当前 key 路径
 * @param {string} key
 * @return {string}
 */
export const usePath = (key: string): string => {
  const parentPath = useContext(PathContext);
  return [...parentPath.split('::'), key].filter(k => k).join('::');
};

/**
 * 处理menukey
 *
 * @param {string} key
 * @return {IUseMenuKeyContext}
 */
export const useMenuKeyContext = (key?: string): IUseMenuKeyContext => {
  const menuKeys = useRef<Record<string, any>>({});
  const ctx = useContext(MenuKeyContext);

  // 获取 key，如果没传，自动生成一个uuid
  const id = useMemo(() => `menu-key-${uuid()}`, []);
  const menuKey = key || id;

  // key的添加和删除方法
  const menuKeyCtxValue = useMemo(() => {
    return {
      addKey: (k: string, keys: Record<string, any>) => {
        menuKeys.current[k] = keys || {};
      },
      delKey: (k: string) => {
        delete menuKeys.current[k];
      },
    };
  }, []);

  if (ctx) {
    ctx.addKey(menuKey, menuKeys.current);
  }

  // 添加和删除
  useLayoutEffect(() => {
    return () => {
      if (ctx) {
        ctx.delKey(menuKey);
      }
    };
  }, [menuKey, ctx]);

  return [menuKeyCtxValue, menuKey, menuKeys.current];
};

// 获取鼠标最后停留位置

export const useMousePosition = () => {
  const [mousePosition, setMousePosition] = useState(0);

  const setMousePositionDebounce = useCallback(debounce((y: number) => {
    setMousePosition(y);
  }, 100), []);

  const handleMouseOver = useCallback((e) => {
    const clientY: number = e.clientY;
    setMousePositionDebounce(clientY);
  }, []);

  return {
    handleMouseOver,
    setMousePosition,
    mousePosition,
  };
};


/**
 * 菜单快捷键 有滚动条是 滚动到合适位置
 */

export const useHotKeyScrollRef = (menuKey: string, isHotkeyType?: boolean, hotkeyPosition?: ISimpleTreeNode | null) => {
  const ref = useRef<HTMLElement>();
  useEffect(() => {
    if (hotkeyPosition && menuKey === hotkeyPosition.menuKey && ref.current && isHotkeyType) {
      if (ref.current.scrollIntoView && typeof ref.current.scrollIntoView === 'function') {
        const { top, position } = ref.current.style;
        ref.current.style.position = 'relative';
        ref.current.style.top = hotkeyPosition.eventKey === 'arrowdown' ? '8px' : '-8px';
        ref.current.scrollIntoView({ block: 'nearest', inline: 'nearest' });
        ref.current.style.top = top;
        ref.current.style.position = position;
      }
    }
  }, [hotkeyPosition, menuKey]);

  return ref;
};


export const useRegisterValidNode = (menuId: string, shouldSelectFirstItem: boolean, openedMenuKeys: string[]) => {
  const eleMap = useRef<{ [id: string]: ISimpleTreeNode }>({});
  const hotKeyPath = useRef<ISimpleTreeNode | null>();
  const validNodes = useRef<ISimpleTreeNode[]>([]);
  const [hotkeyPosition, setHotkeyPosition] = useState<ISimpleTreeNode | null>(null);

  // 获取当前节点挂载的父级节点
  const getParentEle = useCallback((nodes: ISimpleTreeNode[], parentPathSteps: string[]): ISimpleTreeNode | undefined => {
    const el = nodes.find(o => o.menuKey === parentPathSteps[0]);
    if (parentPathSteps.length === 1) {
      return el;
    } else if (el) {
      return getParentEle(el.children || [], parentPathSteps.slice(1));
    }
    return undefined;
  }, []);

  // 根据dom 实际渲染情况，重绘 menuTree 方便快捷键调用
  const getHotkeyPosition = useCallback(debounce((openedKeys: string[]) => {
    const nodes: Element[] = [];
    document.querySelectorAll(`*[data-menuid=${menuId}]`).forEach((o: Element) => nodes.push(o));
    let nodeTree: ISimpleTreeNode[] = [];
    try {
      nodes.sort((a: Element, b: Element): number => {
        const pathAttrA = a.getAttribute('data-path');
        const pathAttrB = b.getAttribute('data-path');
        if (pathAttrA && pathAttrB) {
          return pathAttrA.split('::').length < pathAttrB.split('::').length ? -1 : 1;
        }
        return -1;
      }).forEach((o: Element) => {
        const pathAttr = o.getAttribute('data-path');
        if (pathAttr) {
          const path = pathAttr.split('::');
          const menuKey = path[path.length - 1];
          if (!eleMap.current[menuKey]) return;
          const el = eleMap.current[menuKey];
          if (path.length === 1) {
            nodeTree = nodeTree.concat({ ...el, index: nodeTree.length });
          } else {
            const parentPathSteps = path.slice(0, -1);
            const parentEle = getParentEle(nodeTree, parentPathSteps);
            if (parentEle) {
              const children = parentEle.children || [];
              const elIndex = { ...el, index: children.length };
              parentEle.children = children.concat(elIndex);
              // 判断节点是否是在开一个新的子层级
              const isOpenSubMenu = hotKeyPath.current && hotKeyPath.current.path.split('::').length !== elIndex.path.split('::').length;
              // 高亮subMenu 打开时第一个节点
              if (isOpenSubMenu && parentEle.children.length === 1 && parentPathSteps.join('::') === openedKeys.join('::')) {
                setHotkeyPosition(elIndex);
                hotKeyPath.current = elIndex;
              }
            } else {
              throw new Error(`菜单实例 ${menuId} 子节点 ${path} 找不到父节点 ${parentPathSteps.join('::')}`);
            }
          }
        } else {
          throw new Error(`菜单实例 ${menuId} 存在节点缺少【data-path】属性`);
        }
      });
    } catch (error) {
      throw new Error('菜单实例注册失败');
    }
    validNodes.current = nodeTree;
    // 默认选中第一个节点
    if (shouldSelectFirstItem && !hotkeyPosition && validNodes.current.length > 0) {
      setHotkeyPosition(validNodes.current[0]);
      hotKeyPath.current = validNodes.current[0];
    }
  }, 80), [shouldSelectFirstItem, hotkeyPosition, setHotkeyPosition]);

  const registerValidNode = useCallback((node: ISimpleTreeNode) => {
    if (eleMap.current[node.menuKey]) return;
    // 如果是新增节点触发重绘
    eleMap.current[node.menuKey] = node;
    getHotkeyPosition(openedMenuKeys);
  }, [openedMenuKeys]);

  const unRegisterValidNode = useCallback((node: ISimpleTreeNode) => {
    delete eleMap.current[node.menuKey];
  }, []);

  return { registerValidNode, unRegisterValidNode, hotkeyPosition, setHotkeyPosition, validNodes, hotKeyPath };
};