/* eslint-disable react/no-find-dom-node */
import React, { useCallback, useMemo, useEffect } from 'react';
import ResizeObserver from 'resize-observer-polyfill';
import domAlign, { alignPoint } from 'dom-align';
import { PortalComponent } from '../portal-component';
import { OverlayWrap, OverlayMask } from './styled';
import type { ITriggerProps } from './Trigger';
import { getAlignPointsAndOffsets, debounce } from '~/utils';
import { useClickOuterRef } from '~/hooks';

interface IProps extends Pick<
ITriggerProps,
'getContainer'
| 'overlayStyle'
| 'placement'
| 'zIndex'
| 'overlay'
| 'overlayClassName'
| 'pageX'
| 'pageY'
| 'testid'
> {
  visible: boolean;
  mask: boolean;
  offsetY: number;
  offsetX: number;
  /**
   * trigger节点手动传入
   */
  triggerNode?: HTMLElement;
  onMouseEnter?: React.MouseEventHandler;
  onMouseLeave?: React.MouseEventHandler;
  onMouseDown?: React.MouseEventHandler;
  onClickMask?: React.MouseEventHandler;
  onClickOuter?: any;
}

// eslint-disable-next-line max-lines-per-function
export const Popup: React.FC<IProps> = (props) => {
  const {
    visible,
    getContainer,
    overlayStyle,
    overlay,
    triggerNode,
    placement,
    onMouseEnter,
    onMouseLeave,
    zIndex,
    onClickOuter,
    offsetX,
    offsetY,
    mask,
    onClickMask,
    overlayClassName,
    pageX,
    pageY,
    testid,
  } = props;

  // 容器
  const oPopupContainer = getContainer ? getContainer() : document.body;

  const overlayRef = useClickOuterRef(onClickOuter, visible);


  /**
   * 计算位置
   *
   * @param {HTMLElement} trigger node
   * @return {void}
   */
  const handleAlign = useCallback((oTrigger?: HTMLElement) => {
    if (!overlayRef.current) return;
    const overflow = { adjustX: true, adjustY: true };
    const targetOffset = [0, 0];
    const { points, offsets } = getAlignPointsAndOffsets(placement!, 2);

    if (typeof pageX === 'number' && typeof pageY === 'number') {
      alignPoint(overlayRef.current, { pageX, pageY }, {
        targetOffset, overflow, offset: [offsetX, offsetY], points,
      });
    } else if (oTrigger) {
      domAlign(overlayRef.current, oTrigger, {
        targetOffset,
        overflow,
        points,
        offset: [offsets[0] + offsetX, offsets[1] + offsetY],
      });
    }
    if (testid) {
      overlayRef.current.setAttribute('data-testid', `Popup-${testid || ''}`);
    }
  }, [
    pageX,
    pageY,
    offsetY,
    offsetX,
    placement,
    overlayRef,
    testid,
  ]);

  useEffect(() => {
    let resizeObserver: ResizeObserver | undefined;

    const handleResize = debounce(() => {
      if (overlayRef.current && triggerNode) {
        handleAlign(triggerNode);
      }
    }, 300);

    if (
      visible
      && oPopupContainer
      && overlayRef.current
      && triggerNode
    ) {
      resizeObserver = new ResizeObserver(handleResize);
    }

    if (resizeObserver) {
      resizeObserver.observe(oPopupContainer);
    }
    return () => {
      if (resizeObserver) {
        resizeObserver.disconnect();
        if (typeof handleResize.cancel === 'function') {
          handleResize.cancel();
        }
      }
    };
  }, [
    visible,
    oPopupContainer,
    overlayRef,
    triggerNode,
    handleAlign,
  ]);

  /**
   * 阻止blur
   */
  const handleClick = useCallback((e: React.MouseEvent<HTMLElement>) => {
    const whiteTags = ['input', 'textarea'];
    const tagName = ((e.target as HTMLElement).tagName || '').toLowerCase();
    if (!whiteTags.includes(tagName)) {
      e.preventDefault();
    }
  }, []);

  /**
   * 做位置计算的
   */
  useEffect(() => {
    if (visible) {
      if (triggerNode) {
        handleAlign(triggerNode);
      } else {
        handleAlign();
      }
    } else if (overlayRef.current) {
      overlayRef.current.style.top = '-9999px';
      overlayRef.current.style.left = '-9999px';
    }
  }, [visible, handleAlign, triggerNode, overlayRef]);

  /**
   * 合并样式
   */
  const styleCombined = useMemo(() => {
    const styles = { ...(overlayStyle || {}) };
    if (typeof zIndex === 'number') {
      styles.zIndex = zIndex;
    }
    return styles;
  }, [overlayStyle, zIndex]);

  return (
    <PortalComponent
      visible={!!visible}
      getContainer={getContainer}
    >
      {
        mask && (
          <OverlayMask
            className={visible ? undefined : 'hidden'}
            style={{ zIndex }}
            onClick={onClickMask}
            onContextMenu={(e: React.MouseEvent) => e.preventDefault()}
          />
        )
      }
      <OverlayWrap
        ref={overlayRef}
        style={styleCombined}
        className={visible ? overlayClassName : `${overlayClassName} hidden`}
        onMouseDown={handleClick}
        onMouseEnter={onMouseEnter}
        onMouseLeave={onMouseLeave}
      >
        {overlay}
      </OverlayWrap>
    </PortalComponent>
  );
};
