/* eslint-disable max-lines-per-function */
import React, { useState, useCallback, useMemo } from 'react';
import { SplitButton, ButtonSize } from '../button';
import { ColorPalette } from '../color-picker';
import { CommonLineSelect, ILineTypeValue } from '../lines';
import { PictureBorderNormal, LinethicknessNormal, LinetypeNormal } from '../icons';
import { useTranslate } from '~/locale/locales';
import { BorderPickerWrap, GroupWrap, TextSpan, PaletteBox, LineTypeSelectsWrap, SelectLeftWrap } from './styled';
import { isWhite } from '~/utils';

// export interface IBorderProps {
//   color?: string;
//   lineSize?: string;
//   lineStyle?: string;
// }

interface IBorderPanelProps {
  /**
   * 颜色
   */
  color: string,
  /**
   * 选中颜色的回调，选择无颜色时回传空字符串 `''`
   */
  onColorPick?: (color: string) => void,
  /**
   * 选中的线条粗细（受控），为 lineSizeMap 的 key
   */
  lineSize?: string,
  /**
   * 默认的线条粗细（非受控），为 lineSizeMap 的 key
   */
  defaultLineSize?: string,
  /**
   * 选择线条粗细时使用的映射
   */
  lineSizeMap: Record<string, ILineTypeValue>,
  /**
   * 线条粗细变化的回调函数
   */
  onLineSizeChange?: (lineSize: string) => void,
  /**
   * 选中的线条样式（受控），为 lineStyleMap 的 key
   */
  lineStyle?: string,
  /**
   * 默认的线条样式（非受控），为 lineStyleMap 的 key
   */
  defaultLineStyle?: string,
  /**
   * 选择线条样式时使用的映射
   */
  lineStyleMap: Record<string, ILineTypeValue>,
  /**
   * 线条样式变化的回调函数
   */
  onLineStyleChange?: (lineStyle: string) => void,
  /**
   * 国际化语言
   */
  locale?: string;
  /**
   * 点击左侧按钮的回调
   */
  onClick?: () => void;
}

export interface IBorderPickerProps extends IBorderPanelProps {
  /**
   * Button 的样式
   */
  style?: React.CSSProperties;
  /**
   * Button 是否显示边框
   */
  bordered?: boolean;
  /**
   * 层级
   */
  zIndex?: number;
  /**
   * 类名
   */
  className?: string;
  /**
   * 是否禁用
   */
  disabled?: boolean;
  /**
   * tooltip提示
   */
  title?: string | React.ReactNode;
  /**
   * button 的大小
   */
  size?: ButtonSize;
}

const DEFAULT_COLOR = 'inherit';

export const BorderPicker: React.FC<IBorderPickerProps> = (props) => {
  const {
    color,
    onColorPick,
    lineSize,
    defaultLineSize,
    lineSizeMap,
    onLineSizeChange,
    lineStyle,
    defaultLineStyle,
    lineStyleMap,
    onLineStyleChange,
    locale,
    style,
    bordered,
    className,
    disabled,
    title,
    onClick,
    size,
  } = props;

  const [dropdownVisible, setDropdownVisible] = useState(false);
  // const [colorPickerVisible, setColorPickerVisible] = useState(false);
  const [sizeSelectVisible, setSizeSelectVisible] = useState(false);
  const [styleSelectVisible, setStyleSelectVisible] = useState(false);
  const t = useTranslate(locale);

  const handleDropdownVisible = useCallback((e: boolean) => {
    setDropdownVisible(e);
  }, [setDropdownVisible]);

  // const handleColorPickerVisible = (e: boolean) => {
  //   setColorPickerVisible(e);
  // };

  const handleSizeSelectVisible = useCallback((e: boolean) => {
    setSizeSelectVisible(e);
  }, [setSizeSelectVisible]);

  const handleStyleSelectVisible = useCallback((e: boolean) => {
    setStyleSelectVisible(e);
  }, [setStyleSelectVisible]);

  const handleColorPick = useCallback((pickedColor: string) => {
    if (onColorPick) onColorPick(pickedColor);
  }, [onColorPick]);

  const handleLineSizeChange = useCallback((value: string) => {
    if (onLineSizeChange) onLineSizeChange(value);
    setDropdownVisible(true);
  }, [onLineSizeChange, setDropdownVisible]);

  const handleLineStyleChange = useCallback((value: string) => {
    if (onLineStyleChange) onLineStyleChange(value);
    setDropdownVisible(true);
  }, [onLineStyleChange, setDropdownVisible]);

  const handleClick = useCallback(() => {
    if (onClick) {
      onClick();
    }
  }, [onClick]);

  const getOverlay = () => {
    return (
      <BorderPickerWrap>
        <PaletteBox>
          <ColorPalette
            color={color}
            onPick={handleColorPick}
            automaticColor=""
            clearColor=""
            quickSetText={t('we_ui_color_picker_no_color')}
            showRecentColors={false}
          />
        </PaletteBox>
        <LineTypeSelectsWrap>
          <GroupWrap>
            <SelectLeftWrap>
              <LinethicknessNormal />
              <TextSpan>
                {t('we_ui_border_set_line_thickness')}
              </TextSpan>
            </SelectLeftWrap>
            <CommonLineSelect
              lineTypeArray={lineSizeMap}
              lineType={lineSize}
              length={35}
              color="#000000"
              onLineTypeChange={handleLineSizeChange}
              defaultLineType={defaultLineSize}
              optionText={(p) => `${p.size}pt`}
              visible={sizeSelectVisible}
              onVisibleChange={handleSizeSelectVisible}
            />
          </GroupWrap>
          <GroupWrap>
            <SelectLeftWrap>
              <LinetypeNormal />
              <TextSpan>
                {t('we_ui_border_set_line_type')}
              </TextSpan>
            </SelectLeftWrap>
            <CommonLineSelect
              lineTypeArray={lineStyleMap}
              lineType={lineStyle}
              defaultLineType={defaultLineStyle}
              length={60}
              color="#000000"
              onLineTypeChange={handleLineStyleChange}
              visible={styleSelectVisible}
              onVisibleChange={handleStyleSelectVisible}
            />
          </GroupWrap>
        </LineTypeSelectsWrap>
      </BorderPickerWrap>
    );
  };

  const pathColors = useMemo(() => ([
    // 当选中颜色为白色时，选中边框的外边缘为黑色，否则选中边框外边缘与内部颜色一致
    (!color || isWhite(color))
      ? DEFAULT_COLOR
      : color,
    color || DEFAULT_COLOR,
    DEFAULT_COLOR,
  ]), [color]);

  const visible = dropdownVisible || sizeSelectVisible || styleSelectVisible;

  return (
    <SplitButton
      overlay={getOverlay()}
      active={visible}
      onClickLeft={handleClick}
      onActiveChange={handleDropdownVisible}
      className={className}
      disabled={disabled}
      style={style}
      testid="BorderPicker"
      title={title}
      bordered={bordered}
      size={size}
    >
      <PictureBorderNormal pathColors={pathColors} />
    </SplitButton>
  );
};
