import styled from 'styled-components';
import { resetComponent } from '~/utils/styled.mixin';
import { withTheme } from '../theme';

export const RadioInner: any = withTheme(styled.span<any>`
  ${resetComponent}
  display: inline-block;
  vertical-align: middle;
  width: 16px;
  height: 16px;
  border: 1px solid ${({ theme }) => theme.ui.borderColor};
  background-color: ${({ theme }) => theme.ui.colorGray1};
  transition: all 0.24s ease-in;
  border-radius: 50%;
  position: relative;
  &.disabled {
    background-color: ${({ theme }) => theme.ui.disabledBackgroundColor};
  }
`);

export const RadioWrap = withTheme(styled.label<any>`
  ${resetComponent}
  cursor: ${({ disabled }) => (disabled ? 'not-allowed' : 'pointer')};

  ${({ disabled, theme }) => {
    return disabled
      ? ''
      : `
      &:hover {
        ${RadioInner} {
          border-color: ${theme.ui.primaryColor};
        }
      }
    `;
  }};

  ${({ checked, theme: { ui }, disabled }) => {
    return checked
      ? `
      ${RadioInner} {
        border-color: ${disabled ? ui.borderColor : ui.primaryColor};
        border-width: 4px;
      }
    `
      : '';
  }}
`);

export const RadioText = withTheme(styled.span<any>`
  ${resetComponent}
  display: inline-block;
  vertical-align: middle;
  padding: 0 ${({ theme }) => theme.ui.padding};
`);

export const RadioContent = withTheme(styled.span<any>`
  ${resetComponent}
  position: relative;
  input {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1;
    width: 0;
    height: 0;
    cursor: pointer;
    opacity: 0;

    &:focus ~ ${RadioInner} {
      border-color: ${({ theme }: any) => theme.ui.primaryColor};
    }
  }
`);

export const RadioGroupWrap = withTheme(styled.div<any>`
  ${resetComponent}
`);
