import React, { useMemo, useCallback } from 'react';
import useControlledValue from '~/hooks/useControlledValue';
import { useTranslate } from '~/locale/locales';
import {
  AlignBottomNormal,
  AlignCenterNormal,
  AlignTopNormal,
} from '../icons';
import { Select, SelectOption } from '../select';
import { OptionWrap, TriggerWrap } from './styled';
import { TOptionTitle } from '../select/interface';

export type VerticalAlign = 'middle' | 'top' | 'bottom';

export interface IVerticalAlignSelectProps {
  /**
   * 默认的对齐方式，当设置了value，该项忽略
   */
  defaultValue?: VerticalAlign;
  /**
   * 对齐方式
   */
  value?: VerticalAlign;
  /**
   * 设置国际化语言
   */
  locale?: string;
  /**
   * select 样式
   */
  style?: React.CSSProperties;
  /**
   * 对齐方式修改时的回调函数
   */
  onChange?: (align: VerticalAlign) => void;
  /**
   * 层级
   */
  zIndex?: number;
  /**
   * 类名
   */
  className?: string;
  /**
   * 是否禁用
   */
  disabled?: boolean;
  /**
   * 禁用的选项列表
   */
  disabledKeys?: VerticalAlign[];
  /**
   * tooltip提示
   */
  title?: string | React.ReactNode;
  /**
   * 选项的tooltip提示
   */
  titles?: Record<string, TOptionTitle>;
}

interface ISelectOption {
  title: string;
  value: VerticalAlign;
  Icon: React.ComponentType<any>;
}

const VerticalAlignSelect: React.FC<IVerticalAlignSelectProps> = (props) => {
  const { locale, style, onChange, zIndex, disabledKeys, title, titles, ...restProps } = props;
  const [value, setValue] = useControlledValue(props.value, props.defaultValue);
  const t = useTranslate(locale);

  // 垂直对齐的选项
  const options: ISelectOption[] = useMemo(() => [
    { title: t('we_ui_align_top'), value: 'top', Icon: AlignTopNormal },
    { title: t('we_ui_align_center'), value: 'middle', Icon: AlignCenterNormal },
    { title: t('we_ui_align_bottom'), value: 'bottom', Icon: AlignBottomNormal },
  ], [t]);

  // 该组件的trigger区域只显示Icon
  const renderTrigger = useCallback((align?: string) => {
    const index = options.findIndex((option) => option.value === align);
    const currentOption = options[index];
    return (
      <TriggerWrap style={{ minWidth: 16 }}>
        {currentOption && <currentOption.Icon />}
      </TriggerWrap>
    );
  }, [options]);

  const handleChange = useCallback((e: VerticalAlign) => {
    if (onChange) {
      onChange(e);
    }
    setValue(e);
  }, [setValue, onChange]);

  return (
    <Select
      {...restProps}
      value={value}
      onChange={handleChange}
      style={style || { padding: '0 0 0 6px', marginRight: -1 }}
      renderTrigger={renderTrigger}
      zIndex={zIndex}
      title={title}
      testid="VerticalAlignSelect"
    >
      {
        options.map((option) => (
          <SelectOption
            value={option.value}
            key={option.value}
            disabled={disabledKeys?.includes(option.value)}
            title={titles![option.value]}
          >
            <OptionWrap>
              <option.Icon className="icon" />
              <span>{option.title}</span>
            </OptionWrap>
          </SelectOption>
        ))
      }
    </Select>
  );
};

VerticalAlignSelect.defaultProps = {
  defaultValue: 'top',
  titles: {},
};

export default VerticalAlignSelect;
