import React, { useCallback, useState, useRef, useEffect, useMemo } from 'react';
import useControlledValue from '~/hooks/useControlledValue';
import { Select, SelectOption, ValidationError } from '../select';
import { debounce } from '~/utils';
import styled from 'styled-components';
import classnames from '~/utils/classnames';
import { TOptionTitle, ISelectProps, Rule } from '../select/interface';

export const DEFAULT_SIZES = ['12', '14', '16', '18', '20', '24', '30', '36'];
export const CHINESE_SIZES = [
  '初号', '小初', '一号', '小一', '二号', '小二', '三号', '小三', '四号', '小四', '五号', '小五', '六号', '小六', '七号', '八号',
];
const CHINESE_SIZES_NUMBERS_MAPPER = ['42', '36', '26', '24', '22', '18', '16', '15', '14', '12', '10.5', '9', '7.5', '6.5', '5.5', '5'];

const ch2num = (size: string): string => {
  const index = CHINESE_SIZES.indexOf(size);
  return CHINESE_SIZES_NUMBERS_MAPPER[index];
};

const num2ch = (size: string): string => {
  const index = CHINESE_SIZES_NUMBERS_MAPPER.indexOf(size);
  return CHINESE_SIZES[index] || size;
};

// 增加对于.5的小数正则校验
const isNumberSize = (num: string) => /^[0-9]+$|^([0-9]+[.][5])$/.test(num);

export interface IFontSizeSelectProps extends Partial<ISelectProps<number>> {
  /**
   * 默认的字号，当设置了value，该项忽略
   */
  defaultValue?: number;
  /**
   * 字号
   */
  value?: number;
  /**
   * 字号修改时的回调函数
   */
  onChange?: (e: number) => void;
  /**
   * 字号列表
   */
  sizes?: number[];
  /**
   * 是否允许输入
   */
  allowInput?: boolean;
  /**
   * tooltip提示
   */
  title?: string | React.ReactNode;
  /**
   * 选项的tooltip提示
   */
  titles?: Record<string, TOptionTitle>;
  /**
   * 是否支持中文选项
   */
  supportChinese?: boolean;
}

const fontSizeRules: Array<Rule<string>> = [
  {
    validator: (e: string) => {
      const value = Number(isNumberSize(e) ? e : ch2num(e));
      return value >= 5 && value <= 200 && Number.isInteger(value * 2);
    },
    message: 'min step should be 0.5',
  },
];

const SelectStyled: any = styled(Select)`
  &.select-wrap {
    font-size: 12px;
    line-height: 20px;
  }
`;

// eslint-disable-next-line max-lines-per-function
const FontSizeSelect: React.FC<IFontSizeSelectProps> = (props) => {
  const {
    style,
    sizes,
    onChange,
    zIndex,
    bordered,
    allowInput,
    className,
    disabled,
    title,
    titles,
    supportChinese,
  } = props;
  // 受控的值
  const [valueControlled, setValue] = useControlledValue(props.value, props.defaultValue);

  // 是否使用中文选项
  const usingChinese = localStorage.getItem('we-ui-using-chinese') === 'true';

  // 将值都转化为string
  const value = useMemo<string>(() => {
    if (supportChinese && usingChinese) {
      return num2ch(`${valueControlled}`) || '';
    }
    return `${valueControlled!}`;
  }, [valueControlled, supportChinese, usingChinese]);

  // 有没有错误
  const [hasError, setError] = useState(false);

  const rules = allowInput ? fontSizeRules : undefined;

  const mode = allowInput ? 'input' : undefined;

  const lastVal = useRef<number | undefined>(valueControlled);

  /**
   * 自动修复数据
   */
  // eslint-disable-next-line react-hooks/exhaustive-deps
  const autoFixValue = useCallback(debounce((fixValue) => {
    setValue(fixValue);
    setError(false);
  }, 1000), [setValue, setError]);

  useEffect(() => () => autoFixValue.cancel(), [autoFixValue]);

  const handleChange = useCallback((e: string, error?: ValidationError<number | string>) => {
    if (error) {
      setError(true);
      autoFixValue(lastVal.current);
    } else {
      const isNumber = isNumberSize(e);
      localStorage.setItem('we-ui-using-chinese', (!isNumber).toString());

      const newValue = Number(isNumber ? e : ch2num(e));
      if (onChange) {
        onChange(newValue);
      }

      lastVal.current = newValue;
      setValue(newValue);
    }
  }, [onChange, setValue, autoFixValue]);

  const cssStyle = useMemo(() => ({ width: 52, ...style }), [style]);

  const options = useMemo(() => {
    const fontSizesArray = [
      ...(supportChinese ? CHINESE_SIZES : []),
      ...(sizes || DEFAULT_SIZES),
    ].map((size) => `${size}`);
    return (
      fontSizesArray.map((size) => (
        <SelectOption value={size} key={`${size}`} testid={size} title={titles![size]}>
          <span>{size}</span>
        </SelectOption>
      ))
    );
  }, [sizes, titles, supportChinese]);

  return (
    <SelectStyled
      value={value}
      onChange={handleChange}
      zIndex={zIndex}
      style={cssStyle}
      bordered={bordered}
      mode={mode}
      rules={rules}
      className={classnames('select-wrap', className)}
      disabled={disabled}
      testid="FontSizeSelect"
      title={title}
      errored={hasError}
    >
      { options }
    </SelectStyled>
  );
};

FontSizeSelect.defaultProps = {
  defaultValue: 12,
  titles: {},
};

export default FontSizeSelect;
