
// 插入类型
export enum EInsertType {
  // 选人
  Person = 0,
  // 选群
  Group = 1,
  // 选文件
  Document = 2,
}

// 基础列表项
export interface IInsertItemBase {
  /**
   * 业务层可以扩展字段
   */
  [key: string]: any;
  // 人 / 群 / 文件
  type: EInsertType;
  // uid / cid / dentryKey
  key: string;
  // 人名 / 群名 / 文件名
  title: string;
  // 所在组织 / 所在企业 / 所在文档位置
  description: string;
  // 选人时只有一个头像，取第0个元素；选群时可能会有多个头像
  iconUrls: string[];
}

// 选人列表项
export interface IInsertItemPerson extends IInsertItemBase {
  // 插入人的用户昵称
  nickName: string;
}

// 选群列表项
export type IInsertItemGroup = IInsertItemBase;

// 选文件列表项
export interface IInsertItemDoc extends IInsertItemBase {
  docType: number;
}

export type IInsertItem = IInsertItemPerson | IInsertItemGroup | IInsertItemDoc;


// 列表分组，如：文档成员、最近
export interface IListGroup {
  title: string;
  list: IInsertItem[];
}

// 右上角的高级选项
export interface IAdvancedOption {
  icon: React.ReactNode;
  name: string;
  onClick: () => void;
}

export enum EQuickInsertStatus {
  loading = 0,
  ready = 1,
  loadMore = 2,
}

export interface IQuickInsertProps {
  data: IListGroup[];
  advanceOption: IAdvancedOption;
  // 是否使用快捷键,默认false
  useHotKey?: boolean;
  locale?: string;
  status: EQuickInsertStatus;
  onItemClick: (itemData: IInsertItem) => void;
  loadMore: () => void;
}

