/* eslint-disable no-console */
/* eslint-disable arrow-body-style */
import React, { useMemo, ReactElement, useCallback, useEffect } from 'react';
import { createPortal } from 'react-dom';
import { Menu } from '../menu';
import { ContextRoot, ContextContainer, GlobalStyle } from './styled';
import useClickOuterRef from '~/hooks/useClickOuterRef';

interface IContextMenuPosition {
  x: number;
  y: number;
}

export interface IContextMenuProps {
  /**
   * 是否可见
   */
  visible: boolean;
  /**
   * 是否可见变化
   */
  onVisibleChange?: (visible: boolean) => void;
  /**
   * 位置: type IContextMenuPosition = { x: number; y: number }
   */
  position?: IContextMenuPosition;
}

export const ContextMenu: React.FC<IContextMenuProps> = (props) => {
  const {
    children,
    position,
    onVisibleChange,
    visible,
  } = props;


  // 进行子组件的检查：children只有一个、子组件应该为Menu
  const child = Array.isArray(children) ? children[0] : children;
  if (!React.isValidElement(child) || child.type !== Menu) {
    console.warn("ContextMenu's child should be Menu");
  }

  /**
   * 可不可见变化函数
   *
   * @param {Boolean} visible
   * @return void
   */
  const handleChangeVisible = useCallback((newVisible: boolean): void => {
    if (visible !== newVisible && onVisibleChange) {
      onVisibleChange(newVisible);
    }
  }, [visible, onVisibleChange]);

  // 位置样式
  const { x, y } = position || { x: 0, y: 0 };
  const styles = useMemo(() => ({ left: x, top: y }), [x, y]);

  /**
   * 点击回调函数
   *
   * @return {void}
   */
  const handleClick = useCallback((): void => {
    handleChangeVisible(false);
  }, [handleChangeVisible]);

  // 点击菜单外
  const containerRef = useClickOuterRef(handleClick);

  // 右键菜单显示的时候，阻止body滚动
  useEffect(() => {
    if (visible) {
      document.body.className += ' ctxmenu-hidden';
    }
    return (): void => {
      if (visible) {
        document.body.className = (document.body.className || '').replace(' ctxmenu-hidden', '');
      }
    };
  }, [visible]);

  const handleContextMenu = useCallback((e: React.MouseEvent<HTMLElement>) => {
    e.preventDefault();
  }, []);

  return visible ? createPortal(
    <ContextRoot onContextMenu={handleContextMenu}>
      <ContextContainer
        style={styles}
        onClick={handleClick}
        ref={containerRef}
      >
        {
          React.cloneElement(child as ReactElement, {
            selectedKeys: [],
            openKeys: [],
          })
        }
      </ContextContainer>
      <GlobalStyle />
    </ContextRoot>,
    document.body,
  ) : <GlobalStyle />;
};

ContextMenu.defaultProps = {
  position: {
    x: 0,
    y: 0,
  },
};
