import * as React from 'react';
import {
  Wrapper,
  Line,
  Single,
} from './styled';

const defaultLineColor = '#000';

export type TLineStyle =
  | 'single'
  | 'dotted'
  | 'dashed'
  | 'dotDash'
  | 'dotDotDash'
  | 'double'
  | 'thickThinSmallGap'
  | 'thinThickThinMediumGap'
  | 'dashStroked'
  | 'wave'
  | 'doubleWave'
  | 'dashDotStroked'
  | 'widthDoubleWave'
  | 'widthWave'
  | 'roundDot';


export type TLineWidth = number;

export const lineWidthByBuiltIn = [1, 2, 4, 6];
export const lineWidthByCustom = [4, 6];

// 线段属性
interface ILine {
  content: string;
  // 重复次数
  repeat: number;
  // 默认宽度列表
  sizeList: number[];
  // 线条的宽度(厚度)
  defaultSize: number;
  // 高度
  unitHeight: number;
  // 缩放比
  scale?: number;
}

