import React, { useMemo } from 'react';
import { Trigger } from '../trigger';
import { ITriggerAction, IPlacement } from '../trigger/Trigger';

export interface IDropdownProps {
  /**
   * 触发机制
   */
  trigger?: 'hover' | 'click' | 'contextMenu';
  /**
   * 弹层节点
   */
  overlay: React.ReactElement | null;
  /**
   * 挂在容器
   */
  getContainer?: () => HTMLElement;
  /**
   * 弹层可见性变化处理函数
   */
  onVisibleChange?: (e: boolean) => void;
  /**
   * 弹层位置
   */
  placement?: IPlacement;
  /**
   * 弹层可见性
   */
  visible?: boolean;
  /**
   * 弹层样式
   */
  overlayStyle?: React.CSSProperties;
  /**
   * 弹层类名
   */
  overlayClassName?: string;
  /**
   * 层级
   */
  zIndex?: number;
  /**
   * 偏移x
   */
  offsetX?: number;
  /**
   * 偏移Y
   */
  offsetY?: number;
  /**
   * 根据坐标来定位
   */
  pageX?: number;
  /**
   * 根据坐标来定位
   */
  pageY?: number;
  /**
   * trigger节点
   */
  children?: React.ReactElement;
  /**
   * trigger节点手动传入
   */
  triggerNode?: HTMLElement;
  /**
   * Tooltip消息
   */
  title?: string | React.ReactNode;
  /**
   * 暴露 data-testid, 用于测试
   */
  testid?: string;
}

export const Dropdown: React.FC<IDropdownProps> = (props) => {
  const {
    children,
    trigger,
    overlay,
    getContainer,
    onVisibleChange,
    placement,
    overlayStyle,
    visible,
    zIndex,
    overlayClassName,
    offsetX,
    offsetY,
    pageX,
    pageY,
    triggerNode,
    title,
    testid,
  } = props;

  // trigger's action
  const action: ITriggerAction[] = useMemo(() => (
    trigger === 'contextMenu' ? [trigger] : [trigger!, 'focus']
  ), [trigger]);

  return (
    <Trigger
      action={action}
      overlay={overlay}
      getContainer={getContainer}
      onVisibleChange={onVisibleChange}
      placement={placement}
      visible={visible}
      overlayStyle={overlayStyle}
      zIndex={zIndex}
      overlayClassName={overlayClassName}
      offsetX={offsetX}
      offsetY={offsetY}
      pageX={pageX}
      pageY={pageY}
      triggerNode={triggerNode}
      title={title}
      testid={testid}
    >
      {children}
    </Trigger>
  );
};

Dropdown.defaultProps = {
  trigger: 'click',
};
