import React, { useCallback, useState, useMemo } from 'react';
import dayjs, { Dayjs } from 'dayjs';
import { Dropdown } from '~/components/dropdown';
import { Button } from '~/components/button';
import { CalendarNormal, RoundcloseThickNormal } from '~/components/icons';
import { Input } from '~/components/input';
import { useTranslate } from '~/locale/locales';
import useControlledValue from '~/hooks/useControlledValue';
import { classnames } from '~/utils';
import useComponentDidUpdate from '~/hooks/useComponentDidUpdate';
import { TimePanel } from '~/components/time-picker';
import {
  DatePickerTrigger,
  DatePickerTriggerWrap,
  DatePickerOverlay,
  DatePickerOverlayInputs,
  DatePickerOverlayPanel,
  DatePickerOverlayFooter,
} from './styled';
import { DatePanel } from './DatePanel';
import { MonthPanel } from './MonthPanel';
import { YearPanel } from './YearPanel';

export type DatePickerPanelType = 'date' | 'year' | 'month' | 'time';

export interface IDatePickerProps {
  /**
   * 设置国际化语言
   */
  locale?: string;
  /**
   * 是否禁用
   */
  disabled?: boolean;
  /**
   * 日期值，Dayjs对象
   */
  value?: Dayjs | null;
  /**
   * 默认的日期值，Dayjs对象
   */
  defaultValue?: Dayjs | null;
  /**
   * 提示
   */
  placeholder?: string;
  /**
   * 选中的面板
   */
  panel?: DatePickerPanelType,
  /**
   * 默认选中的面板
   */
  defaultPanel?: DatePickerPanelType,

  /**
   * 是否支持时间选择，可为布尔值，复杂配置查看 TimePicker
   */
  showTime?: boolean | { format?: string }; // TODO ITimePickerProps
  /**
   * 日期的格式，请遵循dayjs规范
   */
  format?: string;
  /**
   * 样式
   */
  style?: React.CSSProperties;
  /**
   * 弹层样式
   */
  overlayStyle?: React.CSSProperties;
  /**
   * 弹层类名
   */
  overlayClassName?: string;
  /**
   * 选择面板是否可见
   */
  visible?: boolean;
  /**
   * 默认的选择面板是否可见
   */
  defaultVisible?: boolean;
  /**
   * 自定义显示区
   */
  renderTrigger?: (e: Dayjs | null | undefined) => React.ReactElement;
  /**
   * 值发生变化
   */
  onChange?: (e: Dayjs | null) => void;
  /**
   * 面板可见变化
   */
  onVisibleChange?: (e: boolean) => void;
  /**
   * 点击确定按钮，只在有时间选择的时候有效
   */
  onOk?: (e: Dayjs | null) => void;
  /**
   * 不可选择的日期
   */
  disabledDate?: (currentDate: Dayjs, view: DatePickerPanelType) => boolean;
  /**
   * 层级
   */
  zIndex?: number;
  /**
   * 支持清除
   */
  allowClear?: boolean;
  /**
   * tooltip提示
   */
  title?: string | React.ReactNode;
  /**
   * 测试使用
   */
  testid?: string;
}

// eslint-disable-next-line max-lines-per-function
const DatePicker: React.FC<IDatePickerProps> = React.memo((props) => {
  const {
    locale,
    format,
    showTime,
    style,
    onChange,
    onVisibleChange,
    onOk,
    disabled,
    renderTrigger,
    disabledDate,
    zIndex,
    allowClear,
    overlayStyle,
    overlayClassName,
    title,
    testid,
  } = props;

  // 国际化函数
  const t = useTranslate(locale);

  // 时间值，受控
  const [value, setValue] = useControlledValue<Dayjs | null | undefined>(
    props.value, props.defaultValue,
  );

  // 面板显示与否，受控
  const [visible, setVisible] = useControlledValue(props.visible, props.defaultVisible);

  // 当前显示的面板
  const [panel, setPanel] = useControlledValue(props.panel, props.defaultPanel);

  // 临时值
  const [temporaryValue, setTemporaryValue] = useState<Dayjs>(value || dayjs());

  // 年份面板起始年份，暂时的状态，应该在面板关闭的时候就还原
  const [yearPanelStartYear, setYearPanelStartYear] = useState<number>(temporaryValue.year() - 4);

  // 月份的临时年份
  const [monthPanelYear, setMonthPanelYear] = useState<number>(temporaryValue.year());

  // 提示的国际化
  const placeholder = props.placeholder || (
    showTime
      ? t('we_ui_datepicker_datetime_placeholder')
      : t('we_ui_datepicker_date_placeholder')
  );

  // 格式化拼装
  const dateFormat = format || 'YYYY-MM-DD';
  let timeFormat = '';
  if (typeof showTime === 'object' && showTime.format) {
    timeFormat = showTime.format;
  } else {
    timeFormat = (showTime ? 'HH:mm:ss' : '');
  }
  const finalFormat = [dateFormat, timeFormat].filter((f) => f).join(' ');

  // 日期输入框的值
  const [dateInputValue, setDateInputValue] = useState(value ? value.format(dateFormat) : '');

  const [timeInputValue, setTimeInputValue] = useState(value ? value.format(timeFormat) : '');

  // temporay 受控于value
  useComponentDidUpdate(() => {
    if (typeof value !== 'undefined') {
      setTemporaryValue(value || dayjs());
    }
  }, [value]);

  // 日期输入框的值受控于value和format
  useComponentDidUpdate(() => {
    setDateInputValue(value ? value.format(dateFormat) : '');
  }, [value, dateFormat]);

  // 时间输入框的值受控于value和format
  useComponentDidUpdate(() => {
    setTimeInputValue(value ? value.format(timeFormat) : '');
  }, [value, timeFormat]);

  /**
   * overlay可见修改
   *
   * @param {boolean} e
   * @return {void}
   */
  const handleVisibleChange = useCallback((e: boolean) => {
    if (!disabled) {
      if (onVisibleChange) {
        onVisibleChange(e);
      }
      setVisible(e);
    }
  }, [onVisibleChange, disabled, setVisible]);

  const handleConfirm = useCallback(() => {
    if (onOk) {
      onOk(value!);
    }
    handleVisibleChange(false);
  }, [handleVisibleChange, onOk, value]);

  const handleChange = useCallback((e: Dayjs | null) => {
    const changed = !e || !value || !value!.isSame(e);
    if (changed) {
      if (onChange) {
        onChange(e);
      }
      setValue(e);
    }
  }, [value, onChange, setValue]);

  const handleChangeDateValue = useCallback((e: Dayjs) => {
    if (!showTime) {
      handleVisibleChange(false);
    }
    handleChange(e);
  }, [handleVisibleChange, showTime, handleChange]);

  /**
   * 切换时间和日期面板
   *
   * @return {void}
   */
  const handleToggleTimeDatePanel = useCallback(() => {
    if (value) {
      if (panel === 'time') {
        setPanel('date');
      } else {
        setPanel('time');
      }
    }
  }, [panel, value, setPanel]);

  /**
   * 日期输入框输入
   *
   * @param {React.ChangeEvent<HTMLInputElement>}
   * @return {void}
   */
  const handleChangeDateInput = useCallback((e: React.ChangeEvent<HTMLInputElement>) => {
    setDateInputValue(e.target.value);
  }, []);

  /**
   * 日期输入框失焦
   *
   * @param {React.FocusEvent<HTMLInputElement>}
   * @return {void}
   */
  const handleDateInputBlur = useCallback((e) => {
    const v = e.target.value.trim();
    let inputDate = dayjs(v, dateFormat);
    const isValid = inputDate.isValid();
    if (!isValid) {
      setDateInputValue(value ? value.format(dateFormat) : '');
    } else {
      if (value) {
        inputDate = inputDate.hour(value.hour()).minute(value.minute()).second(value.second());
      }
      setDateInputValue(inputDate.format(dateFormat));
      handleChange(inputDate);
    }
  }, [dateFormat, value, handleChange]);

  /**
   * 日期输入框聚焦
   *
   * @param {React.FocusEvent<HTMLInputElement>}
   * @return {void}
   */
  const handleDateInputFocus = useCallback(() => {
    if (panel === 'time') {
      setPanel('date');
    }
  }, [panel, setPanel]);

  /**
   * 日期输入框输入
   *
   * @param {React.ChangeEvent<HTMLInputElement>}
   * @return {void}
   */
  const handleChangeTimeInput = useCallback((e: React.ChangeEvent<HTMLInputElement>) => {
    setTimeInputValue(e.target.value);
  }, []);

  /**
   * 日期输入框失焦
   *
   * @param {React.FocusEvent<HTMLInputElement>}
   * @return {void}
   */
  const handleTimeInputBlur = useCallback((e) => {
    const v = e.target.value.trim();
    // 时间format需要加个虚拟的日期进去
    let timeDate = dayjs(`1992-10-22 ${v}`, timeFormat);
    const isValid = timeDate.isValid();
    if (!isValid) {
      setTimeInputValue(value ? value.format(timeFormat) : '');
    } else {
      if (value) {
        timeDate = timeDate.year(value.year()).month(value.month()).date(value.date());
      }
      setTimeInputValue(timeDate.format(timeFormat));
      handleChange(timeDate);
    }
  }, [timeFormat, value, handleChange]);

  /**
   * 日期输入框聚焦
   *
   * @param {React.FocusEvent<HTMLInputElement>}
   * @return {void}
   */
  const handleTimeInputFocus = useCallback(() => {
    if (panel !== 'time') {
      setPanel('time');
    }
  }, [panel, setPanel]);

  /**
   * 点击控制条进行面板切换
   *
   * @param {React.MouseEvent} e
   * @return {void}
   */
  const handleClickControl = useCallback((e) => {
    e.stopPropagation();
    if (panel === 'date') {
      setMonthPanelYear(temporaryValue.year());
      setPanel('month');
    } else if (panel === 'month') {
      setYearPanelStartYear(monthPanelYear - 4);
      setPanel('year');
    } else if (panel === 'year') {
      setPanel('month');
    }
  }, [panel, temporaryValue, monthPanelYear, setPanel]);


  /**
   * 阻止冒泡，Dropdown自动关闭阻止
   *
   * @param {React.MouseEvent<HTMLElement>}
   * @return {void}
   */
  const handleClickOverlay = useCallback((e) => e.stopPropagation(), []);

  /**
   * 年份修改，这个修改是暂时性的值
   *
   * @param {Dayjs} newTemporayValue
   * @return {void}
   */
  const handleChangeTemporaryYear = useCallback((newValue: Dayjs) => {
    setTemporaryValue(newValue);
    // 切换到月份面板
    setPanel('month');
    // 需要将月份面板的年份重置掉
    setMonthPanelYear(newValue.year());
  }, [setPanel]);

  /**
   * 月份修改，这个修改是暂时性的值
   *
   * @param {Dayjs} newTemporayValue
   * @return {void}
   */
  const handleChangeTemporaryMonth = useCallback((newValue: Dayjs) => {
    setTemporaryValue(newValue);
    // 切换回日期面板
    setPanel('date');
  }, [setPanel]);

  /**
   * 清除
   */
  const handleClear = useCallback((e: React.MouseEvent) => {
    e.stopPropagation();
    handleChange(null);
  }, [handleChange]);

  // 三个面板切换的隐藏样式
  const datePanelStyle = useMemo(() => ({ display: panel === 'date' ? 'block' : 'none' }), [panel]);
  const monthPanelStyle = useMemo(() => ({ display: panel === 'month' ? 'block' : 'none' }), [panel]);
  const yearPanelStyle = useMemo(() => ({ display: panel === 'year' ? 'block' : 'none' }), [panel]);

  return (
    <Dropdown
      trigger="click"
      visible={visible}
      zIndex={zIndex}
      overlayStyle={overlayStyle}
      overlayClassName={overlayClassName}
      title={title}
      overlay={(
        <DatePickerOverlay onMouseDown={handleClickOverlay}>
          <DatePickerOverlayInputs>
            <Input
              value={dateInputValue}
              placeholder={dateFormat}
              className="datetime-input"
              onChange={handleChangeDateInput}
              onBlur={handleDateInputBlur}
              onFocus={handleDateInputFocus}
              testid={testid ? `${testid}_date` : undefined}
            />
            {
              !!showTime && (
                <Input
                  value={timeInputValue}
                  placeholder={timeFormat}
                  className="datetime-input"
                  disabled={!value}
                  onChange={handleChangeTimeInput}
                  onBlur={handleTimeInputBlur}
                  onFocus={handleTimeInputFocus}
                  testid={testid ? `${testid}_time` : undefined}
                />
              )
            }
          </DatePickerOverlayInputs>
          <DatePickerOverlayPanel>
            <DatePanel
              style={datePanelStyle}
              onClickControl={handleClickControl}
              temporaryValue={temporaryValue}
              onChangeTemporaryValue={setTemporaryValue}
              value={value}
              onChange={handleChangeDateValue}
              locale={locale}
              disabledDate={disabledDate}
              testid={testid}
            />
            <MonthPanel
              year={monthPanelYear}
              style={monthPanelStyle}
              onClickControl={handleClickControl}
              value={temporaryValue}
              onChangeYear={setMonthPanelYear}
              onChange={handleChangeTemporaryMonth}
              locale={locale}
              disabledDate={disabledDate}
              testid={testid}
            />
            <YearPanel
              startYear={yearPanelStartYear}
              style={yearPanelStyle}
              onChangeStartYear={setYearPanelStartYear}
              value={temporaryValue}
              onChange={handleChangeTemporaryYear}
              onClickControl={handleClickControl}
              disabledDate={disabledDate}
              testid={testid}
            />
            {
              !!showTime && (
                <TimePanel
                  visible={panel === 'time'}
                  value={temporaryValue}
                  onChange={handleChange}
                  testid={testid}
                />
              )
            }
          </DatePickerOverlayPanel>
          {
            !!showTime && (
              <DatePickerOverlayFooter>
                <span
                  className={classnames('time-button', {
                    disabled: !value,
                  })}
                  onClick={handleToggleTimeDatePanel}
                  data-testid={testid ? `time-button-${testid}` : undefined}
                >
                  {panel === 'time' ? '选择日期' : '选择时间'}
                </span>
                <Button
                  shape="round"
                  disabled={!value}
                  type="primary"
                  onClick={handleConfirm}
                  testid={testid}
                >
                  确认
                </Button>
              </DatePickerOverlayFooter>
            )
          }
        </DatePickerOverlay>
      )}
      onVisibleChange={handleVisibleChange}
    >
      {
        typeof renderTrigger === 'function'
          ? renderTrigger(value)
          : (
            <DatePickerTrigger
              className={classnames({
                placeholder: !value,
                disabled,
                allowClear: !disabled && allowClear && !visible,
              })}
              style={style}
            >
              <DatePickerTriggerWrap>
                <span className="text">
                  {
                    value ? value.format(finalFormat) : placeholder
                  }
                </span>
                <CalendarNormal className="calendar-icon" />
                {
                  !disabled && allowClear && !visible && (
                    <RoundcloseThickNormal
                      className="clear-icon"
                      onClick={handleClear}
                      testid={testid}
                    />
                  )
                }
              </DatePickerTriggerWrap>
            </DatePickerTrigger>
          )
      }
    </Dropdown>
  );
});

DatePicker.defaultProps = {
  defaultVisible: false,
  format: 'YYYY-MM-DD',
  showTime: false,
  disabled: false,
  defaultPanel: 'date',
};

export { DatePicker };
