/* eslint-disable react/no-did-mount-set-state */
import React from 'react';
import warning from 'warning';
import { ToolLine } from './ToolLine';
import { ToolGroupWrap } from './styled';

export interface IToolGroupProps {
  /**
  * 包裹的组件
  */
  // children: ToolBox[];
  children?: any;
  /**
   * 复杂布局，应用于单双行按钮混排的情况，默认为false
   */
  isComplexLayout?: boolean;
  /**
   * 工具栏的高度，isComplexLayout为true时需要设置高度
   */
  height?: number;
}

interface IState {
  groupWidth: number;
}

// 工具栏 单行的高度
const DEFAULT_GROUP_HEIGHT = 58;

export class ToolGroup extends React.Component<IToolGroupProps, IState> {
  refToolGroup = React.createRef<HTMLDivElement>();

  constructor(props: IToolGroupProps) {
    super(props);
    let hasIllegalChild = false;
    React.Children.forEach(props.children, (child) => {
      const isToolBox = child!.type === ToolLine;
      if (!isToolBox) {
        hasIllegalChild = true;
      }
    });
    warning(!hasIllegalChild, 'Children of ToolGroup must be ToolLine');
    this.state = {
      groupWidth: 0,
    };
  }

  componentDidMount() {
    const { isComplexLayout } = this.props;
    const domGroup = this.refToolGroup.current as HTMLElement;
    if (isComplexLayout && domGroup) {
      const width = this.calculateWidth();
      this.setState({ groupWidth: width });
    }
  }

  componentDidUpdate(prevProps: IToolGroupProps, prevState: IState) {
    const { isComplexLayout } = this.props;
    const domGroup = this.refToolGroup.current;
    if (isComplexLayout && domGroup && prevProps !== this.props) {
      const width = this.calculateWidth();
      const { groupWidth } = prevState;
      if (width !== groupWidth) {
        this.setState({ groupWidth: width });
      }
    }
  }

  calculateWidth = () => {
    let widthSum = 0;
    let tmpHeight = 0;
    const { height: groupHeight = DEFAULT_GROUP_HEIGHT } = this.props;
    const domGroup = this.refToolGroup.current;
    if (domGroup) {
      const { children } = domGroup;
      for (let index = 0; index < children.length; index++) {
        const item = children[index] as HTMLElement;
        const width = item.offsetWidth;
        const height = item.offsetHeight;
        tmpHeight += height;
        if (tmpHeight >= groupHeight) {
          widthSum += width;
          tmpHeight = 0;
        } else {
          tmpHeight += height;
        }
      }
    }
    return widthSum;
  };

  render() {
    const {
      isComplexLayout,
      children,
      height: groupHeight = DEFAULT_GROUP_HEIGHT,
    } = this.props;
    const { groupWidth } = this.state;

    return (
      <ToolGroupWrap
        isComplexLayout={isComplexLayout}
        width={groupWidth}
        height={groupHeight}
        ref={this.refToolGroup}
      >
        {React.Children.map(children, (child) => (
          React.cloneElement(child!, { isSingleLine: false })
        ))}
      </ToolGroupWrap>
    );
  }
}

export type ToolGroupArray = Array<React.ReactElement<IToolGroupProps>>;

export class HiddenToolGroup extends ToolGroup {
}
