import { useState, useCallback, useRef } from 'react';
import { isEqual } from '~/utils';


export type IKeyboardCondition = number[] | { and: number[] } | { or: number[] };


export type IKeyboardPair = [
  IKeyboardCondition,
  React.KeyboardEventHandler<HTMLElement>,
] | [
  IKeyboardCondition,
  React.KeyboardEventHandler<HTMLElement>,
  boolean,
];

export interface IUseKeyboardsOptions {
  /**
   * 键盘处理条件和回调
   */
  keyboards: IKeyboardPair[];
  /**
   * 总开关
   */
  on?: boolean;
  onFocus?: React.FocusEventHandler<HTMLElement>;
  onBlur?: React.FocusEventHandler<HTMLElement>;
  onKeyDown?: React.KeyboardEventHandler<HTMLElement>;
  onKeyUp?: React.KeyboardEventHandler<HTMLElement>;
}

export default (options: IUseKeyboardsOptions = {} as IUseKeyboardsOptions) => {
  const { keyboards = [], on = true, onFocus, onBlur, onKeyDown, onKeyUp } = options;
  // 当前是否聚焦
  const [isFocused, setIsFocused] = useState(false);

  // 记录当前按下的所有键位的keyCode
  const keyPressed = useRef<number[]>([]);

  // 当on变化时，直接清除
  if (on === false) {
    keyPressed.current = [];
  }

  /**
   * 是否需要进行键位检测
   */
  const hasKeyboard = on && keyboards.some(([,, enable]) => {
    return enable || (typeof enable === 'undefined' && isFocused);
  });

  /**
   * 聚焦
   *
   * @param {React.FocusEvent<HTMLElement>} e
   * @return {void}
   */
  const handleFocus = useCallback((e: React.FocusEvent<HTMLElement>) => {
    setIsFocused(true);
    if (onFocus) {
      onFocus(e);
    }
  }, [onFocus]);

  /**
   * 失焦
   *
   * @param {React.FocusEvent<HTMLElement>} e
   * @return {void}
   */
  const handleBlur = useCallback((e: React.FocusEvent<HTMLElement>) => {
    setIsFocused(false);
    if (onBlur) {
      onBlur(e);
    }
  }, [onBlur]);


  /**
   * 按下键盘时处理
   *
   * @param {React.FocusEvent<HTMLElement>} e
   * @return {void}
   */
  const handleKeyDown = useCallback((e: React.KeyboardEvent<HTMLElement>) => {
    const keyCode = e.which;

    if (keyPressed.current.indexOf(keyCode) === -1) {
      keyPressed.current.push(keyCode);
    }
    keyboards.forEach(([keyCodes, handler, enable]) => {
      const enableKeyboard = typeof enable === 'boolean' ? enable && isFocused : isFocused;
      if (enableKeyboard) {
        let meetKeyCodes = false;
        if (Array.isArray(keyCodes)) {
          meetKeyCodes = isEqual(keyCodes, keyPressed.current);
        } else if ((keyCodes as { and: number[] }).and) {
          meetKeyCodes = isEqual((keyCodes as { and: number[] }).and, keyPressed.current);
        } else if ((keyCodes as { or: number[] }).or) {
          meetKeyCodes = (keyCodes as { or: number[] }).or.some((orCode) => {
            return keyPressed.current.indexOf(orCode) !== -1;
          });
        }
        if (meetKeyCodes) {
          handler(e);
          e.preventDefault();
        }
      }
    });
    if (onKeyDown) {
      onKeyDown(e);
    }
  }, [onKeyDown, keyboards, isFocused]);

  /**
   * 键盘放开的处理
   *
   * @param {React.FocusEvent<HTMLElement>} e
   * @return {void}
   */
  const handleKeyUp = useCallback((e: React.KeyboardEvent<HTMLElement>) => {
    const keyCode = e.which;
    const index = keyPressed.current.indexOf(keyCode);
    if (index !== -1) {
      keyPressed.current.splice(index, 1);
    }
    if (onKeyUp) {
      onKeyUp(e);
    }
  }, [onKeyUp]);

  return {
    onKeyDown: hasKeyboard ? handleKeyDown : onKeyDown,
    onKeyUp: hasKeyboard ? handleKeyUp : onKeyUp,
    onFocus: handleFocus,
    onBlur: handleBlur,
  };
};
