/* eslint-disable max-lines-per-function */
import * as React from 'react';
import { useTranslate } from '~/locale/locales';
import {
  message,
} from '~/components/message';
import {
  Tooltip,
} from '~/components/tooltip';
import {
  WebBetaNormal,
  EditorBorderBetaNormal,
  CopyNormal,
  DeleteNormal,
} from '~/components/icons';
import { copyToClipboard, throttle } from '~/utils';
import { ToolbarWrapper, LinkHref, LinkHrefWrapper, ToolbarIconWrapper } from './styled';

export interface ILinkToolbarProps {
  /**
   * URL链接
   */
  href: string;
  /**
   * 链接跳转的回调函数
   */
  onOpenLink: (url: string) => void;
  /**
   * 点击“编辑”按钮时的回调函数
   */
  onEdit: () => void;
  /**
   * 点击“移除”按钮时的回调函数
   */
  onRemove: () => void;
  /**
   * 隐藏组件的方法，如按“ESC”退出
   */
  onHide?: () => void;
  /**
   * 拷贝成功后的回调函数
   */
  onCopied?: (href: string) => void;
  /**
   * 本地语言类型，如:zh-CN
   */
  locale?: string;
  /**
   * 自定义样式，如用于定位position、z-index、left、top等
   */
  style?: React.CSSProperties;
  /**
   * 是否只读
   */
  readOnly?: boolean;
}

export const LinkToolbar: React.FC<ILinkToolbarProps> = React.memo((props) => {
  const {
    href,
    onEdit,
    onRemove,
    onOpenLink,
    onHide,
    onCopied = throttle(() => message.success(t('we_ui_link_toolbar_copied')), 3000),
    locale,
    style = {},
    readOnly = false,
  } = props;

  const t = useTranslate(locale);

  const handleVisit = React.useCallback((event: React.MouseEvent) => {
    event.preventDefault();
    if (event.button !== 2) {
      onOpenLink(href);
    }
  }, [href, onOpenLink]);

  const handleCopy = React.useCallback((event: React.MouseEvent) => {
    event.preventDefault();
    copyToClipboard(href, onCopied);
  },
  [href, onCopied]);

  const handleRemove = React.useCallback((event: React.MouseEvent) => {
    event.preventDefault();
    onRemove();
  }, [onRemove]);

  React.useEffect(() => {
    const handleKeyDown = (event: KeyboardEvent) => {
      const key = event.key.toLowerCase();
      if (key.toLowerCase() === 'escape' || event.keyCode === 27) {
        event.preventDefault();
        // ESC 退出
        if (onHide) {
          onHide();
        }
      }
    };
    document.addEventListener('keydown', handleKeyDown);
    return () => {
      document.removeEventListener('keydown', handleKeyDown);
    };
  }, [onHide]);

  return (
    <ToolbarWrapper
      data-testid="link-toolbar"
      style={style}
    >
      <WebBetaNormal />
      <LinkHrefWrapper>
        <Tooltip title={t('we_ui_link_open_link')}>
          <LinkHref onMouseDown={handleVisit} data-testid="link-toolbar-visit">
            {href}
          </LinkHref>
        </Tooltip>
      </LinkHrefWrapper>

      {
        !readOnly && (
          <Tooltip title={t('we_ui_link_toolbar_edit')}>
            <ToolbarIconWrapper
              data-testid="link-toolbar-edit"
              onClick={onEdit}
            >
              <EditorBorderBetaNormal />
            </ToolbarIconWrapper>
          </Tooltip>
        )
      }


      <Tooltip title={t('we_ui_link_toolbar_copy')}>
        <ToolbarIconWrapper
          data-testid="link-toolbar-copy"
          onClick={handleCopy}
        >
          <CopyNormal />
        </ToolbarIconWrapper>
      </Tooltip>

      {
        !readOnly && (
          <Tooltip title={t('we_ui_link_delete')}>
            <ToolbarIconWrapper
              data-testid="link-toolbar-remove"
              onClick={handleRemove}
            >
              <DeleteNormal />
            </ToolbarIconWrapper>
          </Tooltip>
        )
      }

    </ToolbarWrapper>
  );
});
