import React from 'react';
import {
  BorderAllNormal,
  BorderBottomNormal,
  BorderHorizontalNormal,
  BorderInnerNormal,
  BorderLeftNormal,
  BorderOuterNormal,
  BorderRightNormal,
  BorderTopNormal,
  BorderVerticalNormal,
} from '~/components/icons';
import { BorderTypeWrapper, IconWrapper } from './styled';
import { Tooltip } from '../tooltip';
import { TranslateProvider } from '~/locale/TranslateProvider';
import { isWhite } from '~/utils';

export enum BorderType {
  top = 'top',
  bottom = 'bottom',
  left = 'left',
  right = 'right',
  none = 'none',
  outer = 'outer',
  inner = 'inner',
  horizon = 'horizon',
  vertical = 'vertical',
  all = 'all',
  unselected = 'unselected',
}

export interface IBorderTypePanelProps {
  /**
   * 选中的边框类型
   */
  borderType?: BorderType;
  /**
   * 边框图标的大小
   */
  borderIconSize?: number;
  /**
   * 图标中边框部分的颜色
   */
  borderSelectedColor?: string;
  /**
   * BorderTypeWrapper 的样式
   */
  style?: React.CSSProperties;
  /**
   * 边框类型变化时的回调函数
   */
  onBorderTypeChange: (type: BorderType) => void;
}

type TypeUnselected = 'unselected';

const UNSELECTED = 'unselected';

export class BorderTypePanel extends React.PureComponent<IBorderTypePanelProps, {
  // 当前选中的边框类型
  currentBorderType: BorderType | TypeUnselected;
}> {
  static defaultProps = {
    borderIconSize: 18,
  };

  constructor(props: IBorderTypePanelProps) {
    super(props);
    const currentBorderType = props.borderType ? props.borderType : UNSELECTED;
    this.state = {
      currentBorderType,
    };
  }

  handleBorderTypeChange = (borderType: BorderType) => {
    this.setState({ currentBorderType: borderType });
    const { onBorderTypeChange } = this.props;
    if (onBorderTypeChange) {
      onBorderTypeChange(borderType);
    }
  };

  render() {
    const { borderType, borderIconSize = 18, borderSelectedColor = '#000000', style } = this.props;
    const { currentBorderType } = this.state;
    const activeBorderType = borderType || currentBorderType;

    const defaultWrapperStyle = {
      width: '100px',
      height: '100px',
    };
    const wrapperStyle = { ...defaultWrapperStyle, ...(style || {}) };

    const iconStyle: React.CSSProperties = {
      width: borderIconSize,
      height: borderIconSize,
      boxSizing: 'border-box',
      padding: '1px 1px',
    };

    const ICON_DEFAULT_COLOR = 'rgba(17, 31, 44, 0.72)';

    const pathColors = [
      // 当选中颜色为白色时，选中边框的外边缘为黑色，否则选中边框外边缘与内部颜色一致
      (!borderSelectedColor || isWhite(borderSelectedColor))
        ? ICON_DEFAULT_COLOR
        : borderSelectedColor,
      borderSelectedColor || ICON_DEFAULT_COLOR,
      ICON_DEFAULT_COLOR,
    ];

    const borderList: Record<string, React.ReactNode> = {
      [BorderType.outer]: <BorderOuterNormal pathColors={pathColors} style={iconStyle} />,
      [BorderType.inner]: <BorderInnerNormal pathColors={pathColors} style={iconStyle} />,
      [BorderType.all]: <BorderAllNormal pathColors={pathColors} style={iconStyle} />,
      [BorderType.left]: <BorderLeftNormal pathColors={pathColors} style={iconStyle} />,
      [BorderType.vertical]: <BorderVerticalNormal pathColors={pathColors} style={iconStyle} />,
      [BorderType.right]: <BorderRightNormal pathColors={pathColors} style={iconStyle} />,
      [BorderType.top]: <BorderTopNormal pathColors={pathColors} style={iconStyle} />,
      [BorderType.horizon]: <BorderHorizontalNormal pathColors={pathColors} style={iconStyle} />,
      [BorderType.bottom]: <BorderBottomNormal pathColors={pathColors} style={iconStyle} />,
    };

    return (
      <BorderTypeWrapper style={wrapperStyle} >
        {Object.keys(borderList).map(key => {
          const iconComp = borderList[key];
          const tipTitle = `we_ui_border_set_border_${BorderType[key as BorderType]}`;
          return (
            <TranslateProvider key={key}>
              {(t) => (
                <Tooltip title={t(tipTitle)}>
                  <IconWrapper
                    active={activeBorderType === key}
                    // style={btStyle}
                    onClick={() => this.handleBorderTypeChange(key as BorderType)}
                  >
                    {iconComp}
                  </IconWrapper>
                </Tooltip>
              )}
            </TranslateProvider>
          );
        })}
      </BorderTypeWrapper>
    );
  }
}
