/* eslint-disable no-plusplus */
/* eslint-disable @typescript-eslint/explicit-function-return-type */
import React from 'react';
import ReactDOM from 'react-dom';
import CSSTransitionGroup from 'react-transition-group/CSSTransitionGroup';
import {
  RoundcloseThickNormal,
  ReportFillNormal,
  LoadingNormal,
  CheckboxFillNormal,
} from '~/components/icons';
import { classnames } from '~/utils';
import {
  ContainerRoot,
  MessageWrap,
  MessageContent,
  Container,
  MessageBox,
  MessageFlex,
} from './styled';

type MessageType = 'success' | 'info' | 'warning' | 'error' | 'loading';

export interface IMessageConfig {
  key?: string;
  duration?: number;
  onClose?: () => void;
  content: string | React.ReactElement;
  type: MessageType;
  animated?: boolean;
}

export interface IMessageProps {
  messages: Array<Required<IMessageConfig>>;
}

export const Message: React.FC<IMessageProps> = (props) => {
  const {
    messages,
  } = props;

  const IconNodes: Record<MessageType, React.ReactNode> = {
    success: <CheckboxFillNormal className="icon success-icon" />,
    error: <RoundcloseThickNormal className="icon error-icon" />,
    info: <ReportFillNormal className="icon info-icon" />,
    warning: <ReportFillNormal className="icon warning-icon" />,
    loading: <LoadingNormal spin className="icon loading-icon" />,
  };

  return (
    <ContainerRoot>
      <Container>
        <CSSTransitionGroup
          transitionName="message"
          transitionEnterTimeout={240}
          transitionLeaveTimeout={240}
        >
          {
            messages.map((msg) => {
              const { key, type, content, animated = true } = msg;
              return (
                <MessageBox key={key}>
                  <MessageWrap className={classnames({ animated })}>
                    <MessageFlex>
                      {IconNodes[type]}
                      <MessageContent>{content}</MessageContent>
                    </MessageFlex>
                  </MessageWrap>
                </MessageBox>
              );
            })
          }
        </CSSTransitionGroup>
      </Container>
    </ContainerRoot>
  );
};

export const message = (() => {
  let uuid = 0;
  let messages: IMessageConfig[] = [];
  let handleUpdate: any = null!;

  function init() {
    if (handleUpdate) {
      return;
    }
    const oDiv = document.createElement('div');
    oDiv.style.position = 'absolute';
    oDiv.style.width = '100%';
    oDiv.style.top = '0px';
    oDiv.style.left = '0px';

    document.body.appendChild(oDiv);

    handleUpdate = (msgs: Array<Required<IMessageConfig>>) => {
      ReactDOM.render(
        <Message messages={msgs} />,
        oDiv,
      );
    };
  }

  const getConfig = (config: string | IMessageConfig, duration?: number): IMessageConfig => {
    const newConfig = (typeof config === 'string' ? {
      content: config,
      duration: typeof duration !== 'undefined' ? duration : 3,
    } : config) as IMessageConfig;

    newConfig.key = newConfig.key || `msgid-${++uuid}`;
    if (uuid > 10000000) {
      uuid = 0;
    }

    return newConfig;
  };

  return {
    open(config: IMessageConfig) {
      init();
      messages = [...messages, config];
      handleUpdate(messages as Array<Required<IMessageConfig>>);
      if ((config.duration as number) > 0) {
        setTimeout(() => {
          const { key, onClose } = config;
          this.close(key);
          onClose && onClose();
        }, (config.duration as number) * 1000);
      }
    },

    close(key?: string) {
      if (key) {
        const index = messages.findIndex((m) => m.key === key);
        if (index !== -1) {
          messages = [...messages];
          messages.splice(index, 1);
        }
      } else {
        messages = [];
      }
      if (handleUpdate) {
        handleUpdate(messages as Array<Required<IMessageConfig>>);
      }
    },

    success(config: string | IMessageConfig, duration?: number) {
      config = getConfig(config, duration);
      config.type = 'success';
      this.open(config);
    },

    error(config: string | IMessageConfig, duration?: number) {
      config = getConfig(config, duration);
      config.type = 'error';
      this.open(config);
    },

    warning(config: string | IMessageConfig, duration?: number) {
      config = getConfig(config, duration);
      config.type = 'warning';
      this.open(config);
    },

    loading(config: string | IMessageConfig, duration?: number) {
      config = getConfig(config, duration);
      config.type = 'loading';
      this.open(config);
    },

    info(config: string | IMessageConfig, duration?: number) {
      config = getConfig(config, duration);
      config.type = 'info';
      this.open(config);
    },
  };
})();
