import React from 'react';
import { Tooltip } from '~/components/tooltip';
import { BoxSingleWrap, BoxVerticalWrap, BoxHorizontalWrap, ToolBoxText } from './styled';
import { Button, ButtonSize } from '~/components/button';
import { classnames } from '~/utils';


export type childrenType = React.ReactNodeArray | React.ReactElement<any> | null;

export enum EToolBoxLayout {
  /**
   * 单独icon，名称通过tooltip显示
  */
  Single = 'single',
  /**
   * 垂直布局(上面icon，下面名称)
  */
  Vertical = 'vertical',
  /**
   * 水平布局(左侧icon，右侧名称)
   */
  Horizontal = 'horizontal',
}

export interface IToolBoxProps {
  /**
   * 布局方式
  */
  layout?: EToolBoxLayout;
  /**
   * 工具名字
  */
  name?: string;
  /**
  * 包裹的组件
  */
  children?: childrenType;
  /**
   * 类名
   */
  className?: string;
  /**
   * 点击
   */
  onClick?: (e: React.MouseEvent<HTMLElement>) => void;
  /**
   * 双击
   */
  onDoubleClick?: (e: React.MouseEvent<HTMLElement>) => void;
  /**
   * 鼠标移入的事件
   */
  onMouseEnter?: () => void;
  /**
   * 鼠标移出的事件
   */
  onMouseLeave?: () => void;
  /**
   * 是否禁用
   */
  disabled?: boolean;
  /**
   * 禁用时显示文案，disabled 为 true 时，优先级高于 name
   */
  disabledTip?: string;
}


export class ToolBox extends React.PureComponent<IToolBoxProps> {
  static defaultProps = {
    layout: EToolBoxLayout.Single,
    disabled: false,
  };

  handleMouseEnter = () => {
    const { onMouseEnter } = this.props;
    if (typeof onMouseEnter === 'function') {
      onMouseEnter();
    }
  };

  handleMouseLeave = () => {
    const { onMouseLeave } = this.props;
    if (typeof onMouseLeave === 'function') {
      onMouseLeave();
    }
  };

  renderSingle = () => {
    const { children, name, className, disabled, disabledTip } = this.props;
    const child = React.Children.only(children) as React.ReactElement;
    const innerNode = (
      <BoxSingleWrap className={className} disabled={disabled} >
        {child}
      </BoxSingleWrap>
    );
    return (
      <Tooltip title={disabled ? (disabledTip || name) : name}>{innerNode}</Tooltip>
    );
  };

  renderVertical = () => {
    const { children, name, onClick, onDoubleClick, className, disabled, disabledTip } = this.props;
    return (
      <Tooltip title={disabled && disabledTip}>
        <BoxVerticalWrap
          onClick={onClick}
          onDoubleClick={onDoubleClick}
          className={className}
          onMouseEnter={this.handleMouseEnter}
          onMouseLeave={this.handleMouseLeave}
          disabled={disabled}
        >
          {children}
          <ToolBoxText>{name}</ToolBoxText>
        </BoxVerticalWrap>
      </Tooltip>
    );
  };

  renderHorizontal = () => {
    const { children, name, onClick, onDoubleClick, className, disabled, disabledTip } = this.props;
    return (
      <Tooltip title={disabled && disabledTip}>
        <BoxHorizontalWrap
          onClick={onClick}
          onDoubleClick={onDoubleClick}
          className={className}
          onMouseEnter={this.handleMouseEnter}
          onMouseLeave={this.handleMouseLeave}
          disabled={disabled}
        >
          {children}
          <ToolBoxText>{name}</ToolBoxText>
        </BoxHorizontalWrap>
      </Tooltip >
    );
  };

  render() {
    const { layout } = this.props;
    return (
      <>
        {layout === EToolBoxLayout.Single ? this.renderSingle() : null}
        {layout === EToolBoxLayout.Vertical ? this.renderVertical() : null}
        {layout === EToolBoxLayout.Horizontal ? this.renderHorizontal() : null}
      </>
    );
  }
}

export type ToolBoxArray = Array<React.ReactElement<IToolBoxProps>>;

export interface IToolButtonProps extends IToolBoxProps {
  /**
   * 设置按钮的图标组件
   */
  icon?: React.ReactNode;
  /**
   * 是否为激活态
   */
  active?: boolean;
  /**
   * Button的样式
   */
  style?: React.CSSProperties;
  /**
   * 暴露 data-testid, 用于测试
   */
  testid?: string;
  /**
   * ToolBox 的类名
   */
  boxClassName?: string;
  /**
   * button 的大小
   */
  size?: ButtonSize;
  /**
   * 是否禁用
   */
  disabled?: boolean;
  /**
   * 禁用时显示文案，disabled 为 true 时，优先级高于 name
   */
  disabledTip?: string;
}


export const ToolButton: React.FC<IToolButtonProps> = React.memo((props) => {
  const { layout = EToolBoxLayout.Single, name, icon, disabled, active, style, className, testid, boxClassName, size, disabledTip } = props;
  const [hovered, setHovered] = React.useState<boolean>(false);
  const cName = hovered ? 'toolBoxHover' : '';
  const onClick = disabled ? undefined : props.onClick;
  const onDoubleClick = disabled ? undefined : props.onDoubleClick;
  return (
    <ToolBox
      layout={layout}
      name={name}
      // vertical 或者 horizontal 布局时，点击文字区域也能响应 click 事件
      onClick={layout === EToolBoxLayout.Single ? undefined : onClick}
      onDoubleClick={layout === EToolBoxLayout.Single ? undefined : onDoubleClick}
      className={boxClassName}
      onMouseEnter={() => setHovered(true)}
      onMouseLeave={() => setHovered(false)}
      disabled={disabled}
      disabledTip={disabledTip}
    >
      <Button
        bordered={false}
        shape="rect"
        icon={icon}
        active={active}
        onClick={layout === EToolBoxLayout.Single ? onClick : undefined}
        onDoubleClick={layout === EToolBoxLayout.Single ? onDoubleClick : undefined}
        tabIndex="false"
        style={style}
        className={classnames('tool-button', cName, className)}
        testid={testid}
        size={size}
      />
    </ToolBox>
  );
});
