import styled from 'styled-components';
import { withTheme } from '../theme';
import { Ellipsis } from '~/utils/styled.mixin';

export const TriggerWrap = withTheme(styled.div`
  ${Ellipsis}
  width: 42px;
  margin-right: 4px;
  font-size: ${({ theme }) => theme.ui.fontSizeSmall};
  line-height: 20px;
`);

