import React, { useMemo, useCallback } from 'react';
import useControlledValue from '~/hooks/useControlledValue';
import { useTranslate } from '~/locale/locales';
import {
  OverflowNormal,
  ClipNormal,
  WrapNormal,
} from '../icons';
import { Select, SelectOption } from '../select';
import { OptionWrap, TriggerWrap } from './styled';
import { TOptionTitle } from '../select/interface';

export type TableWrap = 'overflow' | 'auto-wrap' | 'line-clip';

export interface ITableWrapSelectProps {
  /**
   * 默认的换行方式，当设置了value，该项忽略
   */
  defaultValue?: TableWrap;
  /**
   * 换行方式
   */
  value?: TableWrap;
  /**
   * 设置国际化语言
   */
  locale?: string;
  /**
   * select 样式
   */
  style?: React.CSSProperties;
  /**
   * 对齐方式修改时的回调函数
   */
  onChange?: (align: TableWrap) => void;
  /**
   * 层级
   */
  zIndex?: number;
  /**
   * tooltip提示
   */
  title?: string | React.ReactNode;
  /**
   * 选项的tooltip提示
   */
  titles?: Record<string, TOptionTitle>;
  /**
   * 类名
   */
  className?: string;
  /**
   * 是否现实选中值
   */
  showTitle?: boolean;
  /**
   *  是否禁用
   */
  disabled?: boolean;
}

const TableWrapSelect: React.FC<ITableWrapSelectProps> = (props) => {
  const { locale, style, onChange, title, titles, zIndex, className, showTitle, disabled } = props;
  const [value, setValue] = useControlledValue(props.value, props.defaultValue);
  const t = useTranslate(locale);

  // 垂直对齐的选项
  const options = useMemo(() => [
    { title: t('we_ui_wrap_overflow'), value: 'overflow', Icon: OverflowNormal },
    { title: t('we_ui_wrap_auto-wrap'), value: 'auto-wrap', Icon: WrapNormal },
    { title: t('we_ui_wrap_line-clip'), value: 'line-clip', Icon: ClipNormal },
  ], [t]);

  // 该组件的trigger区域只显示Icon
  const renderTrigger = useCallback((align?: string) => {
    const index = options.findIndex((option) => option.value === align);
    const currentOption = options[index];
    return (
      <TriggerWrap className="we-ui-table-wrap-select-trigger" style={{ minWidth: 16 }}>
        {
          currentOption && (
            <>
              <currentOption.Icon />
              {
                showTitle && <span className="title">{currentOption.title}</span>
              }
            </>
          )
        }
      </TriggerWrap>
    );
  }, [options, showTitle]);

  const handleChange = useCallback((e: TableWrap) => {
    if (onChange) {
      onChange(e);
    }
    setValue(e);
  }, [setValue, onChange]);

  return (
    <Select
      value={value}
      onChange={handleChange}
      style={style}
      renderTrigger={renderTrigger}
      zIndex={zIndex}
      title={title}
      className={className}
      disabled={disabled}
      testid="TableWrapSelect"
    >
      {
        options.map((option) => (
          <SelectOption value={option.value} key={option.value} title={titles![option.value]}>
            <OptionWrap>
              <option.Icon className="icon" />
              <span>{option.title}</span>
            </OptionWrap>
          </SelectOption>
        ))
      }
    </Select>
  );
};

TableWrapSelect.defaultProps = {
  defaultValue: 'overflow',
  titles: {},
};

export default TableWrapSelect;
