/* eslint-disable max-lines-per-function */
import React from 'react';
import { Dropdown } from '~/components/dropdown';
import warning from 'warning';
import {
  AvatarGroupWrap,
  GroupAvatarItem,
  GroupAvatarList,
  GroupMoreItem,
  GroupMoreItemSpan,
  OverlayWrap,
  CountWrap,
  OverlayAvatarItem,
  OverlayAvatarWrap,
  OverlayAvatarName,
  ListWrap,
  // OverlayAvatarStatus,
} from './styled';
import { EditorNormal, ViewNormal } from '../icons';

export interface IAvatarGroupProps {
  /**
   * 显示区最多显示多少个
   */
  max?: number;
  /**
   * 排列方向，依次为：左到右、右到左
   */
  direction: 'ltr' | 'rtl';
  /**
   * 样式
   */
  style?: React.CSSProperties;
  /**
   * 类名
   */
  className?: string;
  /**
   * 层级
   */
  zIndex?: number;
  /**
   *  点击头像组中头像（或 dropdown 列表中选项）的回调函数
   *  idx 为头像组中点击的头像的序号 (0-based)
   */
  onClick?: (idx: number) => void;
  /**
   * overlay的样式
   */
  overlayStyle?: React.CSSProperties;
  /**
   * 弹层最大高度
   */
  overlayMaxHeight?: number;
}

// 固化头像组中颜色角标的选用和顺序
const COLOR_SCHEME = [
  '#0984FF',
  '#12C764',
  '#BF5AF2',
  '#FF9F0C',
  '#3BBEFA',
  '#FFCC01',
  '#99CAFF',
  '#FFD399',
];
const COLOR_SCHEME_LEN = COLOR_SCHEME.length;

const defaultStyle = {
  width: '165px',
};

const overlayIconSize: React.CSSProperties = {
  width: 20,
  height: 20,
};

const AvatarGroup: React.FC<IAvatarGroupProps> = React.memo((props) => {
  const {
    children,
    max,
    className,
    style,
    direction,
    zIndex,
    onClick,
    overlayStyle,
    overlayMaxHeight,
  } = props;

  // 数量
  const avatarCount = React.Children.count(children);
  const isOverflowing = avatarCount > max!;

  // 子组件的类型判定
  React.Children.forEach(children, (child) => {
    warning(
      React.isValidElement(child) && (child.type as any).displayName === 'Avatar',
      'AvatarGroup 的子组件应该为 Avatar 组件.',
    );
  });

  const avatarWidth = isOverflowing ? `${100 / (max! + 1)}%` : `${100 / avatarCount}%`;
  const avatarStyle = React.useMemo(() => ({ width: avatarWidth, maxWidth: 28 }), [avatarWidth]);

  return (
    <AvatarGroupWrap
      style={{ ...defaultStyle, ...style }}
      className={className}
    >
      <GroupAvatarList direction={direction}>
        {
          React.Children.toArray(children).slice(0, max!).map((child: any, idx: number) => {
            return (
              <GroupAvatarItem key={child.props.name} style={avatarStyle}>
                {
                  React.cloneElement(child, {
                    color: child.props.color || COLOR_SCHEME[idx % COLOR_SCHEME_LEN],
                    onClick: child.props.onClick || (() => onClick?.(idx)),
                  })
                }
              </GroupAvatarItem>
            );
          })
        }
        {
          isOverflowing && (
            <GroupAvatarItem
              className="more"
              style={{ width: avatarWidth, maxWidth: 28 }}
            >
              <Dropdown
                overlayStyle={overlayStyle}
                overlay={(
                  <OverlayWrap>
                    <CountWrap>共 {avatarCount} 人</CountWrap>
                    <ListWrap style={{ maxHeight: overlayMaxHeight }}>
                      {
                        React.Children.toArray(children).map((child: any, idx: number) => {
                          const { name, status, color } = child.props;
                          const avatarColor = color || COLOR_SCHEME[(idx) % COLOR_SCHEME_LEN];
                          const iconProps = {
                            style: { color: avatarColor },
                            className: 'status-icon',
                          };

                          return (
                            <OverlayAvatarItem key={name} onClick={() => onClick?.(idx)}>
                              <OverlayAvatarWrap>
                                {
                                  React.cloneElement(child, {
                                    title: '',
                                    style: overlayIconSize,
                                    isInOverlay: true,
                                  })
                                }
                              </OverlayAvatarWrap>
                              <OverlayAvatarName>
                                {name || ''}
                              </OverlayAvatarName>
                              {/* {
                                showStatus && (
                                  <OverlayAvatarStatus>
                                    {status}
                                  </OverlayAvatarStatus>
                                )
                              } */}
                              {(status === 'can-edit' || status === 'is-editing')
                                ? <EditorNormal {...iconProps} />
                                : <ViewNormal {...iconProps} />}
                            </OverlayAvatarItem>
                          );
                        })
                      }
                    </ListWrap>
                  </OverlayWrap>
                )}
                zIndex={zIndex}
              >
                <GroupMoreItem className="more-item-span">
                  {
                    (Number(avatarCount) < 999)
                      ? (<GroupMoreItemSpan>{`${avatarCount}`}</GroupMoreItemSpan>)
                      : (<><i /> <i /> <i /></>)
                  }
                </GroupMoreItem>
              </Dropdown>
            </GroupAvatarItem>
          )
        }
      </GroupAvatarList>
    </AvatarGroupWrap>
  );
});

AvatarGroup.defaultProps = {
  max: 8,
  direction: 'ltr',
};

export { AvatarGroup };
