/* eslint-disable @typescript-eslint/explicit-function-return-type */
import React, { useRef } from 'react';
import warning from 'warning';
import { classnames } from '~/utils';
import { ClosedNormal } from '../icons';
import { ISaveRef } from './interface';
import { TabBarTab, TabBarTabItemWrap, TabBarWrapper } from './styled';
import { ITabBarProps } from './TabBar';

interface ITabBarTabsNodeProps extends ITabBarProps {
  saveRef: ISaveRef;
  panes: React.ReactNode;
  id?: string;
}

const TabBarTabsNode: React.FC<ITabBarTabsNodeProps> = (props) => {
  const { saveRef, panes, size, activeKey, type, className, tabPosition, testid } = props;
  const rst: React.ReactElement[] = [];
  // 下面两个ref是为了缓存函数引用，根据key进行缓存
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  const onTabClicks = useRef<any>({});
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  const onTabCloses = useRef<any>({});

  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  React.Children.forEach(panes as React.ReactElement[], (child: any) => {
    if (!child) {
      return;
    }
    const key = child.key as string;
    const isActived = activeKey === key;

    if (!onTabClicks.current[key] || onTabClicks.current[key] !== props.onTabClick) {
      onTabClicks.current[key] = (e: React.MouseEvent<HTMLDivElement>) => {
        if (props.onTabClick) {
          props.onTabClick(key, e);
        }
      };
    }

    if (!onTabCloses.current[key] || onTabCloses.current[key] !== props.onClose) {
      onTabCloses.current[key] = (e: React.MouseEvent<HTMLElement>) => {
        e.stopPropagation();
        if (props.onClose) {
          props.onClose(key);
        }
      };
    }

    warning('tab' in child.props, 'There must be `tab` property on children of Tabs.');

    const id = props.id ? `${key}-${props.id}` : key;

    const closable = typeof child.props.closable !== 'undefined' ? !!child.props.closable : true;

    const node = (
      <TabBarTab
        role="tab"
        aria-disabled={child.props.disabled ? 'true' : 'false'}
        aria-selected={isActived ? 'true' : 'false'}
        aria-controls={`tabpane-${id}`}
        tabIndex={isActived ? 0 : -1}
        key={key}
        id={`tab-${id}`}
        className={classnames(type, size, tabPosition, className, {
          disabled: !!child.props.disabled,
          actived: isActived,
        })}
        ref={isActived ? saveRef('activeTab') : undefined}
        onClick={!isActived && !child.props.disabled ? onTabClicks.current[key] : undefined}
        data-testid={`${testid}_${id}`}
      >
        <TabBarTabItemWrap className={classnames(type, size, tabPosition, className, { actived: isActived })}>
          {child.props.tab}
          {
            (type === 'editable-card') && closable && (
              <ClosedNormal
                onClick={onTabCloses.current[key]}
                className="card-tab-close-button"
                style={{ fontSize: 14 }}
              />
            )
          }
          {
            (type === 'editable-line') && closable && (
              <ClosedNormal
                onClick={onTabCloses.current[key]}
                className="line-tab-close-button"
                style={{ fontSize: 14 }}
              />
            )
          }
        </TabBarTabItemWrap>
      </TabBarTab>
    );

    rst.push(node);
  });

  return <TabBarWrapper className={classnames(tabPosition && `${tabPosition}`)} ref={saveRef('navTabsContainer')}>{rst}</TabBarWrapper>;
};

export default TabBarTabsNode;
