import React from 'react';
import * as Lines from './lines';
import {
  Select,
  SelectOption as Option,
} from '~/components/select';

// 注意：要确保这里的枚举值和lines中的组件名保持相同
export enum LineType {
  Thin = 'Thin',
  Medium = 'Medium',
  Dashed = 'Dashed',
  Dotted = 'Dotted',
  Thick = 'Thick',
  Double = 'Double',
  Hair = 'Hair',
  MediumDashed = 'MediumDashed',
  DashDot = 'DashDot',
  MediumDashDot = 'MediumDashDot',
  DashDotDot = 'DashDotDot',
  MediumDashDotDot = 'MediumDashDotDot',
  SlantedDashDot = 'SlantedDashDot',
}

export interface ILineSelectProps {
  /**
   * 线条类型的数组
   */
  lineTypes?: LineType[];
  /**
   * 默认的边框样式
   */
  lineTypeValue?: LineType;
  /**
   * 边框的颜色
   */
  color: string;
  /**
   * 线条类型发生变化时的回调函数
   */
  onLineTypeChange: (type: LineType) => void;
  /**
   * 层级
   */
  zIndex?: number;
  /**
   * 是否禁用
   */
  disabled?: boolean;
}

export class LineSelect extends React.PureComponent<ILineSelectProps, {
  // 当前选中的线条类型
  currentLineType: LineType;
}> {
  static defaultProps = {
    lineTypes: [
      LineType.Thin,
      LineType.Medium,
      LineType.Dashed,
      LineType.Dotted,
      LineType.Thick,
      LineType.Double,
      LineType.Hair,
      LineType.MediumDashed,
      LineType.DashDot,
      LineType.MediumDashDot,
      LineType.DashDotDot,
      LineType.MediumDashDotDot,
      LineType.SlantedDashDot,
    ],
    disabled: false,
  };

  constructor(props: ILineSelectProps) {
    super(props);
    const currentLineType = props.lineTypeValue ? props.lineTypeValue : LineType.Thin;
    this.state = {
      currentLineType,
    };
  }

  handleLineTypeChange = (lineType: LineType) => {
    this.setState({ currentLineType: lineType });
    const { onLineTypeChange } = this.props;
    if (onLineTypeChange) {
      onLineTypeChange(lineType);
    }
  };

  getLinesList = (): Record<string, React.ReactNode> => {
    const { lineTypes } = this.props;
    const renderLines = lineTypes !== undefined ? lineTypes : [];
    const lineComps: Record<string, React.ReactNode> = {};
    renderLines.forEach((lineTypItem) => {
      if (Lines[lineTypItem]) {
        lineComps[lineTypItem] = Lines[lineTypItem];
      }
    });
    return lineComps;
  };

  render() {
    const linesList = this.getLinesList();
    const { lineTypeValue, color, zIndex, disabled } = this.props;
    const { currentLineType } = this.state;
    const selectedValue = lineTypeValue || currentLineType;

    return (
      <Select
        value={selectedValue}
        style={{ width: 127 }}
        overlayStyle={{ maxHeight: 400 }}
        onChange={this.handleLineTypeChange}
        zIndex={zIndex}
        disabled={disabled}
      >
        {Object.keys(linesList).map((key) => {
          const LineComp: any = linesList[key];
          return (
            <Option key={key} value={key}>
              <LineComp color={color} width={80} />
            </Option>
          );
        })}
      </Select>
    );
  }
}
