/* eslint-disable max-lines-per-function */
/* eslint-disable react/destructuring-assignment */
/* eslint-disable @typescript-eslint/no-explicit-any */
/* eslint-disable class-methods-use-this */
/* eslint-disable @typescript-eslint/explicit-function-return-type */
/* eslint-disable react/static-property-placement */
import React, { Component } from 'react';
import ResizeObserver from 'resize-observer-polyfill';
import { ArrowLeftNormal, ArrowRightNormal, ArrowDownNormal } from '../icons';
import { debounce, classnames } from '~/utils';
import { setTransform, isTransform3dSupported, noop } from './utils';
import {
  NavContainer,
  NavWrap,
  NavScroll,
  Nav,
  PrevButton,
  NextButton,
  DropdownArrow,
  DropdownWrap,
  DropdownItemWrap,
} from './styled';
import { ITabsProps } from './Tabs';
import { ISaveRef, IGetRef, ExcessMode } from './interface';
import { Dropdown } from '../dropdown';

interface ITabBarNodeProps extends ITabsProps {
  saveRef: ISaveRef;
  getRef: IGetRef;
  panes: React.ReactNode;
}

interface ITabBarNodeState {
  next: boolean;
  prev: boolean;
  needsDropdown: boolean;
}

export default class TabBarNode extends Component<ITabBarNodeProps, ITabBarNodeState> {
  static defaultProps = {
    tabBarPosition: 'left',
    prefixCls: '',
    onPrevClick: noop,
    onNextClick: noop,
  };

  private offset: number;

  private resizeObserver: any;

  private debouncedResize: any;

  private lastNextPrevShown: any;

  constructor(props: ITabBarNodeProps) {
    super(props);
    this.offset = 0;
    this.state = {
      next: false,
      prev: false,
      needsDropdown: false,
    };
  }

  componentDidMount() {
    const { getRef, excessMode } = this.props;
    this.componentDidUpdate();
    let debounceCallback = noop;
    if (excessMode === 'arrow') {
      debounceCallback = () => {
        this.setNextPrev();
        this.scrollToActiveTab();
      };
    } else if (excessMode === 'dropdown') {
      debounceCallback = () => {
        this.setNeedsDropdown();
        this.scrollToActiveTab();
      };
    }

    this.debouncedResize = debounce(debounceCallback, 200);
    this.resizeObserver = new ResizeObserver(this.debouncedResize);
    this.resizeObserver.observe(getRef('container'));
  }

  componentDidUpdate(prevProps?: any) {
    const { props } = this;
    const { excessMode } = props;
    if (excessMode === 'arrow') {
      const nextPrev = this.setNextPrev();
      // wait next, prev show hide
      if (this.isNextPrevShown(this.state) !== this.isNextPrevShown(nextPrev)) {
        // eslint-disable-next-line react/no-did-update-set-state
        this.setState({}, this.scrollToActiveTab);
      } else if (!prevProps || props.activeKey !== prevProps.activeKey) {
        // can not use props.activeKey
        this.scrollToActiveTab();
      }
    } else {
      const needsDropdown = this.setNeedsDropdown();
      if (needsDropdown !== this.state.needsDropdown) {
        this.scrollToActiveTab();
      } else if (!prevProps || props.activeKey !== prevProps.activeKey) {
        this.scrollToActiveTab();
      }
    }
  }

  componentWillUnmount() {
    if (this.resizeObserver) {
      this.resizeObserver.disconnect();
    }
    if (this.debouncedResize && this.debouncedResize.cancel) {
      this.debouncedResize.cancel();
    }
  }

  /**
   * 检测左右按钮的激活状态，并调整 offset
   */
  setNextPrev() {
    const { getRef } = this.props;
    const navNode = getRef('nav');
    const navTabsContainer = getRef('navTabsContainer');
    const navNodeWH = this.getScrollWH(navTabsContainer || navNode);
    // Add 1px to fix `offsetWidth` with decimal in Chrome not correct handle
    // https://github.com/ant-design/ant-design/issues/13423
    const containerWH = this.getOffsetWH(getRef('container')) + 1;
    const navWrapNodeWH = this.getOffsetWH(getRef('navWrap'));
    let { offset } = this;
    const minOffset = containerWH - navNodeWH;
    let { next, prev } = this.state;
    if (minOffset >= 0) {
      next = false;
      this.setOffset(0);
      offset = 0;
    } else if (minOffset < offset) {
      next = true;
    } else {
      next = false;
      // Fix https://github.com/ant-design/ant-design/issues/8861
      // Test with container offset which is stable
      // and set the offset of the nav wrap node
      const realOffset = navWrapNodeWH - navNodeWH;
      this.setOffset(realOffset);
      offset = realOffset;
    }

    if (offset < 0) {
      prev = true;
    } else {
      prev = false;
    }

    this.setNext(next);
    this.setPrev(prev);
    return {
      next,
      prev,
    };
  }

  /**
   * 获取节点的宽度
   *
   * @param {HTMLElement} node
   * @return {number} offsetWidth
   */
  getOffsetWH(node: HTMLElement) {
    return node.offsetWidth;
  }

  /**
   * 获取节点的滚动宽度
   *
   * @param {HTMLElement} node
   * @return {number} scrollWidth
   */
  getScrollWH(node: HTMLElement) {
    return node.scrollWidth;
  }

  /**
   * 获取节点的偏移量
   *
   * @param {HTMLElement} node
   * @return {number} clientRect.left
   */
  getOffsetLT(node: HTMLElement) {
    return node.getBoundingClientRect().left;
  }

  /**
   * 设置偏移量
   *
   */
  setOffset(offset: number) {
    const { getRef } = this.props;
    const target: number = Math.min(0, offset);
    if (this.offset !== target) {
      this.offset = target;
      let navOffset: any = {};
      const navStyle = getRef('nav').style;
      const transformSupported = isTransform3dSupported(navStyle);
      if (transformSupported) {
        navOffset = {
          value: `translate3d(${target}px,0,0)`,
        };
      } else {
        navOffset = {
          name: 'left',
          value: `${target}px`,
        };
      }
      if (transformSupported) {
        setTransform(navStyle, navOffset.value);
      } else {
        navStyle[navOffset.name] = navOffset.value;
      }
    }
  }

  setPrev(v: boolean) {
    if (this.state.prev !== v) {
      this.setState({
        prev: v,
      });
    }
  }

  setNext(v: boolean) {
    if (this.state.next !== v) {
      this.setState({
        next: v,
      });
    }
  }

  prevTransitionEnd = (e: React.TransitionEvent<HTMLDivElement>) => {
    if (e.propertyName !== 'opacity') {
      return;
    }
    const container = this.props.getRef('container');
    this.scrollToActiveTab({
      target: container,
      currentTarget: container,
    });
  };

  scrollToActiveTab = (e?: any) => {
    const { getRef, excessMode } = this.props;
    const activeTab = getRef('activeTab');
    const navWrap = getRef('navWrap');
    if ((e && e.target !== e.currentTarget) || !activeTab) {
      return;
    }

    // when not scrollable or enter scrollable first time, don't emit scrolling
    if (excessMode === 'arrow') {
      const needToSroll = this.isNextPrevShown() && this.lastNextPrevShown;
      this.lastNextPrevShown = this.isNextPrevShown();
      if (!needToSroll) {
        return;
      }
    }

    const activeTabWH = this.getScrollWH(activeTab);
    const navWrapNodeWH = this.getOffsetWH(navWrap);
    let { offset } = this;
    const wrapOffset = this.getOffsetLT(navWrap);
    const activeTabOffset = this.getOffsetLT(activeTab);
    if (wrapOffset > activeTabOffset) {
    // activeTab 在 wrap 的可见区域左侧
      offset += wrapOffset - activeTabOffset;
      this.setOffset(offset);
      if (excessMode === 'arrow') this.setNextPrev();
    } else if (wrapOffset + navWrapNodeWH < activeTabOffset + activeTabWH) {
    // activeTab 在 wrap 的可见区域右侧
      offset -= activeTabOffset + activeTabWH - (wrapOffset + navWrapNodeWH);
      this.setOffset(offset);
      if (excessMode === 'arrow') this.setNextPrev();
    }
  };

  /** 点击左箭头的事件处理函数 */
  prev = (e: React.MouseEvent<HTMLDivElement>) => {
    const { onPrevClick, getRef } = this.props;
    if (onPrevClick) {
      onPrevClick(e);
    }
    const navWrapNode = getRef('navWrap');
    const navWrapNodeWH = this.getOffsetWH(navWrapNode);
    const { offset } = this;
    this.setOffset(offset + navWrapNodeWH);
    this.setNextPrev();
  };

  /** 点击右箭头的事件处理函数 */
  next = (e: React.MouseEvent<HTMLDivElement>) => {
    const { onNextClick, getRef } = this.props;
    if (onNextClick) {
      onNextClick(e);
    }
    const navWrapNode = getRef('navWrap');
    const navWrapNodeWH = this.getOffsetWH(navWrapNode);
    const { offset } = this;
    this.setOffset(offset - navWrapNodeWH);
    this.setNextPrev();
  };

  /**
   * 判断左右的按钮是否显示
   */
  isNextPrevShown(state?: {next: boolean, prev: boolean}) {
    if (state) {
      return state.next || state.prev;
    }
    const { next, prev } = this.state;
    return next || prev;
  }

  /**
   * 检测 dropdown 是否需要显示，并调整 offset
   */
  setNeedsDropdown() {
    const { getRef } = this.props;
    const navNode = getRef('nav');
    const navTabsContainer = getRef('navTabsContainer');
    const navNodeWH = this.getScrollWH(navTabsContainer || navNode);
    const containerWH = this.getOffsetWH(getRef('container')) + 1;
    // 最小的偏移量（可滚动时为负值）
    const minOffset = containerWH - navNodeWH;
    const needsDropdown = (minOffset < 0);
    if (!needsDropdown) {
      this.setOffset(0);
    } else if (minOffset > this.offset) {
      // 如果此时偏移量比最小偏移量小（可能是窗口 resize 引起），则重置 offset
      this.setOffset(minOffset);
    }

    if (needsDropdown !== this.state.needsDropdown) {
      this.setState({ needsDropdown });
    }
    return needsDropdown;
  }

  render() {
    const { next, prev, needsDropdown } = this.state;
    const showNextPrev = prev || next;
    const { prev: prevCallback, next: nextCallback, prevTransitionEnd } = this;
    const {
      size,
      children,
      saveRef,
      animated,
      type,
      excessMode,
      onTabClick,
      panes,
      activeKey,
    } = this.props;

    const getPrevNext = function () {
      const prevButton = (
        <PrevButton
          onClick={prev ? prevCallback : undefined}
          unselectable="on"
          className={classnames({
            disabled: !prev,
            show: showNextPrev,
          })}
          onTransitionEnd={prevTransitionEnd}
        >
          <ArrowLeftNormal className="prev-icon" />
        </PrevButton>
      );

      const nextButton = (
        <NextButton
          onClick={next ? nextCallback : undefined}
          unselectable="on"
          className={classnames({
            disabled: !next,
            show: showNextPrev,
          })}
        >
          <ArrowRightNormal className="next-icon" />
        </NextButton>
      );

      return (
        <>
          {prevButton}
          {nextButton}
        </>
      );
    };

    const getOverlay = function () {
      const rst: React.ReactElement[] = [];
      React.Children.forEach(panes as React.ReactElement[], (child: any) => {
        if (!child) {
          return;
        }
        const key = child.key as string;
        const isActived = activeKey === key;
        const handleClick = (e?: React.MouseEvent<HTMLDivElement>) => {
          if (onTabClick) {
            onTabClick(key, e);
          }
        };

        const node = (
          <DropdownItemWrap
            aria-disabled={child.props.disabled ? 'true' : 'false'}
            aria-selected={isActived ? 'true' : 'false'}
            tabIndex={isActived ? 0 : -1}
            key={key}
            className={classnames({
              disabled: Boolean(child.props.disabled),
              actived: isActived,
            })}
            onClick={!isActived && !child.props.disabled ? handleClick : undefined}
          >
            {child.props.tab}
          </DropdownItemWrap>
        );

        rst.push(node);
      });

      return (
        <DropdownWrap>
          {rst}
        </DropdownWrap>
      );
    };


    const excessTabsHelper = function (mode: ExcessMode) {
      if (mode === 'arrow') {
        return showNextPrev ? getPrevNext() : undefined;
      } else if (mode === 'dropdown') {
        return needsDropdown ? (
          <Dropdown
            overlay={getOverlay()}
            trigger="click"
            placement="bottomRight"
          >
            <DropdownArrow>
              <ArrowDownNormal />
            </DropdownArrow>
          </Dropdown>
        ) : undefined;
      }
    };

    return (
      <NavContainer
        className={classnames(size, type, {
          scrolling: excessMode === 'arrow' ? showNextPrev : needsDropdown,
        })}
        ref={saveRef('container')}
      >
        { excessTabsHelper(excessMode!) }
        <NavWrap className={classnames(type)} ref={saveRef('navWrap')}>
          <NavScroll>
            <Nav className={classnames(type, animated && 'animated')} ref={saveRef('nav')}>
              {children}
            </Nav>
          </NavScroll>
        </NavWrap>
      </NavContainer>
    );
  }
}
