import React, { useCallback, useMemo } from 'react';
import useControlledValue from '~/hooks/useControlledValue';
import { LineheightNormal } from '../icons';
import { Select, SelectOption, SelectOptionGroup } from '../select';
import { OptionWrap, TriggerWrap } from './styled';
import { TOptionTitle } from '../select/interface';
import { useTranslate } from '~/locale/locales';

export const LINE_HEIGHT_DEFAULT_VALUES = [1, 1.15, 1.5, 2, 2.5, 3];

export interface ILineHeightSelectProps {
  /**
   * 默认的行高，当设置了value，该项忽略
   */
  defaultValue?: number;
  /**
   * 行高
   */
  value?: number;
  /**
   * 行高值列表
   */
  lineHeightValues?: number[];
  /**
   * 设置国际化语言
   */
  locale?: string;
  /**
   * select 样式
   */
  style?: React.CSSProperties;
  /**
   * 对齐方式修改时的回调函数
   */
  onChange?: (lineHeight: number) => void;
  /**
   * 层级
   */
  zIndex?: number;
  /**
   * 类名
   */
  className?: string;
  /**
   * 是否禁用
   */
  disabled?: boolean;
  /*
   * tooltip提示
   */
  title?: string | React.ReactNode;
  /**
   * 选项的tooltip提示
   */
  titles?: Record<string, TOptionTitle>;
  /**
   * 选中 “自定义行高” 项的回调函数， 不传（或传入的不是函数）则无 “自定义行高” 选项
   */
  onCustomize?: () => void;
}

const LineHeightSelect: React.FC<ILineHeightSelectProps> = (props) => {
  const { style, zIndex, lineHeightValues, className, disabled, onChange, onCustomize, title, titles, locale } = props;
  const [value, setValue] = useControlledValue(props.value, props.defaultValue);
  const t = useTranslate(locale);

  const handleChange = useCallback((e: number) => {
    if (e !== -1) {
      if (onChange) {
        onChange(e);
      }
      setValue(e);
    } else if (onCustomize) {
      onCustomize();
    }
  }, [onChange, setValue, onCustomize]);

  // 该组件的trigger区域只显示Icon
  const renderTrigger = useCallback(() => (
    <TriggerWrap>
      <LineheightNormal />
    </TriggerWrap>
  ), []);

  const renderOptions = useMemo(() => (
    (lineHeightValues || LINE_HEIGHT_DEFAULT_VALUES)
      .map((lineHeight) => (
        <SelectOption
          value={lineHeight}
          key={`${lineHeight}`}
          testid={`${lineHeight}`}
          title={titles![lineHeight]}
        >
          <OptionWrap>
            <span>{lineHeight}</span>
          </OptionWrap>
        </SelectOption>
      ))
  ), [lineHeightValues, titles]);

  return (
    <Select
      value={value}
      onChange={handleChange}
      style={style}
      renderTrigger={renderTrigger}
      zIndex={zIndex}
      className={className}
      disabled={disabled}
      testid="LineHeightSelect"
      title={title}
    >
      <SelectOptionGroup>
        {renderOptions}
      </SelectOptionGroup>
      { typeof onCustomize === 'function' && (
        <SelectOption value={-1} key="selectLineHeight">
          <OptionWrap className="pseudo-option">
            <span> {t('we_ui_line_height_select_customized_line_height')} </span>
          </OptionWrap>
        </SelectOption>
      ) as any}
    </Select>
  );
};

LineHeightSelect.defaultProps = {
  defaultValue: 1,
  titles: {},
};

export default LineHeightSelect;
