/* eslint-disable max-lines-per-function */
import React from 'react';
import {
  isEnterHotKey,
  isUpHotKey,
  isDownHotKey,
} from '~/utils';
import { TranslateComp } from '~/locale/locales';
import {
  QuickInsertItem,
  LoadingItem,
} from './quickInsertItem';
import {
  IQuickInsertProps,
  IInsertItem,
  IListGroup,
  EQuickInsertStatus,
} from './type';
import {
  HeaderWrapper,
  HeaderTitle,
  HeaderButton,
  QuickInsertWrapper,
  ListWrapper,
  GroupTitle,
  ButtonText,
  EmptyWrapper,
  ImageWrapper,
  EmptyText,
  IconWrapper,
} from './styled';

const DEFAULT_ITEM_HEIGHT = 50;
const LIST_ITEM_HEIGHT = 47;
const GROUP_TITLE_HEIGHT = 30;
const EMPTY_IMG = 'https://img.alicdn.com/tfs/TB1ko6CKG61gK0jSZFlXXXDKFXa-240-240.png';

type ItemData = IInsertItem | string;

interface UiItemData {
  // 列表项在列表中的(y轴)偏移量
  pos: number;
  // 当前列表项对在组的标题
  groupTitle: string;
  // 列表项的数据
  itemData: ItemData;
}

const getItemSize = (itemData: ItemData) => {
  let size = DEFAULT_ITEM_HEIGHT;
  if (itemData) {
    size = (typeof itemData === 'string') ? GROUP_TITLE_HEIGHT : LIST_ITEM_HEIGHT;
  }
  return size;
};

const getListData = (datas: IListGroup[]): [UiItemData[], string] => {
  const listData: UiItemData[] = [];
  let offset = 0;
  let headerTitle = '';
  datas.forEach((subGroup, index) => {
    // 第1个分组在标题中展示
    if (index === 0) {
      headerTitle = subGroup.title;
    } else {
      listData.push({
        pos: offset,
        itemData: subGroup.title,
        groupTitle: subGroup.title,
      });
      offset += getItemSize(subGroup.title);
    }
    if (subGroup.list && subGroup.list.length > 0) {
      subGroup.list.forEach(item => {
        listData.push({
          pos: offset,
          groupTitle: subGroup.title,
          itemData: item,
        });
        offset += getItemSize(item);
      });
    }
  });
  return [listData, headerTitle];
};

export const QuickInsertPanel = React.memo((props: IQuickInsertProps) => {
  const { data, status, locale } = props;
  const [listData, defaultTitle] = React.useMemo(() => getListData(data), [data]);

  // 列表引用
  const refList = React.useRef<HTMLDivElement>(null);
  // // 最近一次 mousemove event的鼠标位置
  const latestMousePosY = React.useRef<number>(0);
  // // 最近一次 scroll event的滚动位置
  const latestScrollPosY = React.useRef<number>(0);
  // // 当前是否在处理 keydown 事件
  const isKeyDownTyping = React.useRef<boolean>(false);

  const [activeIndex, setActiveIndex] = React.useState<number>(0);
  const [headerTitle, setHeaderTitle] = React.useState<string>('');
  // 是否显示标题，
  const [isShowTitle, setShowTitle] = React.useState<boolean>(true);

  React.useEffect(() => {
    document.addEventListener('keydown', handleKeyDown, true);
    return () => {
      document.removeEventListener('keydown', handleKeyDown, true);
    };
  });

  // 获取列表项(相对与列表顶部)的高度
  const getItemRelativeHeight = (index: number) => {
    if (!listData || !listData.length) {
      return 0;
    }

    const uiItemData = listData[index] as UiItemData;
    if (!uiItemData) {
      return 0;
    }

    return uiItemData.pos + getItemSize(uiItemData.itemData);
  };

  const getListContainerHeight = () => {
    let containerHeight = 0;
    if (refList.current) {
      const rect = refList.current.getBoundingClientRect();
      containerHeight = rect.height;
    }
    return containerHeight;
  };

  const findCursorIndex = (offset: number) => {
    if (!listData || offset < 0) {
      return -1;
    }

    const index = listData.findIndex((item: UiItemData, id: number, array: UiItemData[]) => {
      if (offset >= item.pos
        && ((array[id + 1] && offset < array[id + 1].pos) || !array[id + 1])) {
        return true;
      } else {
        return false;
      }
    });
    return index;
  };

  const handleListScroll = () => {
    // 快捷键产生的事件，忽略处理
    if (isKeyDownTyping.current) {
      return;
    }

    let scrollOffset = 0;
    let scrollHeight = 0;
    if (refList.current) {
      scrollOffset = refList.current.scrollTop;
      scrollHeight = refList.current.scrollHeight;
    }

    // 查找距离最近的一个item
    const resultItem = listData.find((item: UiItemData, index: number, array: UiItemData[]) => {
      if (scrollOffset >= item.pos
        && ((array[index + 1] && scrollOffset < array[index + 1].pos) || !array[index + 1])) {
        return true;
      } else {
        return false;
      }
    });

    if (resultItem) {
      const resultUiItem = resultItem as UiItemData;
      setHeaderTitle(resultUiItem.groupTitle);
      // 是否显示标题
      const isShow = typeof resultUiItem.itemData !== 'string';
      setShowTitle(isShow);
    }

    // 查找光标所在的列表项
    const offsetY = latestMousePosY.current + scrollOffset;
    const index = findCursorIndex(offsetY);
    if (index >= 0) {
      setActiveIndex(index);
    }

    // 滑动到最底部，触发“加载更多”
    const containerHeight = getListContainerHeight();
    if (scrollOffset > latestScrollPosY.current
      && scrollOffset >= (scrollHeight - containerHeight)) {
      const { loadMore } = props;
      if (typeof loadMore === 'function') {
        loadMore();
      }
    }
    latestScrollPosY.current = scrollOffset;
  };

  const handleMouseMove = (e: any) => {
    // 获取鼠标相对于列表容器的位置
    const { pageY = 0 } = e;
    let containerY = 0;
    if (refList.current) {
      const rect = refList.current.getBoundingClientRect();
      containerY = rect.y;
    }
    const mouseRelativeY = pageY - containerY;
    latestMousePosY.current = mouseRelativeY;

    // 查找光标所在的列表项
    const index = findCursorIndex(latestScrollPosY.current + mouseRelativeY);
    if (index >= 0) {
      setActiveIndex(index);
    }
  };

  const handleKeyDown = (event: any) => {
    const { useHotKey, onItemClick } = props;
    if (!useHotKey) {
      return;
    }

    isKeyDownTyping.current = true;

    if (isUpHotKey(event)) {
      event.stopPropagation();
      event.preventDefault();
      let newActiveIndex = Math.max(activeIndex - 1, 0);
      const uiItemData = listData[newActiveIndex] as UiItemData;
      if (typeof uiItemData.itemData === 'string') {
        newActiveIndex = Math.max(newActiveIndex - 1, 0);
      }
      const listHeight = getItemRelativeHeight(listData.length - 1);
      const relativeHeight = getItemRelativeHeight(newActiveIndex);
      const containerHeight = getListContainerHeight();
      if (refList.current && relativeHeight < (listHeight - containerHeight)) {
        const uiItem = listData[newActiveIndex] as UiItemData;
        const offset = uiItem ? uiItem.pos : 0;
        refList.current.scrollTo(0, offset);
        latestScrollPosY.current = offset;
      }
      setActiveIndex(newActiveIndex);
    } else if (isDownHotKey(event)) {
      event.stopPropagation();
      event.preventDefault();
      let newActiveIndex = Math.min(activeIndex + 1, listData.length - 1);
      const uiItemData = listData[newActiveIndex] as UiItemData;
      if (typeof uiItemData.itemData === 'string') {
        newActiveIndex = Math.min(newActiveIndex + 1, listData.length - 1);
      }
      const relativeHeight = getItemRelativeHeight(newActiveIndex);
      const containerHeight = getListContainerHeight();
      if (refList.current && relativeHeight > containerHeight) {
        const offset = relativeHeight - containerHeight;
        refList.current.scrollTo(0, offset);
        latestScrollPosY.current = offset;
      }
      setActiveIndex(newActiveIndex);
    } else if (isEnterHotKey(event)) {
      event.stopPropagation();
      event.preventDefault();
      const uiItem = listData && (listData[activeIndex] as UiItemData);
      if (uiItem && typeof onItemClick === 'function') {
        const itemData = uiItem.itemData as IInsertItem;
        if (itemData) {
          onItemClick(itemData);
        }
      }
    }
    setTimeout(() => {
      isKeyDownTyping.current = false;
    }, 10);
  };

  const renderHeader = () => {
    const { advanceOption } = props;
    const { icon, name, onClick: handleAdvanceSelect } = advanceOption;
    let title = '';
    if (status !== EQuickInsertStatus.loading && listData && listData.length > 0) {
      title = headerTitle || defaultTitle;
    }

    return (
      <HeaderWrapper >
        <HeaderTitle isShowTitle={isShowTitle}>{title}</HeaderTitle>
        <HeaderButton
          onClick={handleAdvanceSelect}
        >
          <IconWrapper>
            {icon}
          </IconWrapper>
          <ButtonText>{name}</ButtonText>
        </HeaderButton>
      </HeaderWrapper>
    );
  };

  const renderList = () => {
    const isEmpty = !listData || !listData.length;
    return isEmpty ? (
      <EmptyWrapper>
        <ImageWrapper src={EMPTY_IMG} />
        <EmptyText>
          <TranslateComp locale={locale} textKey={'we_ui_quick_insert_no_data'} />
        </EmptyText>
      </EmptyWrapper>
    ) : (
      <ListWrapper
        className="quick-insert-list"
        ref={refList}
        onScroll={handleListScroll}
        onMouseMove={handleMouseMove}
      >
        {listData.map((item: UiItemData, index: number) => renderRow(index, item.itemData))}
      </ListWrapper>
    );
  };

  const renderRow = (index: number, dt: ItemData) => {
    if (!dt) {
      return null;
    }

    const { onItemClick } = props;
    if (typeof dt === 'string') {
      return <GroupTitle key={`group-title-${index}`}>{dt}</GroupTitle>;
    } else {
      const insertData = dt as IInsertItem;
      return (
        <QuickInsertItem
          key={`list-item-${index}`}
          active={index === activeIndex}
          onItemClick={onItemClick}
          data={insertData}
        />
      );
    }
  };

  const isLoading = status === EQuickInsertStatus.loading;
  const isLoadingMore = status === EQuickInsertStatus.loadMore;
  return (
    <QuickInsertWrapper>
      {renderHeader()}
      {isLoading ? <LoadingItem locale={locale} /> : renderList()}
      {isLoadingMore ? <LoadingItem locale={locale} /> : null}
    </QuickInsertWrapper>
  );
});

