import React from 'react';

import {
  FillColorPicker,
} from '~/components/color-picker';

import {
  SplitButton,
} from '~/components/button';

import {
  BorderTopNormal,
  BorderNoneNormal,
  BorderAllNormal,
  BorderBottomNormal,
  BorderHorizontalNormal,
  BorderInnerNormal,
  BorderLeftNormal,
  BorderOuterNormal,
  BorderRightNormal,
  BorderVerticalNormal,
} from '~/components/icons';

import { NumberInput } from '~/components/number';

import {
  CommonLineSelect,
  ILineTypeValue,
} from '~/components/lines';

import { BorderTypePanel, BorderType } from './BorderTypePanel';

import { TranslateComp } from '~/locale/locales';

import {
  BorderSetWrapper,
  LineTypeWrapper,
  ContentWrapper,
  FooterWrapper,
  IconWrapper,
  TextSpan,
  ThicknessInputWrapper,
  FillColorWrapper,
  LineSelectWrapper,
  LabelWrapper,
  FooterInner,
} from './styled';
import { classnames, isWhite } from '~/utils';

export interface IBorderProps {
  borderColor?: string;
  borderType?: BorderType;
  borderStyle?: string;
}

export interface IBorderSetProps {
  /**
   * 选中的边框类型
   */
  borderType?: BorderType;
  /**
   * 边框图标的大小
   */
  borderIconSize?: number;
  /**
   * 线条类型的数组
   */
  borderStyles?: Record<string, ILineTypeValue>;
  /**
   * 默认的边框样式
   */
  defaultBorderStyle?: string;
  /**
   * 当前选中的颜色值
   */
  color?: string;
  /**
   * 边框属性发生变化时的回调
   * changeProps: 发生变化的属性值
   * allProps: BorderSet相关的所有属性集
   */
  onChange?: (allProps: Required<IBorderProps>, changeProps: IBorderProps) => void;
  /**
   * 点击左侧按钮的回调
   */
  onClick?: (allProps: Required<IBorderProps>) => void;
  /**
   * 是否有设置边框大小(粗细)的功能
   */
  supportConfigSize?: boolean;
  /**
   * 本地语言类型，如zh-CN
   */
  locale?: string;
  /**
   * SplitButton 的样式
   */
  style?: React.CSSProperties;
  /**
   * 是否禁用
   */
  disabled?: boolean;
  /**
   * 类名
   */
  className?: string;
  /**
   * tooltip提示
   */
  title?: string | React.ReactNode;
}

interface IBorderSetState {
  // 内部维护的颜色
  innerColor: string;
  // 内部维护的线条类型
  innerLineType: string;
  // 内部维护的边框类型
  innerBorderType: BorderType;
  // 是否显示下拉框
  dropdownVisible: boolean;
  // 是否显示颜色选择器
  colorPickerVisible: boolean;
  // 是否显示线形选择
  lineSelectVisible: boolean;
}

const DEFAULT_BORDER_COLOR = '#000000';
const ICON_DEFAULT_COLOR = 'rgba(17, 31, 44, 0.72)';


export class BorderSet extends React.Component<IBorderSetProps, IBorderSetState> {
  static defaultProps = {
    supportConfigSize: true,
  };

  constructor(props: IBorderSetProps) {
    super(props);
    const innerColor = props.color ? props.color : DEFAULT_BORDER_COLOR;
    const innerBorderType = props.borderType || BorderType.all;
    const innerLineType = props.defaultBorderStyle || 'single';
    this.state = {
      innerColor,
      innerBorderType,
      innerLineType,
      dropdownVisible: false,
      colorPickerVisible: false,
      lineSelectVisible: false,
    };
  }

  handleClick = (e: React.MouseEvent) => {
    // 阻止冒泡
    e.stopPropagation();
  };

  handleVisibleChange = (visible: boolean) => {
    this.setState({ dropdownVisible: visible });
  };

  handleClickLeft = () => {
    const { onClick } = this.props;
    if (onClick) {
      const { innerColor, innerLineType, innerBorderType } = this.state;
      const data = {
        borderColor: innerColor,
        borderType: innerBorderType,
        borderStyle: innerLineType,
      };
      onClick(data);
    }
  };

  handlePropertyChange = (borderProps: IBorderProps) => {
    const { onChange } = this.props;
    if (onChange) {
      const { innerColor, innerLineType, innerBorderType } = this.state;
      const data = {
        borderColor: innerColor,
        borderType: innerBorderType,
        borderStyle: innerLineType,
      };
      const allProps = {
        ...data,
        ...borderProps,
      };
      onChange(allProps, borderProps);
    }
  };

  handleColorChange = (color: string) => {
    this.setState({ innerColor: color });
    this.handlePropertyChange({ borderColor: color });
    this.setState({ dropdownVisible: true });
  };

  handleLineChange = (type: string) => {
    this.setState({ innerLineType: type });
    this.handlePropertyChange({ borderStyle: type });
    this.setState({ dropdownVisible: true });
  };

  handleBorderChange = (type: BorderType) => {
    this.setState({ innerBorderType: type });
    this.handlePropertyChange({ borderType: type });
  };

  handleColorPickerVisible = (visible: boolean) => {
    this.setState({ colorPickerVisible: visible });
  };

  handleLineSelectVisible = (e: boolean) => {
    this.setState({ lineSelectVisible: e });
  };

  handleBorderNoneClick = () => this.handleBorderChange(BorderType.none);

  // eslint-disable-next-line max-lines-per-function
  getOverlayContent = () => {
    const {
      borderIconSize,
      borderStyles: lineTypes,
      color,
      supportConfigSize,
      locale,
    } = this.props;
    const {
      innerColor: currentColor,
      innerBorderType,
      innerLineType,
      colorPickerVisible,
      lineSelectVisible,
    } = this.state;

    const activeColor = color || currentColor;

    const inputStyle: React.CSSProperties = {
      width: 63,
      height: 22,
    };
    const iconStyle: React.CSSProperties = {
      width: borderIconSize,
      height: borderIconSize,
      boxSizing: 'border-box',
      padding: '1px 1px',
    };

    return (
      <BorderSetWrapper onMouseDown={this.handleClick}>
        <ContentWrapper>
          <BorderTypePanel
            borderType={innerBorderType}
            borderIconSize={borderIconSize}
            onBorderTypeChange={this.handleBorderChange}
            borderSelectedColor={activeColor}
          />
          <LineTypeWrapper>
            <FillColorWrapper>
              <LabelWrapper>
                <TranslateComp locale={locale} textKey={'we_ui_border_set_color'} />
              </LabelWrapper>
              <FillColorPicker
                active={colorPickerVisible}
                onActiveChange={this.handleColorPickerVisible}
                color={activeColor}
                automaticColor={DEFAULT_BORDER_COLOR}
                onColorPicker={this.handleColorChange}
                size="small"
                locale={locale}
              />
            </FillColorWrapper>

            <LineSelectWrapper>
              <LabelWrapper>
                <TranslateComp locale={locale} textKey={'we_ui_border_set_line_type'} />
              </LabelWrapper>
              <CommonLineSelect
                lineTypeArray={lineTypes}
                length={80}
                style={inputStyle}
                lineType={innerLineType}
                // 线型颜色不需要跟随变化
                color="#000000"
                onLineTypeChange={this.handleLineChange}
                visible={lineSelectVisible}
                onVisibleChange={this.handleLineSelectVisible}
              />
            </LineSelectWrapper>

            {!supportConfigSize ? null : (
              <ThicknessInputWrapper>
                <LabelWrapper>
                  <TranslateComp locale={locale} textKey={'we_ui_border_set_line_thickness'} />
                </LabelWrapper>
                <NumberInput
                  defaultValue={1}
                  min={1}
                  max={6}
                  step={1}
                  style={inputStyle}
                />
              </ThicknessInputWrapper>
            )}
          </LineTypeWrapper>
        </ContentWrapper>
        <FooterWrapper>
          <FooterInner
            className={classnames({ active: innerBorderType === BorderType.none })}
            onClick={this.handleBorderNoneClick}
          >
            <IconWrapper
              style={{ color: ICON_DEFAULT_COLOR, backgroundColor: 'transparent' }}
            >
              <BorderNoneNormal style={iconStyle} />
            </IconWrapper>
            <TextSpan>
              <TranslateComp locale={locale} textKey={'we_ui_border_set_border_none'} />
            </TextSpan>
          </FooterInner>
        </FooterWrapper>
      </BorderSetWrapper>
    );
  };

  renderShowIcon = () => {
    const { innerBorderType } = this.state;

    switch (innerBorderType) {
      case 'outer': return <BorderOuterNormal />;
      case 'inner': return <BorderInnerNormal />;
      case 'all': return <BorderAllNormal />;
      case 'left': return <BorderLeftNormal />;
      case 'right': return <BorderRightNormal />;
      case 'top': return <BorderTopNormal />;
      case 'bottom': return <BorderBottomNormal />;
      case 'none': return <BorderNoneNormal />;
      case 'horizon': return <BorderHorizontalNormal />;
      case 'vertical': return <BorderVerticalNormal />;
      default: return <BorderNoneNormal />;
    }
  };

  render() {
    const { dropdownVisible, colorPickerVisible, lineSelectVisible, innerColor: color } = this.state;
    const { className, style, disabled, title } = this.props;
    const overlayVisible = dropdownVisible || colorPickerVisible || lineSelectVisible;

    const pathColors = [
      // 当选中颜色为白色时，选中边框的外边缘为黑色，否则选中边框外边缘与内部颜色一致
      (!color || isWhite(color))
        ? ICON_DEFAULT_COLOR
        : color,
      color || ICON_DEFAULT_COLOR,
      ICON_DEFAULT_COLOR,
    ];

    return (
      <SplitButton
        overlay={this.getOverlayContent()}
        active={overlayVisible}
        onClickLeft={this.handleClickLeft}
        onActiveChange={this.handleVisibleChange}
        className={className}
        disabled={disabled}
        style={style}
        testid="BorderSet"
        title={title}
      >
        {React.cloneElement(this.renderShowIcon(), { pathColors })}
      </SplitButton>
    );
  }
}
