import React, { Component } from 'react';
import ResizeObserver from 'resize-observer-polyfill';
import warning from 'warning';
import { Dropdown } from '~/components/dropdown';
import { Button } from '~/components/button';
import { MoreNormal } from '~/components/icons';
import { debounce } from '~/utils';
import { childrenType, ToolBoxArray } from './ToolBox';
import { ToolLine, ToolLineArray, HiddenToolLine } from './ToolLine';
import { ToolsShow, ToolsMore, Content, ToolbarWrapper } from './styled';

export interface ISingleLineToolbarProps {
  /**
   * 是否启用自动折叠功能
   */
  autoFolding?: boolean;
  /**
   * 包裹的组件
   */
  children?: ToolLineArray;
  /**
   * toolbar wrapper 的行内样式
   */
  style?: React.CSSProperties;
  /**
   * 层级
   */
  zIndex?: number;
  /**
   * 对齐方式
   */
  alignType?: 'left' | 'center' | 'right';
  /**
   * 类名
   */
  className?: string;
  /**
   * 更多面板点击后自动关闭
   */
  autoCloseMorePanel?: boolean;
}

interface LineWidth {
  totalWidth: number;
  buttonWidths: number[];
}

interface IToolbarState {
  // 从第hiddenPivot个slot开始隐藏
  hiddenPivot: number;
  hiddenBtnPivot: number;
  groups: ToolLineArray;
  hiddenGroups: ToolLineArray;
  prevChildren: ToolLineArray | null;
  // 是否需要重新技术算布局
  needCalculating: boolean;
  morePanelVisible: boolean;
}

const INIT_PIVOT = -1;

/**
 * 获取初始的自组件(包含首次渲染和children变化的情况), 重新布局
 * @param children
 */
const getChildren = (children: childrenType) => {
  const groups: ToolLineArray = [];
  const hiddenGroups: ToolLineArray = [];
  let hasIllegalChild = false;

  const handleChild = (child: React.ReactElement) => {
    if (!child) return;
    if (child.type === ToolLine) {
      groups.push(child);
    } else if (child.type === HiddenToolLine) {
      hiddenGroups.push(child);
    } else if (child.type === React.Fragment) {
      React.Children.forEach(child.props.children, handleChild);
    } else {
      hasIllegalChild = true;
    }
  };
  React.Children.forEach(children as React.ReactElement, handleChild);

  warning(!hasIllegalChild, 'Children of SingleLineToolbar must be ToolLine or HiddenToolLine');

  if (!((groups.length || hiddenGroups.length) > 0)) {
    throw new Error('SingleLineToolbar should has more than one child which type is ToolLine or HiddenToolLine');
  }
  return { groups, hiddenGroups };
};

const toolsMoreOverlayStyle: React.CSSProperties = {
  padding: '0 10px',
};

export class SingleLineToolbar extends Component<ISingleLineToolbarProps, IToolbarState> {
  // eslint-disable-next-line react/static-property-placement
  static defaultProps = {
    autoFolding: true,
    alignType: 'center',
  };

  resizeObserver: ResizeObserver;

  // Slot的宽度缓存
  slotsWidthList: LineWidth[] = [];

  refToolbar = React.createRef<HTMLElement>();

  refToolContent = React.createRef<HTMLElement>();

  refShow = React.createRef<HTMLDivElement>();

  refMore = React.createRef<HTMLElement>();

  recomputeLayout = debounce(() => {
    this.calculateWidth(false);
  }, 50);

  constructor(props: ISingleLineToolbarProps) {
    super(props);
    this.state = {
      hiddenPivot: INIT_PIVOT,
      hiddenBtnPivot: INIT_PIVOT,
      groups: [],
      hiddenGroups: [],
      // eslint-disable-next-line react/no-unused-state
      prevChildren: null, // 用于状态比对
      needCalculating: false,
      morePanelVisible: false,
    };
    this.resizeObserver = new ResizeObserver(this.handleResize);
  }

  static isNeedCalculating = (params: {
    prevGroups: ToolLineArray;
    prevHiddenGroups: ToolLineArray;
    groups: ToolLineArray;
    hiddenGroups: ToolLineArray;
  }) => {
    const {
      prevGroups,
      prevHiddenGroups,
      groups,
      hiddenGroups,
    } = params;
    if (prevGroups.length !== groups.length
      || prevHiddenGroups.length !== hiddenGroups.length) {
      return true;
    }
    for (let i = 0; i < groups.length; i++) {
      const { children: prevChildren = [] } = prevGroups[i].props;
      const { children: curChildren = [] } = groups[i].props;
      if (prevChildren.length !== curChildren.length) {
        return true;
      }
    }
    for (let i = 0; i < hiddenGroups.length; i++) {
      const { children: prevChildren = [] } = prevHiddenGroups[i].props;
      const { children: curChildren = [] } = hiddenGroups[i].props;
      if (prevChildren.length !== curChildren.length) {
        return true;
      }
    }

    return false;
  };

  static getDerivedStateFromProps = (props: ISingleLineToolbarProps, state: IToolbarState) => {
    const { prevChildren } = state;

    const { children, autoFolding } = props;
    const { groups, hiddenGroups } = getChildren(children!);

    if (!autoFolding) {
      return {
        groups,
        hiddenGroups,
        prevChildren: children,
        hiddenPivot: INIT_PIVOT,
        hiddenBtnPivot: INIT_PIVOT,
        needCalculating: false,
      };
    }
    /**
     * 分两种情况
     * 1: 布局变换, 则重新计算布局
     * 2: 布局没变化, 但是children变换(children的props变化), 需要重新渲染
     */
    if (children !== prevChildren) {
      // TODO 如果对应自组件长度变化, 则认为布局变化
      const needCalculating = SingleLineToolbar.isNeedCalculating({
        prevGroups: state.groups,
        prevHiddenGroups: state.hiddenGroups,
        groups,
        hiddenGroups,
      });

      if (needCalculating) {
        return {
          groups,
          hiddenGroups,
          prevChildren: children,
          hiddenPivot: INIT_PIVOT,
          hiddenBtnPivot: INIT_PIVOT,
          needCalculating: true,
        };
      }
      // 布局未变化, 则沿用上次计算结果
      return {
        groups,
        hiddenGroups,
        prevChildren: children,
        needCalculating: false,
      };
    }

    return null;
  };

  componentDidMount() {
    const { autoFolding } = this.props;
    if (!autoFolding) return;
    setTimeout(() => this.calculateWidth(true), 0);
    if (this.refToolContent.current && this.resizeObserver) {
      this.resizeObserver.observe(this.refToolContent.current);
    }
    if (this.refShow.current) {
      this.refShow.current.addEventListener('mousedown', this.mouseDownAtShow);
    }
  }

  componentDidUpdate(prevProps: ISingleLineToolbarProps) {
    const { needCalculating } = this.state;
    if (prevProps.children !== this.props.children && needCalculating) {
      this.calculateWidth(true);
    }
  }

  componentWillUnmount() {
    const { autoFolding } = this.props;
    if (!autoFolding) return;

    if (this.resizeObserver) {
      this.resizeObserver.disconnect();
    }

    if (this.refShow.current) {
      this.refShow.current.removeEventListener('mousedown', this.mouseDownAtShow);
    }
  }

  mouseDownAtShow = (e: MouseEvent) => {
    const whiteTags = ['input', 'textarea'];
    const tagName = ((e.target as HTMLElement).tagName || '').toLowerCase();
    if (!whiteTags.includes(tagName)) {
      e.preventDefault();
    }
  };

  // 获取一个ToolLine 各个子元素的宽度
  getLineWidth = (toolLine: HTMLElement): LineWidth => {
    if (!toolLine) {
      return { totalWidth: 0, buttonWidths: [] };
    }

    const totalWidth = toolLine.offsetWidth;
    const buttonWidths = [];
    const buttons = toolLine.children;
    for (let btnIdx = 0; btnIdx < buttons.length; btnIdx++) {
      const button = buttons[btnIdx] as HTMLElement;
      if (button) {
        buttonWidths.push(button.offsetWidth);
      }
    }
    return { totalWidth, buttonWidths };
  };

  /**
   * 重新计算宽度来布局
   */
  calculateWidth = (isFirst = false) => {
    if (!this.refShow.current || !this.refShow.current.children) return;
    const { children } = this.refShow.current;
    const parentElement = this.refShow.current.parentElement as HTMLElement;
    const { offsetWidth: containerWidth } = parentElement;

    let pivot = INIT_PIVOT;
    let buttonPivot = INIT_PIVOT;
    // 更多(...)的宽度
    let sum = 32;
    if (this.refMore.current) {
      sum = this.refMore.current.offsetWidth;
    }

    // 非第一次计算(如resize)，从缓存中取已计算好的数据
    if (!isFirst && this.slotsWidthList && this.slotsWidthList.length > 0) {
      for (let index = 0; index < this.slotsWidthList.length; index += 1) {
        const lineWidth = this.slotsWidthList[index];
        if ((sum + lineWidth.totalWidth) > containerWidth) {
          pivot = index;
          const widths = lineWidth.buttonWidths;
          const leftPadding = lineWidth.totalWidth - widths.reduce((a, b) => a + b, 0)
          sum += leftPadding
          for (let btnIdx = 0; btnIdx < widths.length; btnIdx++) {
            const btnWidth = widths[btnIdx];
            sum += btnWidth;
            if (sum > containerWidth && buttonPivot <= INIT_PIVOT) {
              buttonPivot = btnIdx;
            }
          }
          break;
        } else {
          sum += lineWidth.totalWidth;
        }
      }

      this.setState({
        hiddenPivot: pivot,
        hiddenBtnPivot: buttonPivot,
      });
      return;
    }

    // 第一次渲染或children发生变化，重新计算宽度
    for (let index = 0; index < children.length; index += 1) {
      const child = children[index] as HTMLElement;
      // 计算的结构存入缓存
      const lineWidth = this.getLineWidth(child);
      this.slotsWidthList.push(lineWidth);
      if ((sum + lineWidth.totalWidth) > containerWidth && pivot <= INIT_PIVOT) {
        pivot = index;
        const widths = lineWidth.buttonWidths;
        for (let btnIdx = 0; btnIdx < widths.length; btnIdx++) {
          const btnWidth = widths[btnIdx];
          sum += btnWidth;
          if (sum > containerWidth && buttonPivot <= INIT_PIVOT) {
            buttonPivot = btnIdx;
          }
        }
      } else {
        sum += lineWidth.totalWidth;
      }
    }

    this.setState({
      hiddenPivot: pivot,
      hiddenBtnPivot: buttonPivot,
    });
  };

  getResizeContainer = (): HTMLElement => (
    this.refToolbar.current!
  );

  /**
 * resize回调事件
 */
  handleResize = (): void => {
    this.recomputeLayout();
  };

  handleClickMorePanel = (e: React.MouseEvent) => {
    const { autoCloseMorePanel } = this.props;
    // 阻止冒泡
    e.stopPropagation();
    if (autoCloseMorePanel) {
      this.setState({ morePanelVisible: false });
    }
  };

  handleMorePanelVisibleChange = (e: boolean) => {
    this.setState({ morePanelVisible: e });
  };

  renderMore(splitHiddenBoxs: ToolBoxArray, hiddenSlots: React.ReactNodeArray): React.ReactNode {
    const { zIndex } = this.props;
    const { morePanelVisible } = this.state;

    if (splitHiddenBoxs.length === 0 && hiddenSlots.length === 0) {
      return null;
    }

    const DropContent = (
      <ToolsShow onClick={this.handleClickMorePanel}>
        {splitHiddenBoxs.length ? <ToolLine>{splitHiddenBoxs}</ToolLine> : null}
        {hiddenSlots}
      </ToolsShow>
    );

    return (
      <ToolsMore ref={this.refMore} isSingleLine>
        <Dropdown
          trigger="click"
          overlay={DropContent}
          placement="bottomRight"
          zIndex={zIndex}
          overlayStyle={toolsMoreOverlayStyle}
          visible={morePanelVisible}
          onVisibleChange={this.handleMorePanelVisibleChange}
        >
          <Button
            bordered={false}
            shape="rect"
            icon={<MoreNormal />}
          />
        </Dropdown>
      </ToolsMore>
    );
  }

  render() {
    const { style, autoFolding, alignType, className } = this.props;
    const { hiddenPivot, hiddenBtnPivot, hiddenGroups, groups } = this.state;
    let computedShowSlots = groups;
    let computedHiddenSlots = hiddenGroups;
    let splitHiddenBox: ToolBoxArray = [];
    let splitShowBox: ToolBoxArray = [];
    // 如果为初始值, 说明没有计算, 直接略过
    if (hiddenPivot > INIT_PIVOT) {
      // 分割点的ToolLine
      const toolLine = groups[hiddenPivot];
      const buttons = toolLine && toolLine.props && toolLine.props.children;
      if (buttons && buttons.length) {
        splitShowBox = buttons.slice(0, hiddenBtnPivot);
        splitHiddenBox = buttons.slice(hiddenBtnPivot);
      }
      computedShowSlots = groups.slice(0, hiddenPivot);
      computedHiddenSlots = computedHiddenSlots.concat(groups.slice(hiddenPivot + 1));
    }

    const styles: React.CSSProperties = {
      width: autoFolding ? '0' : '100%',
    };

    return (
      <ToolbarWrapper
        style={{ ...style }}
        ref={this.refToolbar}
        className={className}
      >
        <Content ref={this.refToolContent} style={styles} className={alignType}>
          <ToolsShow ref={this.refShow}>
            {computedShowSlots}
            {splitShowBox.length ? <ToolLine>{splitShowBox}</ToolLine> : null}
          </ToolsShow>
          {this.renderMore(splitHiddenBox, computedHiddenSlots)}
        </Content>
      </ToolbarWrapper>
    );
  }
}
