import styled, { css } from 'styled-components';
import { Dropdown } from '../dropdown';
import { withTheme } from '../theme';

const hoverActive = css`
  &:hover {
    background-color: ${({ theme }) => theme.ui.backgroundColor};
    cursor: pointer;
  }
`;

const handleItemCommon = css<any>`
  position: relative;
  min-width: 150px;
  height: 36px;
  font-size: 13px;
  line-height: 20px;
  padding: 0 ${({ theme }) => theme.ui.paddingMedium};
  margin: 0 ${({ theme }) => theme.ui.marginMedium};
  color: ${({ theme }) => theme.ui.commonBaseColor1};
  border-radius: ${({ theme }) => theme.ui.radiusLarge};

  ${({ isHotkeyType, hoverable, theme }) => hoverable !== false && `
    &:hover {
      background-color: ${isHotkeyType ? 'inherit' : theme.ui.commonBgHoverColor};
      cursor: pointer;
    }
  `}

  .icon {
    position: absolute;
    top: 50%;
    right: 23px;
    transform: translate(120%, -50%);
  }

  &.disabled {
    color: ${({ theme }) => theme.ui.disabledColor};
    &:hover {
      background-color: transparent;
      cursor: not-allowed;
    }
  }
`;

export const MenuRoot = withTheme(styled.div`
  box-sizing: border-box;
  font-size: 13px;
  color: ${({ theme }) => theme.ui.commonActionIconColor};

  &.vertical {
    min-width: 150px;
    padding: 8px 0;
  }

  &.horizontal {
    display: flex;
  }
`);

export const Divider = withTheme(styled.div`
  height: 1px;
  margin: ${({ theme }) => theme.ui.marginMedium} 8px;
  background-color: ${({ theme }) => theme.ui.commonLineLightColor};
`);

export const SubMenuWrap = withTheme(styled.div.attrs(({ path, menuId }: IIndentProps) => ({
  'data-path': path,
  'data-menuId': menuId,
}))`
`);

export const SubMenuInnerWrap = withTheme(styled.div`
  box-sizing: border-box;
  ${hoverActive}

  &.horizontal {
    height: 28px;
    vertical-align: middle;
    display: inline-block;
    padding: ${({ theme }) => theme.ui.paddingTiny};
    margin: 0 ${({ theme }) => theme.ui.marginTiny};
    border-radius: ${({ theme }) => theme.ui.radiusMedium};
  }

  &.hotkeyHover{
    background-color: ${({ theme }) => theme.ui.commonBgHoverColor} !important;
  }

  &.vertical {
    ${handleItemCommon}
  }

  &.opened {
    background-color: ${({ theme }) => theme.ui.backgroundColor};
  }

  .arrow-icon {
    color: ${({ theme }) => theme.ui.commonBaseColor3};
    &.disabled{
      color: ${({ theme }) => theme.ui.disabledColor};
    }
  }
`);

export const TriggerWrap = withTheme(styled.div`
  display: flex;
  align-items: center;
  height: 100%;
  &.hasArrow {
    padding-right: 24px;
  }
`);

export const TriggerIconWrap = withTheme(styled.div`
  display: flex;
  align-items: center;
  height: 100%;
  margin-right: 8px;
  color: ${({ theme }) => theme.ui.commonActionIconColor};
`);

export const MenuItemInner = withTheme(styled.div`
  display: flex;
  align-items: center;
  &.selected {
    padding-right: 35px;
  }
`);

export const MenuItemIconWrap = withTheme(styled.div`
  display: flex;
  align-items: center;
  height: 100%;
  margin-right: 8px;
  color: ${({ theme }) => theme.ui.commonActionIconColor};
`);

export const MenuItemContentWrap = withTheme(styled.div`

`);

export const TriggerTitleWrap = withTheme(styled.div`
`);

interface IIndentProps {
  textIndent: number;
  path: string;
  menuId: string;
}

export const MenuItemWrap = withTheme(styled.div.attrs(({ path, menuId }: IIndentProps) => ({
  'data-path': path,
  'data-menuId': menuId,
})) <IIndentProps>`
  .selected-icon {
    display: none;
    color: ${({ theme }) => theme.ui.primaryColor};
    font-size: 12px;
  }
  &.hotkeyHover{
    background-color: ${({ theme }) => theme.ui.commonBgHoverColor} !important;
  }  

  &.vertical {
    ${handleItemCommon};
    display: flex;
    align-items: center;

    &.selected {
      .selected-icon {
        display: inline-block;
      }
    }
  }

  &.horizontal {
    ${hoverActive}
    display: inline-block;
    padding: ${({ theme }) => theme.ui.paddingTiny};
    margin: 0 ${({ theme }) => theme.ui.marginTiny};
    border-radius: ${({ theme }) => theme.ui.radiusMedium};
  }

`);

export const MenuItemGroupWrap = styled.div``;

export const MenuItemGroupTitle = withTheme(styled.div`
  font-size: ${({ theme }) => theme.ui.fontSizeSmall};
  color: ${({ theme }) => theme.ui.commonBaseColor3};
  // 行高为 24px / 12px
  line-height: 2;
  padding-left: ${({ theme }) => theme.ui.paddingMedium};
`);

