import React from 'react';
import styled, { keyframes } from 'styled-components';
import { Tooltip } from '../tooltip';

const loadingCircle = keyframes`
100% {
  transform: rotate(360deg);
}
`;

const Weicon = styled.span`
    display: inline-block;
    color: inherit;
    font-style: normal;
    line-height: 0;
    text-align: center;
    text-transform: none;
    text-rendering: optimizeLegibility;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    outline: none;
    font-size: 16px;
    /* 默认的 icon 字体大小为 16px */

    > * {
        line-height: 1;
    }

    > svg {
        display: inline-block;
    }

    ::before {
        display: none;
    }

    > .weicon-icon {
        display: block;
    }

    > .weicon[tabindex] {
        cursor: pointer;
    }

    > .weicon-spin::before,
    > .weicon-spin {
        display: inline-block;
        animation: ${loadingCircle} 1s infinite linear;
    }
`;

// Icon 暴露给应用层的接口
export interface IIconBaseProps {
  className?: string;
  onClick?: React.MouseEventHandler<SVGElement>;
  onMouseDown?: React.MouseEventHandler<SVGElement>;
  spin?: boolean;
  style?: React.CSSProperties;
  rotate?: number;
  tabIndex?: number;
  title?: string | React.ReactNode;
  testid?: string;
}

export interface IIconProps extends IIconBaseProps {
  name: string;
  path: string[] | string;
  /**
   * 设置多路径 svg 的路径填充颜色，传入的字符串与 path 各项对应
   * 如果传入数组长度小于 path，则剩余的 path 颜色设置为 'inherited'
   * 如果传入数组长度多于 path，多余的参数将被忽略
   */
  pathColors?: string[];
  viewBox: string;
}

export const svgDefaultProps = {
  width: '1em',
  height: '1em',
  fill: 'currentColor',
  'aria-hidden': true,
  focusable: false,
};

export const IconBase: React.FC<IIconProps> = (icon) => {
  const {
    name,
    path,
    pathColors,
    viewBox,
    className,
    onClick,
    onMouseDown,
    spin,
    style,
    rotate,
    tabIndex,
    title,
    testid,
    ...restProps
  } = icon;

  const classString = `weicon weicon-${name} ${className || ''}`;

  let svgClassString = '';
  if (!!spin || name === 'loading') {
    svgClassString = 'weicon-spin';
  }

  const rotateStyle = rotate
    ? {
      msTransform: `rotate(${rotate}deg)`,
      transform: `rotate(${rotate}deg)`,
    }
    : {};

  const svgStyle = Object.assign(rotateStyle, style || {});

  let iconTabIndex = tabIndex;
  if (iconTabIndex === undefined && onClick) {
    iconTabIndex = -1;
  }

  let pathes = null;
  if (typeof path === 'string') {
    pathes = <path d={path} />;
  } else if (path instanceof Array) {
    // eslint-disable-next-line react/no-array-index-key
    pathes = path.map((p, index) => (<path
      d={p}
      key={index}
      style={{ fill: (pathColors && pathColors[index]) ? pathColors[index] : 'inherit' }}
    />));
  }

  const iconNode = (
    <Weicon
      role="img"
      aria-label={name}
      data-testid={testid ? `Icon_${testid}` : undefined}
      {...restProps}
      tabIndex={iconTabIndex}
      className={classString}
    >
      <svg
        {...svgDefaultProps}
        viewBox={viewBox}
        className={svgClassString}
        style={svgStyle}
        onClick={onClick}
        onMouseDown={onMouseDown}
      >
        {pathes}
      </svg>
    </Weicon>
  );

  return title ? (
    <Tooltip title={title}>
      {iconNode}
    </Tooltip>
  ) : iconNode;
};
