import React from 'react';
import {
  Select,
  // SelectOption as Option,
  // ISelectProps,
} from '~/components/select';
import { CommonLine, commonLineMap, TLineStyle } from './commonLine';
import { TextSpan, OptionStyled, TriggerStyled } from './styled';
import { classnames } from '~/utils';

export interface ILineTypeValue {
  lineType: TLineStyle; // 线条样式
  size?: number; // 线条粗度
}

export interface ICommonLineSelectProps {
  /**
   * 线条类型的数组
   */
  lineTypeArray?: Record<string, ILineTypeValue>;
  /**
   * 边框的颜色
   */
  color: string;
  /**
   * 线条的长度，单位：px
   */
  length: number;
  /**
   * 默认的线条样式，非受控模式
   */
  defaultLineType?: string;
  /**
   * 线条样式,受控模式
   */
  lineType?: string;
  /**
   * 显示区样式
   */
  style?: React.CSSProperties;
  /**
   * 弹层样式
   */
  overlayStyle?: React.CSSProperties;
  /**
   * 设置 option 容器样式
   */
  optionWrapperStyle?: React.CSSProperties;
  /**
   * 线条类型发生变化时的回调函数
   */
  onLineTypeChange: (type: string) => void;
  /**
   * 层级
   */
  zIndex?: number;
  /**
   * 选项除了线条外还可附加说明文字（如 '2pt' ）
   */
  optionText?: (lineType: ILineTypeValue) => string;
  /**
   * 自定义的 trigger
   */
  customTrigger?: () => JSX.Element;
  /**
   * 是否有边框
   */
  bordered?: boolean;
  /**
   * tooltip提示
   */
  title?: string | React.ReactNode;
  /**
   * 弹层的可见性
   */
  visible?: boolean;
  /**
   * 默认弹层的可见性
   */
  defaultVisible?: boolean;
  /**
   * 弹层可见性变化
   */
  onVisibleChange?: (e: boolean) => void;
  /**
   * 是否可以选中
   */
  selectable?: boolean;
  /**
   * 类名
   */
  className?: string;
  /**
   * 是否禁用
   */
  disabled?: boolean;
}

interface IState {
  innerLineType: string;
  innerVisible: boolean;
}

const getDefaultLineTypeArray = (): Record<string, ILineTypeValue> => {
  const typeArray: Record<string, ILineTypeValue> = {};
  Object.keys(commonLineMap).forEach((key) => {
    const lineProps = commonLineMap[key];
    const { defaultSize: size } = lineProps;
    typeArray[key] = { size, lineType: key as TLineStyle };
  });
  return typeArray;
};

export class CommonLineSelect extends React.PureComponent<ICommonLineSelectProps, IState> {
  static lineTypes = getDefaultLineTypeArray();

  static defaultProps = {
    bordered: true,
    defaultVisible: false,
    disabled: false,
  };

  constructor(props: ICommonLineSelectProps) {
    super(props);
    const innerLineType = props.lineType || props.defaultLineType || 'single';
    this.state = {
      innerLineType,
      innerVisible: (props.visible === undefined) ? props.defaultVisible! : props.visible,
    };
  }

  static getDerivedStateFromProps(nextProps: ICommonLineSelectProps, state: IState) {
    const nextVisible = nextProps.visible;
    if (nextVisible !== undefined && nextVisible !== state.innerVisible) {
      return {
        innerVisible: nextVisible,
      };
    }
    return null;
  }

  handleLineTypeChange = (type: string) => {
    this.setState({ innerLineType: type });
    const { onLineTypeChange } = this.props;
    if (onLineTypeChange) {
      onLineTypeChange(type);
    }
  };


  render() {
    const {
      lineType: selectedLineType,
      lineTypeArray,
      color,
      overlayStyle,
      optionWrapperStyle,
      zIndex,
      style,
      length,
      optionText,
      customTrigger,
      bordered,
      title,
      onVisibleChange,
      selectable = true,
      className,
      disabled,
    } = this.props;
    const lineTypeMap = lineTypeArray || CommonLineSelect.lineTypes;
    const { innerLineType, innerVisible } = this.state;
    const activeLineType = selectedLineType || innerLineType;
    const withText = !!optionText && typeof optionText === 'function';

    const activeLineProps = lineTypeMap[activeLineType];

    const getTrigger = (typeof customTrigger === 'function')
      ? customTrigger
      : () => (
        <TriggerStyled>
          {withText && (<TextSpan>{activeLineProps ? optionText!(activeLineProps) : ''}</TextSpan>)}
          <CommonLine
            lineType={activeLineProps?.lineType}
            size={activeLineProps?.size}
            color={color}
            length={length}
          />
        </TriggerStyled>);

    const handleVisibleChange = (e: boolean) => {
      const { visible } = this.props;
      if (onVisibleChange) onVisibleChange(e);
      if (visible === undefined) {
        this.setState({ innerVisible: e });
      }
    };


    return (
      <Select
        value={activeLineType}
        style={style}
        overlayStyle={overlayStyle}
        optionWrapperStyle={optionWrapperStyle}
        // maxDropdownHeight={400}
        onChange={this.handleLineTypeChange}
        zIndex={zIndex}
        testid="CommonLineSelect"
        renderTrigger={getTrigger}
        bordered={bordered}
        title={title}
        visible={innerVisible}
        onVisibleChange={handleVisibleChange}
        selectable={selectable}
        className={className}
        disabled={disabled}
      >
        {Object.keys(lineTypeMap).map((key) => {
          const lineProps: ILineTypeValue = lineTypeMap[key];
          const { lineType, size } = lineProps;
          return (
            <OptionStyled key={key} value={key} testid={key} className={classnames({ 'with-text': withText })}>
              {withText && (<TextSpan>{`${optionText!(lineTypeMap[key])}`}</TextSpan>)}
              <CommonLine
                lineType={lineType}
                size={size}
                color={color}
                length={length}
              />
            </OptionStyled>
          );
        })}
      </Select>
    );
  }
}
