import React from 'react';
import { Tooltip } from '~/components/tooltip';
import { Button } from '~/components/button';
import { DoubletoolbarNormal } from '~/components/icons';
import { SingleLineToolbar } from './SingleLineToolbar';
import { DoubleLineToolbar } from './DoubleLineToolbar';
import { ToolbarFrame } from './styled';

export enum EToolbarMode {
  singleLine = 'singleLine',
  doubleLine = 'doubleLine',
}
interface IToolbarProps {
  /**
   * 初始化工具栏的模式(单行或双行)，非受控模式
   */
  defaultMode?: EToolbarMode;
  /**
   * 工具栏的模式(单行或双行)，受控模式
   */
  mode?: EToolbarMode;
  /**
   * 工具栏模式发生变化时回调
   */
  onModeChange?: (mode: EToolbarMode) => void;
  /**
   * 是否启用自动折叠功能
   */
  autoFolding?: boolean;
  /**
   * 单行工具栏
   */
  singleLineToolbar?: React.ReactElement<SingleLineToolbar>;
  /**
   * 双行工具栏
   */
  doubleLineToolbar?: React.ReactElement<DoubleLineToolbar>;
}

interface IToolbarState {
  // 工具栏的（单、双行）模式
  innerMode: EToolbarMode;
}

export class Toolbar extends React.Component<IToolbarProps, IToolbarState> {
  constructor(props: IToolbarProps) {
    super(props);
    this.state = {
      innerMode: props.mode || props.defaultMode || EToolbarMode.singleLine,
    };
  }

  handleModeChange = () => {
    // setState是异步执行
    this.setState((preState, props) => {
      const { innerMode } = preState;
      const newMode = innerMode === EToolbarMode.singleLine
        ? EToolbarMode.doubleLine
        : EToolbarMode.singleLine;
      const { onModeChange } = props;
      // 受控模式
      if (onModeChange) {
        onModeChange(newMode);
      }
      return { innerMode: newMode };
    });
  };

  render() {
    const { mode, singleLineToolbar, doubleLineToolbar } = this.props;
    const { innerMode } = this.state;
    const activeMode = mode || innerMode;
    const isSingleLine = activeMode === EToolbarMode.singleLine;
    const iconRotate = isSingleLine ? 0 : 180;
    return (
      <ToolbarFrame>
        {isSingleLine ? singleLineToolbar : doubleLineToolbar}
        <Tooltip title="">
          <Button
            bordered={false}
            shape="rect"
            onClick={this.handleModeChange}
            icon={<DoubletoolbarNormal className="toggle-button" rotate={iconRotate} />}
          />
        </Tooltip>
      </ToolbarFrame>
    );
  }
}
