/* eslint-disable no-param-reassign */
/* eslint-disable @typescript-eslint/no-explicit-any */
import React, { useRef, useMemo, useCallback, useLayoutEffect, useState, PropsWithChildren, useEffect } from 'react';
import ReactDOM, { createPortal } from 'react-dom';
import { useTranslate } from '~/locale/locales';
import { TranslateProvider } from '~/locale/TranslateProvider';
import { Button, IButtonProps } from '~/components/button';
import { ClosedNormal, LoadingNormal, ReportFillNormal, CheckboxFillNormal, RoundcloseThickNormal, WarnFillNormal } from '~/components/icons';
import {
  Mask,
  Root,
  Wrap,
  Title,
  Content,
  Footer,
  Header,
  Body,
  CloseButton,
  FooterWrap,
  ConfirmButton,
  TitleTextWrap,
  TitleIconTextWrap,
  TitleIconWrap,
} from './styled';
import { KEY_CODES } from '~/utils';

export interface IModalConfirmOptions extends Pick<
IModalProps,
'okText' | 'cancelText' | 'locale' | 'onOk' | 'onCancel' | 'zIndex' | 'width' | 'title' | 'okButtonProps' | 'cancelButtonProps' | 'keyboard'
> {
  content: string | React.ReactNode;
}

export interface IModalMesOptions extends Pick<
IModalProps,
'okText' | 'locale' | 'onOk' | 'zIndex' | 'width' | 'title' | 'okButtonProps' | 'testid' | 'keyboard'
> {
  content: string | React.ReactNode;
  showLoadingIcon?: boolean;
}

export interface IModalProps {
  /**
   * 设置国际化语言
   */
  locale?: string;
  /**
   * 标题
   */
  title?: string | React.ReactNode;
  /**
   * 自定义按钮区
   */
  footer?: React.ReactNode | null;
  /**
   * 是否显示
   */
  visible?: boolean;
  /**
   * 确认按钮文案
   */
  okText?: string;
  /**
   * 取消按钮文案
   */
  cancelText?: string;
  /**
   * 宽度
   */
  width?: number;
  /**
   * 层叠顺序
   */
  zIndex?: number;
  /**
   * 确认按钮是否loading
   */
  confirmLoading?: boolean;
  /**
   * 是否有右上角关闭Icon
   */
  closable?: boolean;
  /**
   * 确认回调
   */
  onOk?: () => void;
  /**
   * 取消回调
   */
  onCancel?: () => void;
  /**
   * 可用于设置浮层的样式，调整浮层位置等
   */
  style?: React.CSSProperties;
  /**
   * Modal body 样式
   */
  bodyStyle?: React.CSSProperties;
  /**
   * 遮罩样式
   */
  maskStyle?: React.CSSProperties;
  /**
   * ok 按钮 props
   */
  okButtonProps?: IButtonProps;
  /**
   * cancel 按钮 props
   */
  cancelButtonProps?: IButtonProps;
  /**
   * 点击蒙层是否允许关闭 默认true
   */
  maskClosable?: boolean;
  /**
   * 测试使用
   */
  testid?: string;
  /**
   * 是否支持键盘 esc 关闭
   */
  keyboard?: boolean;
}

interface IModalMethodResult {
  destroy: () => void; // 函数调用生成Modal后，可以根据函数返回值关闭Modal
}

interface IModalMethods {
  confirm: (e: IModalConfirmOptions) => IModalMethodResult;
  info: (e: IModalMesOptions) => IModalMethodResult;
  success: (e: IModalMesOptions) => IModalMethodResult;
  error: (e: IModalMesOptions) => IModalMethodResult;
  warning: (e: IModalMesOptions) => IModalMethodResult;
}

// eslint-disable-next-line max-lines-per-function
export const Modal: React.FC<IModalProps> & IModalMethods = Object.assign((
  props: PropsWithChildren<IModalProps>,
) => {
  const {
    visible,
    onOk,
    title,
    children,
    onCancel,
    width,
    footer,
    confirmLoading,
    bodyStyle,
    maskStyle,
    style,
    closable,
    okButtonProps,
    cancelButtonProps,
    zIndex,
    maskClosable = true,
    testid,
    keyboard,
  } = props;

  // 是否在加载中
  const [confirming, setConfirming] = useState(false);
  const finalConfirming: boolean = confirmLoading === undefined ? confirming : confirmLoading;

  // 记录是否已经显示过
  const mounted = useRef(false);
  mounted.current = mounted.current || !!visible;

  // 组件是否已被卸载
  const isUnmounted = useRef(false);

  // 国际化
  const t = useTranslate(props.locale);

  // OK和cancel的文案
  const okText = props.okText || t('we_ui_ok_text');
  const cancelText = props.cancelText || t('we_ui_cancel_text');

  // 创建container节点
  const oContainer = useMemo(() => {
    if (mounted.current) {
      const oDiv = document.createElement('div');
      oDiv.style.position = 'absolute';
      oDiv.style.left = '0px';
      oDiv.style.top = '0px';
      oDiv.style.width = '100%';
      return oDiv;
    }
    return null;
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [mounted.current]);

  // 插入节点到body
  useLayoutEffect(() => {
    if (oContainer) {
      document.body.appendChild(oContainer);
    }
    return (): void => {
      if (oContainer) {
        document.body.removeChild(oContainer);
      }
      isUnmounted.current = true;
    };
  }, [oContainer]);

  /**
   * 点击遮罩
   *
   * @return {void}
   */
  const handleClose = useCallback(() => {
    if (onCancel) {
      onCancel();
    }
  }, [onCancel]);

  /**
   * 阻止冒泡
   */
  const stopPropagation = useCallback((e: React.MouseEvent<HTMLElement>) => {
    e.stopPropagation();
  }, []);

  /**
   * 确认点击
   */
  const handleConfirm = useCallback(() => {
    if (onOk) {
      const returnedValue: any = onOk();
      if (returnedValue && returnedValue instanceof Promise) {
        setConfirming(true);
        returnedValue.then(() => {
          if (!isUnmounted.current) setConfirming(false);
        }).catch((e) => {
          // eslint-disable-next-line no-console
          console.error(e);
        });
      }
    }
  }, [onOk, isUnmounted]);

  const maskStyleCombined = useMemo(() => {
    const v = { ...(maskStyle || {}) };
    if (typeof zIndex === 'number') {
      v.zIndex = zIndex;
    }
    return v;
  }, [maskStyle, zIndex]);

  const wrapStyle = useMemo(() => {
    return typeof zIndex === 'number' ? { zIndex } : undefined;
  }, [zIndex]);

  const onClickMask = useCallback(() => {
    if (maskClosable) {
      handleClose();
    }
  }, [maskClosable, handleClose]);

  useEffect(() => {
    const handleKeyDown = (event: KeyboardEvent) => {
      if (event.which === KEY_CODES.ESC) {
        handleClose();
      }
    };

    if (visible && keyboard) {
      document.addEventListener('keydown', handleKeyDown);
    }
    return () => {
      if (visible && keyboard) {
        document.removeEventListener('keydown', handleKeyDown);
      }
    };
  }, [handleClose, keyboard, visible]);

  return oContainer ? createPortal(
    <Root data-testid={testid ? `Modal_${testid}` : undefined}>
      <Mask visible={visible} style={maskStyleCombined} />
      <Wrap visible={visible} onMouseDown={onClickMask} style={wrapStyle}>
        <Content width={width} onMouseDown={stopPropagation} style={style} data-testid={testid ? `Modal_content_${testid}` : undefined}>
          {!!title && <Header><Title>{title}</Title></Header>}
          <Body style={bodyStyle}>{children}</Body>
          {
            footer !== null && (
              <Footer>
                {footer || (
                  <FooterWrap>
                    <Button
                      shape="round"
                      onClick={handleClose}
                      testid={testid ? `${testid}_close` : undefined}
                      {...(cancelButtonProps || {})}
                    >
                      {cancelText}
                    </Button>
                    <ConfirmButton
                      shape="round"
                      type="primary"
                      icon={finalConfirming ? <LoadingNormal spin /> : undefined}
                      onClick={handleConfirm}
                      testid={testid ? `${testid}_confirm` : undefined}
                      {...(okButtonProps || {})}
                    >
                      {okText}
                    </ConfirmButton>
                  </FooterWrap>
                )}
              </Footer>
            )
          }
          {
            !!closable && (
              <CloseButton onClick={handleClose}>
                <ClosedNormal className="close-icon" testid={testid ? `${testid}_CloseButton` : undefined} />
              </CloseButton>
            )
          }
        </Content>
      </Wrap>
    </Root>,
    oContainer,
  ) : null;
}, {
  confirm(options = {} as IModalConfirmOptions): IModalMethodResult {
    const { content, onOk, onCancel, ...props } = options;
    let oConfirmContainer: HTMLDivElement | undefined;
    oConfirmContainer = document.createElement('div');
    document.body.appendChild(oConfirmContainer);

    const destroy = () => {
      ReactDOM.unmountComponentAtNode(oConfirmContainer!);
      document.body.removeChild(oConfirmContainer!);
      oConfirmContainer = undefined;
    };

    const handleOk = () => {
      let returnedValue: any;
      if (onOk) {
        returnedValue = onOk();
      }
      if (returnedValue && returnedValue instanceof Promise) {
        return returnedValue
          .then(() => destroy());
      } else {
        destroy();
      }
    };

    const handleCancel = () => {
      if (onCancel) {
        onCancel();
      }
      destroy();
    };

    ReactDOM.render(
      <Modal
        {...props}
        visible
        title={options.title}
        width={options.width || 420}
        onOk={handleOk}
        onCancel={handleCancel}
      >
        {content || ''}
      </Modal>,
      oConfirmContainer,
    );
    return { destroy };
  },
  /**
   * 辅助构造不同类型的信息提示模态框（只提供一个按钮用于关闭）
   * 接受不同的 icon 元素作为参数，返回用于构造信息提示模态框的方法
   */
  // eslint-disable-next-line max-lines-per-function
  makeMessageModal(iconNode: React.ReactElement) {
    return function (options = {} as IModalMesOptions) {
      const {
        content,
        onOk,
        title,
        okText,
        okButtonProps,
        showLoadingIcon = true,
        ...props
      } = options;
      let oMesContainer: HTMLDivElement | undefined;

      if (!oMesContainer) {
        oMesContainer = document.createElement('div');
        document.body.appendChild(oMesContainer);
      }

      const destroy = () => {
        ReactDOM.unmountComponentAtNode(oMesContainer!);
        document.body.removeChild(oMesContainer!);
        oMesContainer = undefined;
      };

      const handleOk = () => {
        let returnedValue: any;
        if (onOk) {
          returnedValue = onOk();
        }
        if (returnedValue && returnedValue instanceof Promise) {
          if (showLoadingIcon) render({ visible: true, loading: true });
          return returnedValue
            .then(() => destroy());
        } else {
          destroy();
        }
      };

      const handleCancel = () => {
        render({ visible: false });
        destroy();
      };

      function render({ visible, loading }: { visible: boolean, loading?: boolean | undefined }) {
        const titleNode = (
          <TitleIconTextWrap>
            {iconNode}
            <TitleTextWrap>{title}</TitleTextWrap>
          </TitleIconTextWrap>
        );

        const el = (
          <Modal
            {...props}
            visible={visible}
            title={titleNode}
            width={options.width || 420}
            onCancel={handleCancel}
            bodyStyle={{ paddingLeft: '34px' }}
            footer={(
              <FooterWrap>
                <TranslateProvider>{(t) => (
                  <ConfirmButton
                    shape="round"
                    type="primary"
                    icon={loading ? <LoadingNormal spin /> : undefined}
                    onClick={handleOk}
                    testid={options.testid ? `${options.testid}_ConfirmButton` : undefined}
                    {...(okButtonProps || {})}
                  >
                    {okText || t('we_ui_ok_text')}
                  </ConfirmButton>
                )}
                </TranslateProvider>

              </FooterWrap>
            )}
          >
            {content || ''}
          </Modal>);

        ReactDOM.render(
          el,
          oMesContainer!,
        );
      }

      render({ visible: true, loading: false });
      return { destroy };
    };
  },
  info(options = {} as IModalMesOptions) {
    const infoModal = this.makeMessageModal((
      <TitleIconWrap colorKey="colorBlue1">
        <ReportFillNormal />
      </TitleIconWrap>
    ));
    return infoModal(options);
  },
  success(options = {} as IModalMesOptions) {
    const successModal = this.makeMessageModal((
      <TitleIconWrap colorKey="colorGreen1">
        <CheckboxFillNormal />
      </TitleIconWrap>
    ));
    return successModal(options);
  },
  error(options = {} as IModalMesOptions) {
    const errorModal = this.makeMessageModal((
      <TitleIconWrap colorKey="colorRed1">
        <RoundcloseThickNormal />
      </TitleIconWrap>
    ));
    return errorModal(options);
  },
  warning(options = {} as IModalMesOptions) {
    const warningModal = this.makeMessageModal((
      <TitleIconWrap colorKey="colorOrange1">
        <WarnFillNormal />
      </TitleIconWrap>
    ));
    return warningModal(options);
  },
});

Modal.defaultProps = {
  width: 520,
  closable: true,
  keyboard: true,
};
