import React from 'react';
import { classnames } from '~/utils';
import {
  ButtonWrap,
  ButtonChildrenWrap,
  ButtonInner,
} from './styled';
import { Tooltip } from '../tooltip';

export type ButtonType = 'primary' | 'danger' | 'ghost' | 'text' | 'normal';

export type ButtonShape = 'round' | 'circle' | 'rect';

export type ButtonSize = 'small' | 'large' | 'normal';

export interface IButtonProps {
  /**
   * 设置按钮大小
   */
  size?: ButtonSize;
  /**
   * 设置按钮类型
   */
  type?: ButtonType;
  /**
   * 设置按钮形状
   */
  shape?: ButtonShape;
  /**
   * 设置原生button的type值
   */
  htmlType?: string;
  /**
   * 设置按钮的图标组件
   */
  icon?: React.ReactNode;
  /**
   * 是否禁用
   */
  disabled?: boolean;
  /**
   * 是否有边框
   */
  bordered?: boolean;
  /**
   * 样式
   */
  style?: React.CSSProperties;
  /**
   * 类名
   */
  className?: string;
  /**
   * 点击
   */
  onClick?: (e: React.MouseEvent<HTMLElement>) => void;
  /**
   * 双击
   */
  onDoubleClick?: (e: React.MouseEvent<HTMLElement>) => void;
  /**
   * [mouse down] or [touch start]
   */
  onMouseDown?: (e: React.MouseEvent<HTMLElement>) => void;
  /**
   * 是否为激活态
   */
  active?: boolean;
  /**
   * 聚焦index
   */
  tabIndex?: string
  /**
   * tooltip
   */
  title?: string | React.ReactNode;
  /**
   * 暴露 data-testid, 用于测试
   */
  testid?: string;
}

const Button: React.FC<IButtonProps> = React.memo(React.forwardRef((props, ref) => {
  const {
    style,
    children,
    className,
    onClick,
    shape,
    size,
    type,
    onDoubleClick,
    onMouseDown,
    bordered,
    icon,
    active,
    disabled,
    tabIndex,
    title,
    testid,
    ...restProps
  } = props;

  const buttonNode = (
    <ButtonWrap
      {...restProps}
      className={classnames(className, { active })}
      shape={shape}
      style={style}
      size={size}
      type={type}
      ref={ref}
      onClick={disabled ? undefined : onClick}
      onDoubleClick={disabled ? undefined : onDoubleClick}
      onMouseDown={disabled ? undefined : onMouseDown}
      bordered={bordered}
      disabled={disabled}
      onlyIcon={!!icon && !children}
      tabIndex={tabIndex === '-1' ? undefined : tabIndex || '0'}
      data-testid={testid ? `button-${testid}` : undefined}
    >
      <ButtonInner>
        {icon}
        <ButtonChildrenWrap>{children}</ButtonChildrenWrap>
      </ButtonInner>
    </ButtonWrap>
  );

  return title ? (
    <Tooltip title={title}>{buttonNode}</Tooltip>
  ) : buttonNode;
}));

Button.defaultProps = {
  shape: 'rect',
  size: 'normal',
  type: 'normal',
  bordered: true,
  icon: null,
  active: false,
  disabled: false,
};

export { Button };
