import styled, { css } from 'styled-components';
import { Input } from '~/components/input';
import { resetComponent, Ellipsis } from '~/utils/styled.mixin';
import { withTheme } from '../theme';

const iconStyle = css`
  text-transform: none;
  text-rendering: optimizeLegibility;
  line-height: 0;
  text-align: center;
`;

export const SelectWrap = withTheme(styled.div<any>`
  ${resetComponent}
  display: inline-block;
  user-select: none;
  height: ${({ theme }) => theme.ui.elementHeight};
  padding: ${({ showArrow }) => showArrow && '0 4px 0 6px'};
  // margin: 0 4px 0 2px;
  border-radius: ${({ theme }) => theme.ui.radius};
  transition: all .1s ease-in-out;
  cursor: pointer;
  outline: none;

  &.small {
    height: ${({ theme }) => theme.ui.elementHeightSmall};
  }

  &.large {
    height: ${({ theme }) => theme.ui.elementHeightLarge};
    padding: ${({ showArrow }) => showArrow && '0 12px 0 12px'};
  }

  &:hover {
    background-color: ${({ theme }) => theme.ui.commonBgHoverColor};
  }

  &:active, &.actived {
    background-color: ${({ theme }) => theme.ui.commonBgPressColor};
    outline: none;
  }

  &.bordered {
    border: 1px solid ${({ theme }) => theme.ui.commonLineLightColor};
  }

  &.errored {
    border: 1px solid #FF523C;
  }

  &.disabled {
    cursor: not-allowed;
    color: ${({ theme }) => theme.ui.disabledColor};
    background-color: transparent;

    & .weicon {
      color: ${({ theme }) => theme.ui.disabledColor};
    }

    & .select-arrow {
      color: ${({ theme }) => theme.ui.disabledColor};;
    }
  }

`);

export const TriggerWrap = withTheme(styled.div`
  position: relative;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  .weicon {
    // 使得 icon 可以正确垂直居中
    display: block;
    color: ${({ theme }) => theme.ui.commonActionIconColor};
  }

  .select-arrow {
    ${iconStyle}
    color: ${({ theme }) => theme.ui.commonBaseColor3};
    flex-shrink: 0;
    flex-grow: 0;
    font-size: 12px;
    transform: rotate(0deg) scale(0.67);
    transform-origin: center center;
    margin-left: 2px;
    &.opened {
      transform: rotate(180deg) scale(0.67);
    }
    &.hidden {
      display: none;
    }
  }
`);

export const TriggerContent = withTheme(styled.div`
  ${Ellipsis};
  flex-grow: 1;
  flex-shrink: 1;
`);

export const OverlayWrap = withTheme(styled.div`
  max-height: 400px;
  overflow-y: auto;
  padding: ${({ theme }) => theme.ui.padding} 0;
`);


export const SelectOptionWrap = withTheme(styled.div`
  ${resetComponent}
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: ${({ theme }) => theme.ui.paddingSmall} 20px;
  margin: 0 ${({ theme }) => theme.ui.marginMedium};
  height: 100%;
  min-width: 100px;
  min-height: 32px;
  // 字体设置为 13px，行高 20px;
  // 参考 ant-d: https://ant.design/docs/spec/font-cn
  font-size: 13px;
  line-height: 20px;
  white-space: nowrap;
  cursor: pointer;
  user-select: none;
  border-radius: ${({ theme }) => theme.ui.radiusLarge};

  &:hover, &.hovered {
    background-color: ${({ theme }) => theme.ui.backgroundColor};
  }

  &.selected {
    .check-icon {
      display: inline-block;
    }
  }

  &.disabled {
    color: ${({ theme }) => theme.ui.disabledColor};
    background-color: transparent;
    cursor: not-allowed;
  }

  .check-icon {
    ${iconStyle}
    color: ${({ theme }) => theme.ui.primaryColor};
    font-size: 12px;
    display: none;
    color: #3296fa;
    position: absolute;
    left: 4px;
    top: 50%;
    transform: translateY(-50%);
  }
`);

export const InputStyled = withTheme(styled(Input)`
  border: 0;
  background-color: transparent;
  color: inherit;
  font-size: inherit;
  line-height: inherit;
  padding-left: 0;
  padding-right: 0;

  // 需要 overide 默认的 input 样式
  & > input {
    font-size: inherit;
    color: inherit;
    line-height: inherit;
  }

`);

export const EmptyWrap = withTheme(styled.div`
  padding: 60px 24px;
`);

export const OptionGroupWrap: any = withTheme(styled.div`
  position: relative;
  &:after {
    display: block;
    content: ' ';
    height: 1px;
    background-color: ${({ theme }) => theme.ui.commonLineLightColor};
    width: 80%;
    margin: 6px auto;
  }
  &:before {
    display: block;
    content: ' ';
    height: 1px;
    background-color: ${({ theme }) => theme.ui.commonLineLightColor};
    width: 80%;
    margin: 6px auto;
  }
  &:nth-child(1) {
    &:before {
      display: none;
    }
  }
  &:last-child {
    &:after {
      display: none;
    }
  }
  ~ .select-group-wrapper {
    &:before {
      display: none;
    }
  }
`);
