import React, { useCallback } from 'react';
import useControlledValue from '~/hooks/useControlledValue';
import { Dropdown } from '~/components/dropdown';
import { ArrowDownTriangleNormal } from '~/components/icons';
import { SplitButtonWrap, SplitButtonLeft, SplitButtonRight, SplitButtonInner } from './styled';
import { ButtonSize } from './Button';
import { classnames } from '~/utils';

export interface ISplitButtonProps {
  /**
   * 设置按钮大小
   */
  size?: ButtonSize;
  /**
   * 默认是否展开
   */
  defaultActive?: boolean;
  /**
   * 是否展开
   */
  active?: boolean;
  /**
   * 是否禁用
   */
  disabled?: boolean;
  /**
   * 是否有边框
   */
  bordered?: boolean;
  /**
   * 类名
   */
  className?: string;
  /**
   * 样式
   */
  style?: React.CSSProperties;
  /**
   * 下拉内容
   */
  overlay: React.ReactElement;
  /**
   * 下拉内容样式
   */
  overlayStyle?: React.CSSProperties;
  /**
   * 点击左边
   */
  onClickLeft?: (e: React.MouseEvent<HTMLElement>) => void;
  /**
   * 激活态变化
   */
  onActiveChange?: (active: boolean) => void;
  /**
   * 层级
   */
  zIndex?: number;
  /**
   * 暴露 data-testid, 用于测试
   */
  testid?: string;
  /**
   * Tooltip
   */
  title?: string | React.ReactNode;
  /**
   * 背景色高亮
   */
  highlight?: boolean;
}

const SplitButton: React.FC<ISplitButtonProps> = React.memo((props) => {
  const {
    disabled,
    overlay,
    overlayStyle,
    className,
    onActiveChange,
    children,
    style,
    size,
    bordered,
    onClickLeft,
    zIndex,
    testid,
    title,
    highlight,
  } = props;

  const [active, setActive] = useControlledValue(props.active, props.defaultActive);

  /**
   * 下拉面板是否可见修改函数
   *
   * @param {boolean} visible
   * @return {void}
   */
  const onVisibleChange = useCallback((visible: boolean) => {
    if (!disabled) {
      if (onActiveChange) {
        onActiveChange(visible);
      }
      setActive(visible);
    }
  }, [onActiveChange, setActive, disabled]);

  /**
   * 点击左部区域
   *
   * @param {React.MouseEvent<HTMLElement>}
   * @return {void}
   */
  const handleClickLeft = useCallback((e: React.MouseEvent<HTMLElement>) => {
    if (!disabled) {
      if (!active) {
        e.stopPropagation();
      }
      if (onClickLeft) {
        onClickLeft(e);
      }
    }
  }, [disabled, active, onClickLeft]);


  return (
    <Dropdown
      title={title}
      visible={disabled ? false : active}
      overlay={React.cloneElement(overlay, { 'data-testid': testid && `overlay-${testid}` })}
      overlayStyle={overlayStyle}
      onVisibleChange={onVisibleChange}
      zIndex={zIndex}
    >
      <SplitButtonWrap
        active={active}
        disabled={disabled}
        className={classnames(className, { active: active || highlight })}
        size={size}
        style={style}
        bordered={bordered}
      >
        <SplitButtonInner>
          <SplitButtonLeft
            disabled={disabled}
            onClick={handleClickLeft}
            data-testid={testid && `splitbutton-left-${testid}`}
            className="splitbutton-left"
          >
            {children}
          </SplitButtonLeft>
          <SplitButtonRight
            active={active}
            disabled={disabled}
            className="splitbutton-right"
          >
            <ArrowDownTriangleNormal className="arrow-icon" data-testid={testid && `splitbutton-arrow-${testid}`} />
          </SplitButtonRight>
        </SplitButtonInner>
      </SplitButtonWrap>
    </Dropdown>
  );
});

SplitButton.defaultProps = {
  defaultActive: false,
  size: 'normal',
  bordered: false,
};

export { SplitButton };
