import React, { useMemo } from 'react';
import { Tooltip } from '../tooltip';
import {
  AvatarWrap,
  AvatarImage,
  AvatarInner,
  AvatarCorner,
  // AvatarColor,
  TooltipWrap,
  // TextSpan,
  NameWrap,
} from './styled';
import { AvatarGroup, IAvatarGroupProps } from './AvatarGroup';
import { ViewNormal, EditorNormal } from '../icons';
import { isEmojiCharacter } from './util';

export type IAvatarSize = 'small' | 'normal' | 'large';

export interface IAvatarProps {
  /**
   * 头像url，数组表示群聊
   */
  url?: string | string[];
  /**
   * 如果需要颜色
   */
  color?: string;
  /**
   * 用户名字
   */
  name?: string;
  /**
   * 用户的状态
   */
  status?: 'can-edit' | 'can-view' | 'is-editing' | 'is-viewing'
  /**
   * Tooltip提示信息
   */
  title?: string;
  /**
   * 样式
   */
  style?: React.CSSProperties;
  /**
   * 点击头像
   */
  onClick?: React.MouseEventHandler<HTMLElement>;
  /**
   * 类名
   */
  className?: string;
  /**
   * 自定义角落图标
   */
  corner?: React.ReactNode;
  /**
   * 头像的尺寸
   */
  size?: IAvatarSize;
  /**
   * 名字大小
   */
  nameWrapStyle?: React.CSSProperties;
}

const GROUP_DEFAULT_AVATAR = '//gw.alicdn.com/tfs/TB1TBGRHW61gK0jSZFlXXXDKFXa-144-144.png';

const SINGLE_DEFAULT_AVATAR = '//gw.alicdn.com/tfs/TB1yx7PjsKfxu4jSZPfXXb3dXXa-144-144.png';

export const Avatar: React.FC<IAvatarProps> & {
  Group: React.FC<IAvatarGroupProps>;
} = Object.assign(React.memo((props: IAvatarProps) => {
  const {
    url,
    style,
    className,
    name,
    color,
    corner,
    title,
    status,
    onClick,
    size,
    isInOverlay,
    nameWrapStyle,
  } = props as IAvatarProps & { isInOverlay: boolean };

  /**
   * 头像情况：
   * 1) 名字、蓝底作为头像：url不存在，且名字存在
   * 2）多人头像：url为数组，如果url为空数组，则为默认群聊头像
   * 2）单人头像：url为字符串，如果url不存在，则为默认单人头像
   */
  let urls: string[] = [];
  // 过滤 emoji
  const avatarFilterName = Array.from(name || '').reverse().find(element => !isEmojiCharacter(element));

  const isNameAvatar = !url && !!avatarFilterName; // 是否为名字头像

  if (!isNameAvatar) {
    // 统一转为字符串数组
    urls = (Array.isArray(url) ? url : [url]).filter(u => u).slice(-4) as string[];
    // 如果数组为空，根据是否群聊设置默认头像
    if (urls.length === 0) {
      urls.push(Array.isArray(url) ? GROUP_DEFAULT_AVATAR : SINGLE_DEFAULT_AVATAR);
    }
  }

  const iconProps = useMemo(() => ({
    style: { color },
    className: 'status-icon',
  }), [color]);

  // 优先使用 title (为空字符串则无 tooltip)，若 name 和 status 均传入则按规则渲染 tooltip
  const avatarTitle = (title !== undefined)
    ? title
    : typeof name === 'string' && status && (
      <TooltipWrap >
        <span style={{ paddingRight: 6 }}>{name}</span>
        {/* TODO: status -> 国际化文案 */}
        {/* <TextSpan>{status}</TextSpan> */}
        {(status === 'can-edit' || status === 'is-editing') ? <EditorNormal {...iconProps} /> : <ViewNormal {...iconProps} />}
      </TooltipWrap>
    );

  return (
    <Tooltip title={avatarTitle}>
      <AvatarWrap
        style={style}
        className={className}
        onClick={onClick}
        size={size}
      >
        <AvatarInner color={isInOverlay ? undefined : color}>
          {
            urls.map((src, index) => {
              return (
                <AvatarImage
                  key={src}
                  className={`avatar_${urls.length}_${index + 1}`}
                  url={src}
                />
              );
            })
          }
          {
            isNameAvatar && (
              <NameWrap>
                <span style={nameWrapStyle}>{avatarFilterName}</span>
              </NameWrap>
            )
          }
        </AvatarInner>
        {
          (corner) && (
            <AvatarCorner>
              {
                // color ? (
                //   <AvatarColor backgroundColor={color} />
                // ) : corner
                corner
              }
            </AvatarCorner>
          )
        }
      </AvatarWrap>
    </Tooltip>
  );
}), {
  Group: AvatarGroup,
});

Avatar.defaultProps = {
  size: 'normal',
};

Avatar.displayName = 'Avatar';
