import React, { useMemo, useCallback } from 'react';
import useControlledValue from '~/hooks/useControlledValue';
import { useTranslate } from '~/locale/locales';
import {
  PageFlowBetaNormal,
  PageWebBetaNormal,
  PageLayoutBetaNormal,
} from '../icons';
import { Select, SelectOption } from '../select';
import { OptionWrap, TriggerWrap } from './styled';
import { TOptionTitle } from '../select/interface';

export type PageView = 'page-view' | 'paging-view' | 'web-view';

export interface IPageViewSelectProps {
  /**
   * 默认的页面视图，当设置了value，该项忽略
   */
  defaultValue?: PageView;
  /**
   * 当前页面视图
   */
  value?: PageView;
  /**
   * 设置国际化语言
   */
  locale?: string;
  /**
   * select 样式
   */
  style?: React.CSSProperties;
  /**
   * 页面视图改变时的回调函数
   */
  onChange?: (view: PageView) => void;
  /**
   * 层级
   */
  zIndex?: number;
  /**
   * tooltip提示
   */
  title?: string | React.ReactNode;
  /**
   * 选项的tooltip提示
   */
  titles?: Record<string, TOptionTitle>;
  /**
   * 是否禁止
   */
  disabled?: boolean;
}

const PageViewSelect: React.FC<IPageViewSelectProps> = (props) => {
  const { locale, style, onChange, title, titles, zIndex, disabled } = props;
  const [value, setValue] = useControlledValue(props.value, props.defaultValue);
  const t = useTranslate(locale);

  // 页面视图的选项
  const options = useMemo(() => [
    { title: t('we_ui_view_page_view'), value: 'page-view', Icon: PageFlowBetaNormal },
    { title: t('we_ui_view_paging_view'), value: 'paging-view', Icon: PageLayoutBetaNormal },
    { title: t('we_ui_view_web_view'), value: 'web-view', Icon: PageWebBetaNormal },
  ], [t]);

  // 该组件的trigger区域显示Icon+文字
  const renderTrigger = useCallback((selectedView?: string) => {
    const index = options.findIndex((option) => option.value === selectedView);
    const currentOption = options[index];
    return (
      <TriggerWrap style={{ minWidth: 60 }}>
        {
          currentOption && (
            <>
              <currentOption.Icon style={{ marginRight: '8px' }} />
              {currentOption.title}
            </>
          )
        }
      </TriggerWrap>
    );
  }, [options]);

  const handleChange = useCallback((e: PageView) => {
    if (onChange) {
      onChange(e);
    }
    setValue(e);
  }, [setValue, onChange]);

  return (
    <Select
      value={value}
      onChange={handleChange}
      style={style}
      renderTrigger={renderTrigger}
      zIndex={zIndex}
      title={title}
      testid="PageViewSelect"
      disabled={disabled}
    >
      {
        options.map((option) => (
          <SelectOption value={option.value} key={option.value} title={titles![option.value]}>
            <OptionWrap>
              <option.Icon className="icon" />
              <span>{option.title}</span>
            </OptionWrap>
          </SelectOption>
        ))
      }
    </Select>
  );
};

PageViewSelect.defaultProps = {
  defaultValue: 'page-view',
  titles: {},
  disabled: false,
};

export default PageViewSelect;

