import React, { useMemo, useCallback } from 'react';
import useControlledValue from '~/hooks/useControlledValue';
import { useTranslate } from '~/locale/locales';
import {
  LeftalignNormal,
  RightalignNormal,
  JustifyNormal,
  CenteralignNormal,
} from '../icons';
import { Select } from '../select';
import { TriggerWrap, OptionWrap } from './styled';
import { TOptionTitle } from '../select/interface';

export type HorizontalAlign = 'left' | 'center' | 'right' | 'justify';

export interface IHorizontalAlignSelectProps {
  /**
   * 默认的对齐方式，当设置了value，该项忽略
   */
  defaultValue?: HorizontalAlign;
  /**
   * 对齐方式
   */
  value?: HorizontalAlign;
  /**
   * 设置国际化语言
   */
  locale?: string;
  /**
   * select 样式
   */
  style?: React.CSSProperties;
  /**
   * 对齐方式修改时的回调函数
   */
  onChange?: (align: HorizontalAlign) => void;
  /**
   * 层级
   */
  zIndex?: number;
  /**
   * 类名
   */
  className?: string;
  /**
   * 是否禁用
   */
  disabled?: boolean;
  /**
   * 禁用的选项列表
   */
  disabledKeys?: HorizontalAlign[];
  /**
   * 选项的tooltip提示
   */
  titles?: Record<string, TOptionTitle>;
  /**
   * tooltip提示
   */
  title?: string | React.ReactNode;
}

interface ISelectOption {
  title: string;
  value: HorizontalAlign;
  Icon: React.ComponentType<any>;
}


const HorizontalAlignSelect: React.FC<IHorizontalAlignSelectProps> = (props) => {
  const { locale, onChange, disabledKeys, title, titles, style, ...restProps } = props;
  const [value, setValue] = useControlledValue(props.value, props.defaultValue);
  const t = useTranslate(locale);

  // 垂直对齐的选项
  const options: ISelectOption[] = useMemo(() => [
    { title: t('we_ui_align_left'), value: 'left', Icon: LeftalignNormal },
    { title: t('we_ui_align_center'), value: 'center', Icon: CenteralignNormal },
    { title: t('we_ui_align_right'), value: 'right', Icon: RightalignNormal },
    { title: t('we_ui_align_justify'), value: 'justify', Icon: JustifyNormal },
  ], [t]);

  // 该组件的trigger区域只显示Icon
  const renderTrigger = useCallback((align?: string) => {
    const index = options.findIndex((option) => option.value === align);
    const currentOption = options[index];
    return (
      <TriggerWrap style={{ minWidth: 16 }}>
        {currentOption && <currentOption.Icon />}
      </TriggerWrap>
    );
  }, [options]);

  const handleChange = useCallback((e: HorizontalAlign) => {
    if (onChange) {
      onChange(e);
    }
    setValue(e);
  }, [onChange, setValue]);

  return (
    <Select
      {...restProps}
      value={value}
      style={style || { padding: '0 0 0 6px', marginRight: -1 }}
      onChange={handleChange}
      renderTrigger={renderTrigger}
      testid="HorizontalAlignSelect"
      title={title}
    >
      {
        options.map((option) => (
          <OptionWrap
            value={option.value}
            key={option.value}
            testid={option.value}
            disabled={disabledKeys?.includes(option.value)}
            title={titles![option.value]}
          >
            <option.Icon className="icon" />
            <span>{option.title}</span>
          </OptionWrap>
        ))
      }
    </Select>
  );
};

HorizontalAlignSelect.defaultProps = {
  defaultValue: 'left',
  titles: {},
};

export default HorizontalAlignSelect;
