/* eslint-disable no-nested-ternary */
/* eslint-disable @typescript-eslint/no-explicit-any */
/* eslint-disable @typescript-eslint/explicit-function-return-type */
// /* eslint-disable @typescript-eslint/explicit-function-return-type */
import styled, { css } from 'styled-components';
import { withTheme } from '~/components/theme';
import { resetComponent, Ellipsis } from '~/utils/styled.mixin';

const commonButton = css<any>`
  // 设置行高为 22px 
  // 参考 ant-d: https://ant.design/docs/spec/font-cn
  ${resetComponent}
  display: inline-block;
  width: fit-content;
  border: 1px solid ${({ theme }) => theme.ui.commonLineLightColor};
  outline: 0;
  height: ${({ theme, size }) => {
    let sizeString = '';
    if (size === 'small') sizeString = 'Small';
    if (size === 'large') sizeString = 'Large';
    return theme.ui[`elementHeight${sizeString}`];
  }};
  ${({ theme, onlyIcon, size }) => {
    const isRect = onlyIcon;
    const width = theme.ui[`elementHeight${size === 'small' ? 'Small' : size === 'large' ? 'Large' : ''}`];
    return isRect ? `
      width: ${width};
    ` : '';
  }};
  min-width: ${({ theme, size, onlyIcon }) => {
    let sizeString = '';
    if (onlyIcon) return 'auto';
    if (size === 'small') sizeString = 'Small';
    if (size === 'large') sizeString = 'Large';
    return theme.ui[`elementHeight${sizeString}`];
  }};
  padding: ${({ theme, onlyIcon }) => {
    if (onlyIcon) return '0';
    return `0 ${theme.ui.paddingLarge}`;
  }};
  cursor: ${({ disabled }) => (disabled ? 'not-allowed' : 'pointer')};
  transition: all .2s ease-out;
  user-select: none;
`;

const roundButton = css`
  border-radius: ${({ theme }) => theme.ui.elementHeightSmall};
`;

const rectButton = css`
  border-radius: ${({ theme }) => theme.ui.radius};
`;

const circleButton = css<any>`
  border-radius: 50%;
`;

const primaryButton = css<any>`
  background-color: ${({ theme }) => theme.ui.primaryColor};
  border-color: ${({ theme }) => theme.ui.primaryColor};
  color: ${({ theme }) => theme.ui.colorGray1};

  ${({ theme, disabled }) => {
    if (!disabled) {
      return `
        &:hover, &:focus {
          border-color: ${theme.ui.primaryHoverColor};
          background-color: ${theme.ui.primaryHoverColor};
        }

        &:active, &.active {
          border-color: ${theme.ui.primaryPressColor};
          background-color: ${theme.ui.primaryPressColor};
        }
      `;
    }
    return `
      border-color: ${theme.ui.primaryDisabledColor};
      background-color: ${theme.ui.primaryDisabledColor};
    `;
  }};
`;

const normalButton = css<any>`
  border-color: ${({ theme }) => theme.ui.borderColor};

  ${({ theme, disabled }) => {
    if (!disabled) {
      return `
        &:hover, &:focus {
          background-color: ${theme.ui.buttonNormalHoverBgColor};
        }

        &.toolBoxHover {
          background-color: ${theme.ui.buttonNormalHoverBgColor};
        }

        &:active {
          background-color: ${theme.ui.buttonNormalClickBgColor};
        }

        &.active {
          background-color: ${theme.ui.buttonNormalActiveBgColor};
        }
      `;
    }
    return `
      border-color: ${theme.ui.buttonNormalDisabledBorderColor};
      background-color: transparent;
      color: ${theme.ui.disabledColor};
    `;
  }};
`;

const dangerButton = css<any>`
  background-color: ${({ theme }) => theme.ui.buttonDangerBgColor};
  border-color: ${({ theme }) => theme.ui.buttonDangerBgColor};
  color: ${({ theme }) => theme.ui.colorGray1};

  ${({ theme, disabled }) => {
    if (!disabled) {
      return `
        &:hover, &:focus {
          background-color: ${theme.ui.buttonDangerHoverBgColor};
        }

        &:active, &.active {
          background-color: ${theme.ui.buttonDangerActiveBgColor};
        }
      `;
    }
    return `
      border-color: ${theme.ui.buttonDangerDisabledBgColor};
      background-color: ${theme.ui.buttonDangerDisabledBgColor};
    `;
  }};
`;

const ghostButton = css<any>`
  background-color: transparent;
  border-color: ${({ theme }) => theme.ui.colorGray1};
  color: ${({ theme }) => theme.ui.colorGray1};

  ${({ theme, disabled }) => {
    if (!disabled) {
      return `
        &:hover, &:focus {
          background-color: ${theme.ui.buttonGhostHoverBgColor};
          border-color: ${theme.ui.buttonGhostHoverBorderColor};
        }

        &:active, &.active {
          background-color: ${theme.ui.buttonGhostActiveBgColor};
          border-color: ${theme.ui.buttonGhostActiveBorderColor};
        }
      `;
    }
    return `
      border-color: ${theme.ui.buttonGhostDisabledBorderColor};
      color: ${theme.ui.buttonGhostDisabledBorderColor};
    `;
  }};
`;

const textButton = css<any>`
  background-color: transparent;
  border-color: transparent;
  color: ${({ theme }) => theme.ui.primaryColor};
  padding: 0;

  ${({ theme, disabled }) => {
    if (!disabled) {
      return `
        &:hover, &:focus {
          color: ${theme.ui.buttonPrimaryHoverBgColor};
        }

        &:active, &.active {
          color: ${theme.ui.buttonPrimaryActiveBgColor};
        }
      `;
    }
    return `
      color: ${theme.ui.buttonPrimaryDisabledBgColor};
    `;
  }};
`;

export const ButtonInner = styled.div`
  box-sizing: border-box;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  ${Ellipsis}
`;

export const ButtonChildrenWrap = styled.div`
  max-width: 100%;
  ${Ellipsis}
`;

export const ButtonWrap = withTheme(styled.div<any>`
  ${resetComponent}
  ${commonButton}
  ${({ disabled, theme }) => (disabled ? '' : `.weicon {color: ${theme.ui.commonActionIconColor};}`)}
  ${({ shape }) => shape === 'round' && roundButton}
  ${({ shape }) => shape === 'rect' && rectButton}
  ${({ shape }) => shape === 'circle' && circleButton}
  ${({ type }) => type === 'primary' && primaryButton}
  ${({ type }) => type === 'normal' && normalButton}
  ${({ type }) => type === 'danger' && dangerButton}
  ${({ type }) => type === 'ghost' && ghostButton}
  ${({ type }) => type === 'text' && textButton}
  ${({ bordered }) => !bordered && `
    border-color: transparent !important;
  `}
`);

export const SplitButtonWrap = withTheme(styled.div<any>`
  ${resetComponent}
  ${commonButton}
  ${({ disabled, theme }) => (disabled ? '' : `.weicon {color: ${theme.ui.commonActionIconColor};}`)}
  border: ${({ bordered, theme }) => (bordered ? `1px solid ${theme.ui.commonLineLightColor}` : 'none')};
  border-radius: ${({ theme }) => theme.ui.radius}};
  padding: 0;
  // margin-right: 4px;
  overflow: hidden;
  ${({ theme, disabled }) => {
    if (!disabled) {
      return `
        &:hover, &.active {
          background-color: ${theme.ui.splitButtonBgColor};
        }
      `;
    }
    return `
      cursor: not-allowed;
      background-color: transparent;
      color: ${theme.ui.disabledColor};

      // 提升此规则的优先级
      .arrow-icon.arrow-icon {
        color: ${theme.ui.disabledColor};
      }
    `;
  }};

  // splitButton 左侧已被选中（如选中单元格已被合并）
  &.leftSelected .splitbutton-left {
    background-color: ${({ theme }) => theme.ui.splitButtonLeftActiveBgColor};
  }

  &.leftSelected .splitbutton-right {
    background-color: ${({ theme }) => theme.ui.splitButtonBgColor};
  }

`);

export const SplitButtonInner = styled.div`
  box-sizing: border-box;
  height: 100%;
  display: flex;
  align-items: stretch;
  justify-content: center;
  ${Ellipsis}
`;

export const SplitButtonLeft = withTheme(styled.div<any>`
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 4px 0 4px;
  margin: 2px 0 2px 0;
  transition: background-color 0.1s ease-in-out;
  ${({ disabled, theme }) => {
    if (!disabled) {
      return `
          &:hover {
            padding-right: 3px;
            border-right: 1px solid ${theme.ui.commonLineLightColor};
          }
          &:active {
            background-color: ${theme.ui.splitButtonLeftActiveBgColor};
          }
        `;
    }
    return 'opacity:0.5';
  }}
`);

export const SplitButtonRight = withTheme(styled.div<any>`
  display: flex;
  align-items: center;
  justify-content: center;
  width: 10px;
  padding: 0 4px 0 1px;
  box-sizing: content-box;
  ${({ disabled, theme, active }) => {
    if (!disabled) {
      if (active) {
        return `background-color: ${theme.ui.splitButtonActiveBgColor};`;
      }
      return `
          &:hover {
            background-color: ${theme.ui.splitButtonHoverBgColor};
          }
        `;
    }
    return '';
  }}
  .arrow-icon {
    transform: ${({ active }) => (active ? 'rotate(180deg) scale(.67)' : 'rotate(0) scale(.67)')};
    font-size: 12px;
    color: ${({ theme }) => theme.ui.commonBaseColor3};
  }
`);
