/* eslint-disable max-lines-per-function */
import React, { useCallback } from 'react';
import useControlledValue from '~/hooks/useControlledValue';
import { Select, SelectOption } from '../select';
import { OptionWrap, TriggerWrap } from './styled';
import { TOptionTitle } from '../select/interface';
import { fontFamily, useFontFamily } from './hooks';

export interface IFontFamilySelectProps {
  defaultValue?: string;
  value?: string;
  locale?: string;
  onChange?: (e: string) => void;
  style?: React.CSSProperties;
  className?: string;
  zIndex?: number;
  bordered?: boolean;
  disabled?: boolean;
  /**
   * tooltip提示
   */
  title?: string | React.ReactNode;
  /**
   * 选项的tooltip提示
   */
  titles?: Record<string, TOptionTitle>;
}

const FontFamilySelect: React.FC<IFontFamilySelectProps> = (props) => {
  const { style, locale, onChange, zIndex, bordered, className, disabled, titles, title } = props;
  const [value, setValue] = useControlledValue(props.value, props.defaultValue);
  const [options, installedRecord, t] = useFontFamily(locale);

  // 该组件的trigger区域只显示Icon
  const renderTrigger = useCallback((align?: string) => {
    const index = options.findIndex((option:fontFamily) => option.value === align);
    const currentOption = options[index];
    const useDefault = props.defaultValue ? options[0] : undefined;
    return <TriggerWrap>{currentOption?.title || useDefault?.title || align}</TriggerWrap>;
  }, [options]);

  const handleChange = useCallback((e: string) => {
    if (onChange) {
      onChange(e);
    }
    setValue(e);
  }, [onChange, setValue]);

  return (
    <Select
      value={value}
      onChange={handleChange}
      renderTrigger={renderTrigger}
      optionWrapperStyle={{ minWidth: 200 }}
      style={style}
      zIndex={zIndex}
      bordered={bordered}
      className={className}
      disabled={disabled}
      testid="FontFamilySelect"
      title={title}
    >
      {
        options.map((option: fontFamily) => (
          <SelectOption
            value={option.value}
            key={option.value}
            disabled={!installedRecord[option.value]}
            disabledTip={t('we_ui_font_family_UninstallFonts')}
            testid={option.value}
            title={titles![option.value]}
          >
            <OptionWrap disabled={!installedRecord[option.value]}>
              <span
                style={{ fontFamily: option.value, color: 'inherit' }}
              >
                {option.title}
              </span>
            </OptionWrap>
          </SelectOption>
        ))
      }
    </Select>
  );
};

FontFamilySelect.defaultProps = {
  titles: {},
};

export default FontFamilySelect;
