import React, { useCallback, useContext, useEffect, useMemo } from 'react';
import { classnames } from '~/utils';
import { IMenuItemProps } from './interface';
import { MenuItemWrap, MenuItemInner, MenuItemIconWrap, MenuItemContentWrap } from './styled';
import { useController, useMenuKeyContext, useHotkeyController, useHotKeyScrollRef } from './hooks';
import { SelectNormal } from '../icons';
import { TextIndentContext, MenuKeyContext, PathContext } from './contexts';
import { MENU_VISIBLE_TOGGLE_DELAY } from './consts';
import { Tooltip } from '../tooltip';


// eslint-disable-next-line max-lines-per-function
export const MenuItem: React.FC<IMenuItemProps> = React.memo((props) => {
  const {
    children,
    disabled,
    disabledTip,
    style,
    selected,
    onClick,
    hoverable,
    height,
    testid,
    icon,
  } = props;
  // 接口
  const controller = useController('MenuItem');

  // 快捷键触达的位置key
  const hotkeyController = useHotkeyController('MenuItem');

  const {
    hotkeyPosition,
    isHotkeyType,
    mousePosition,
    registerValidNode,
    unRegisterValidNode,
  } = hotkeyController;

  // 被 Group 包裹需要缩进
  const textIndent = useContext(TextIndentContext);

  // menuKey处理
  const [menuKeyCtxValue, menuKey] = useMenuKeyContext(props.menuKey);

  // 父组件的key路径
  const parentPath = useContext(PathContext);

  // 支持快捷键时 菜单过长元素自动定位
  const elRef = useHotKeyScrollRef(menuKey, isHotkeyType, hotkeyPosition);

  // 是否选中，props.selected优先级最高
  const isSelected = typeof selected === 'boolean'
    ? selected
    : controller.isMenuItemSelected(menuKey);

  // 自动关闭，当前组件的props优先级更高
  const autoClose = typeof props.autoClose === 'boolean'
    ? props.autoClose
    : controller.autoClose;

  /**
   * 点击 Item
   *
   * @param {React.MouseEvent<HTMLElement>} event
   * @return {void}
   */
  const handleClick = useCallback((event: React.MouseEvent<HTMLElement>) => {
    // 自动关闭处理
    if (autoClose) {
      controller.closeAllMenu();
    }
    if (disabled) {
      return;
    }
    // 点击处理
    if (onClick) {
      onClick(menuKey, event);
    }
    // 选中处理
    controller.selectMenuItem(menuKey);
    if (controller.clickMenuItem) {
      controller.clickMenuItem(menuKey, event);
    }
  }, [disabled, autoClose, controller, onClick, menuKey]);

  /**
   * 如果有快捷键时新增的鼠标进入事件
   */
  const hotkeyMouseEnterHander = useCallback((event) => {
    if (!disabled && mousePosition !== event.clientY) {
      hotkeyController.setHotkeyType(false);
      hotkeyController.setMousePosition(event.clientY);
      hotkeyController.setHotkeyPosition(hotkeyController.getActionNode(menuKey));
    }
  }, [disabled, mousePosition, hotkeyController, menuKey]);

  /**
   * 鼠标移入 Item
   * 应该收起其他菜单
   *
   * @return {void}
   */
  const handleMouseEnter = useCallback((event) => {
    if (controller.shortcutKey) {
      hotkeyMouseEnterHander(event);
    }
    if (controller.showMenuTimer) {
      window.clearTimeout(controller.showMenuTimer);
    }
    controller.showMenuTimer = window.setTimeout(() => {
      controller.showSubMenu(parentPath);
    }, MENU_VISIBLE_TOGGLE_DELAY);
  }, [parentPath, controller, hotkeyMouseEnterHander]);

  // 整合height样式
  const styleCombined = useMemo(() => {
    const combined: React.CSSProperties = { ...(style || {}) };
    if (typeof height === 'number' || height === 'auto') {
      combined.height = height;
    }
    return combined;
  }, [height, style]);

  /**
  * 快捷键：注册有效节点
  */
  useEffect(() => {
    if (!disabled && controller.shortcutKey && elRef.current) {
      registerValidNode({ menuKey, path: `${parentPath ? `${parentPath}::` : ''}${menuKey}`, onClick: handleClick, parentPath });
      return () => {
        unRegisterValidNode({ menuKey, path: `${parentPath ? `${parentPath}::` : ''}${menuKey}` });
      };
    }
  }, [disabled, controller]);

  return (
    <Tooltip
      disabled={!(disabled && disabledTip)}
      title={disabledTip}
      placement={'top'}
    >
      <MenuItemWrap
        path={`${parentPath ? `${parentPath}::` : ''}${menuKey}`}
        menuId={controller.menuId}
        className={classnames('vertical', {
          selected: isSelected,
          disabled,
          hotkeyHover: hotkeyPosition &&
            menuKey === hotkeyPosition.menuKey &&
            !disabled && hoverable && controller.shortcutKey,
        })}
        ref={elRef}
        hoverable={hoverable}
        isHotkeyType={isHotkeyType}
        textIndent={textIndent}
        onClick={handleClick}
        style={styleCombined}
        onMouseEnter={handleMouseEnter}
        data-testid={testid ? `menu-item-${testid}` : undefined}
      >
        <SelectNormal
          className="icon selected-icon"
        />
        <MenuKeyContext.Provider value={menuKeyCtxValue}>
          {
            icon ? (
              <MenuItemInner className={classnames({ selected: isSelected })}>
                <MenuItemIconWrap>{icon}</MenuItemIconWrap>
                <MenuItemContentWrap>{children}</MenuItemContentWrap>
              </MenuItemInner>
            ) : children
          }
        </MenuKeyContext.Provider>
      </MenuItemWrap>
    </Tooltip>
  );
});

MenuItem.defaultProps = {
  disabled: false,
  hoverable: true,
};
