import styled from 'styled-components';
import { withTheme } from '../theme';

export const OverlayWrap = withTheme(styled.div`
  position: absolute;
  left: -9999px;
  top: -9999px;
  border: 1px solid ${({ theme }) => theme.ui.dropdownBorderColor};
  box-shadow: 1px 3px 8px 0 rgba(0,0,0,0.10);
  background-color: ${({ theme }) => theme.ui.colorGray1};
  border-radius: ${({ theme }) => theme.ui.radiusLarge};
  z-index: ${({ theme }) => theme.ui.zIndexDropdown};

  &.hidden {
    left: -9999px;
    top: -9999px;
  }
`);

export const OverlayMask = withTheme(styled.div`
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: ${({ theme }) => (theme.ui.zIndexDropdown - 1)};

  &.hidden {
    display: none;
  }
`);

export const OverlayBefore = styled.div`
  position: absolute;
  top: -2px;
  right: -2px;
  bottom: -2px;
  left: -2px;
  opacity: .0001;
  z-index: -1;
`;

