
import React from 'react';
import { Container, Cancel } from './styled';
import { useTranslate } from '~/locale/locales';
import { InputComponent } from '../input';

export interface SearchProps {
  /**
   * 国际化
   */

  local?: string;
  /**
   * 是否可见
   */
  visible: boolean;

  /**
   * 默认查找文本
   */
  text: string;

  /**
   * 类名
   */
  className?: string;

  /**
   * 执行搜索
   */
  search: (value: string) => void;

  /**
   * 取消查找
   */
  cancel: () => void;
}

export const Search: React.FC<SearchProps> = (props) => {
  const {
    text,
    search,
    cancel,
    visible,
    className,
  } = props;

  // 国际化
  const t = useTranslate(props.local);
  // 是否显示
  const style = { display: visible ? 'flex' : 'none' };

  return (
    <Container style={style} className={className}>
      <InputComponent text={text} onChange={search} placeholder={t('we_ui_find_and_replace_find')} />
      <Cancel onClick={cancel}>{t('we_ui_cancel_text')}</Cancel>
    </Container>
  );
};
