import React, { useCallback } from 'react';
import { MinusNormal, PlusNormal } from '~/components/icons';
import { useTranslate } from '~/locale/locales';
import useControlledValue from '~/hooks/useControlledValue';
import { Tooltip } from '../tooltip';
import { Select, SelectOption, SelectOptionGroup } from '../select';
import { KEY_CODES } from '~/utils/consts';
import { classnames } from '~/utils';
import { RatioSelectWrap, RatioSelectInner, Button, SelectWrap, TriggerSpanWrap } from './styled';
import { IPlacement } from '../trigger';

export interface IRatioSelectProps {
  /**
   * 国际化语言
   */
  locale?: string;
  /**
   * 可见性
   */
  visible?: boolean;
  /**
   * 默认的可见性
   */
  defaultVisible?: boolean;
  /**
   * 可见性变化回调
   */
  onVisibleChange?: (e: boolean) => void;
  /**
   * 变化的差值
   */
  step?: number;
  /**
   * 选中值
   */
  value?: number;
  /**
   * 默认的选中值
   */
  defaultValue?: number;
  /**
   * 值变化
   */
  onChange?: (e: number) => void;
  /**
   * 层级
   */
  zIndex?: number;
  /**
   * 出现的位置
   */
  placement?: IPlacement;
  /**
   * 样式
   */
  style?: React.CSSProperties;
  /**
   * 是否禁用
   */
  disabled?: boolean;
  /**
   * 自定义值
   */
  customedList?: Array<{text: string, value: number}>;
}

const RATIOS = [0.25, 0.5, 0.75, 1, 1.25, 1.5, 1.75, 2];

const triggerStyle: React.CSSProperties = {
  width: 36,
  textAlign: 'center',
  fontSize: 12,
};

// eslint-disable-next-line max-lines-per-function
export const RatioSelect: React.FC<IRatioSelectProps> = React.memo((props) => {
  const {
    locale,
    onVisibleChange,
    onChange,
    step,
    zIndex,
    placement,
    disabled,
    customedList,
  } = props;

  // 国际化文案
  const t = useTranslate(locale);
  const placeholderTitle = t('we_ui_ratio_select_placeholder');
  const zoomInTitle = t('we_ui_ratio_select_zoom_in');
  const zoomOutTitle = t('we_ui_ratio_select_zoom_out');
  const addText = t('we_ui_add_text');
  const reduceText = t('we_ui_reduce_text');

  // visible 受控
  const [visible, setVisible] = useControlledValue(props.visible, props.defaultVisible);

  // value 受控
  const [value, setValue] = useControlledValue(props.value, props.defaultValue);

  /**
   * 可见性变化
   */
  const handleVisibleChange = useCallback((e: boolean): void => {
    if (onVisibleChange) {
      onVisibleChange(e);
    }
    setVisible(e);
  }, [setVisible, onVisibleChange]);

  /**
   * 值发生变化
   */
  const handleValueChange = useCallback((e: number): void => {
    if (onChange) {
      onChange(e);
    }
    setValue(e);
  }, [onChange, setValue]);

  /**
   * 减少比例
   */
  const handleReduce = useCallback((): void => {
    if (disabled) return;
    // 浮点数精度问题消除
    const newValue = Math.round(value! * 100) - Math.round(step! * 100);
    if (newValue >= RATIOS[0] * 100) {
      handleValueChange(newValue / 100);
    }
  }, [value, handleValueChange, step]);

  /**
   * 增加比例
   */
  const handleAdd = useCallback((): void => {
    if (disabled) return;
    // 浮点数精度问题消除
    const newValue = Math.round(value! * 100) + Math.round(step! * 100);
    if (newValue <= RATIOS[RATIOS.length - 1] * 100) {
      handleValueChange(newValue / 100);
    }
  }, [value, handleValueChange, step]);

  /**
   * 如果是focus到减少按钮并回车
   */
  const handleMinusKeyDown = useCallback((e: React.KeyboardEvent<HTMLElement>) => {
    if (e.which === KEY_CODES.ENTER) {
      handleReduce();
    }
  }, [handleReduce]);

  /**
   * 如果是focus到增加按钮并回车
   */
  const handleAddKeyDown = useCallback((e: React.KeyboardEvent<HTMLElement>) => {
    if (e.which === KEY_CODES.ENTER) {
      handleAdd();
    }
  }, [handleAdd]);

  /**
   * 不是根据select来渲染
   */
  const renderRatio = useCallback((e: number | undefined) => (
    <TriggerSpanWrap>{Math.round(e! * 100)}%</TriggerSpanWrap>
  ), []);

  return (
    <RatioSelectWrap>
      <RatioSelectInner>
        <Tooltip title={zoomOutTitle}>
          <Button
            title={zoomOutTitle}
            tabIndex={0}
            onKeyDown={handleMinusKeyDown}
            onClick={handleReduce}
            className={classnames({ disabled: value === RATIOS[0] || disabled })}
            data-testid="RatioSelect_minus"
          >
            <MinusNormal />
          </Button>
        </Tooltip>
        <SelectWrap>
          <Select
            size="small"
            value={value}
            visible={visible}
            onChange={handleValueChange}
            renderTrigger={renderRatio}
            onVisibleChange={handleVisibleChange}
            zIndex={zIndex}
            style={triggerStyle}
            showArrow={false}
            placement={placement!}
            title={placeholderTitle}
            disabled={disabled}
            testid="RatioSelect"
          >
            <SelectOptionGroup>
              {
                RATIOS.map((ratio) => {
                  return (
                    <SelectOption key={`${ratio}`} value={ratio} testid={`RatioSelect_${ratio}`}>
                      {ratio * 100}%
                    </SelectOption>
                  );
                })
              }
            </SelectOptionGroup>
            {
              customedList ? (
                <SelectOptionGroup>
                  {
                    customedList.map((item) => {
                      return (
                        <SelectOption key={`${item.text}`} value={item.value} testid={`RatioSelect_${item.text}`}>
                          {item.text}
                        </SelectOption>
                      );
                    }) 
                  }
                </SelectOptionGroup>
              ) : <></>
            }
            <SelectOption
              disabled={value! >= RATIOS[RATIOS.length - 1]}
              onSelect={handleAdd}
              testid="RatioSelect_add"
            >
              {addText}{step! * 100}%
            </SelectOption>
            <SelectOption
              onSelect={handleReduce}
              disabled={value! <= RATIOS[0]}
              testid="RatioSelect_reduce"
            >
              {reduceText}{step! * 100}%
            </SelectOption>
          </Select>
        </SelectWrap>
        <Tooltip title={zoomInTitle}>
          <Button
            tabIndex={0}
            onKeyDown={handleAddKeyDown}
            onClick={handleAdd}
            className={classnames({ disabled: value === RATIOS[RATIOS.length - 1] || disabled })}
            data-testid="RatioSelect_plus"
          >
            <PlusNormal />
          </Button>
        </Tooltip>
      </RatioSelectInner>
    </RatioSelectWrap>
  );
});

RatioSelect.defaultProps = {
  defaultVisible: false,
  step: 0.05,
  defaultValue: 1,
  placement: 'top',
  disabled: false,
};
