/* eslint-disable @typescript-eslint/explicit-function-return-type */
import styled from 'styled-components';
import { withTheme } from '~/components/theme';
import { Placeholder, resetComponent } from '~/utils/styled.mixin';


export const InputWrap = withTheme(styled.label`
  position: relative;
  display: flex;
  align-items: center;
  width: 100%;
  height: ${({ theme }) => theme.ui.elementHeight};
  padding: ${({ theme }) => theme.ui.paddingTiny} ${({ theme }) => theme.ui.padding};
  color: ${({ theme }) => theme.ui.textColor};
  font-size: ${({ theme }) => theme.ui.fontSize};
  background-color: ${({ theme }) => theme.ui.colorGray1};
  border: 1px solid ${({ theme }) => theme.ui.commonLineLightColor};
  border-radius: ${({ theme }) => theme.ui.radius};
  box-sizing: border-box;
  cursor: text;

  * {
    box-sizing: border-box;
  }

  input {
    background-color: transparent;
  }

  &:hover, &.focused {
    border-color: ${({ theme }) => theme.ui.primaryColor};
  }

  &.disabled {
    background-color: ${({ theme }) => theme.ui.colorGray3};
    border-color: ${({ theme }) => theme.ui.colorGray4};
    cursor: not-allowed;
  }

  .clear-icon {
    cursor: pointer;
    color: ${({ theme }) => theme.ui.colorText2};
    transition: all .3s;
    &:hover {
      color: ${({ theme }) => theme.ui.colorText3};
    }
  }
`);

export const InputPrefix = withTheme(styled.span`
  margin-right: ${({ theme }) => theme.ui.marginSmall};
  color: ${({ theme }) => theme.ui.colorText2};
  display: flex;
  align-items: center;
`);

export const InputSuffix = withTheme(styled.span`
  margin-left: ${({ theme }) => theme.ui.marginSmall};
  color: ${({ theme }) => theme.ui.colorText3};
  display: flex;
  align-items: center;
`);

export const InputNode = withTheme(styled.input<any>`
  ${resetComponent}
  display: inline-block;
  width: 0;
  flex: 1 auto;
  margin: 0;
  padding: 0;
  outline: none;
  border: 0;
  ${({ theme, placeholderColor }) => Placeholder(placeholderColor || theme.ui.colorText2)}
`);
