import React, { Component } from 'react';
import ResizeObserver from 'resize-observer-polyfill';
import warning from 'warning';
import { Dropdown } from '~/components/dropdown';
import { Button } from '~/components/button';
import { MoreNormal } from '~/components/icons';
import { debounce } from '~/utils';
import { childrenType, ToolBox, EToolBoxLayout } from './ToolBox';
import { ToolGroup, HiddenToolGroup, ToolGroupArray } from './ToolGroup';
import { ToolsShow, ToolsMore, Content, ToolbarWrapper } from './styled';
import { TranslateProvider } from '~/locale/TranslateProvider';

export interface IDoubleLineToolbarProps {
  /**
   * 是否启用自动折叠功能
   */
  autoFolding?: boolean;
  /**
   * 包裹的组件
   */
  children?: ToolGroupArray;
  /**
   * toolbar wrapper 的行内样式
   */
  style?: React.CSSProperties;
  /**
   * 层级
   */
  zIndex?: number;
  /**
   * 对齐方式
   */
  alignType?: 'left' | 'center' | 'right';
  /**
   * 类名
   */
  className?: string;
  /**
   * 更多面板点击后自动关闭
   */
  autoCloseMorePanel?: boolean;
}

interface IDoubleLineToolbarState {
  // 从第hiddenPivot个slot开始隐藏
  hiddenPivot: number;
  groups: ToolGroupArray;
  hiddenGroups: ToolGroupArray;
  prevChildren: ToolGroupArray | null;
  // 是否需要重新技术算布局
  needCalculating: boolean;
  morePanelVisible: boolean;
}

const INIT_PIVOT = -1;

/**
 * 获取初始的自组件(包含首次渲染和children变化的情况), 重新布局
 * @param children
 */
const getChildren = (children: childrenType) => {
  const groups: ToolGroupArray = [];
  const hiddenGroups: ToolGroupArray = [];
  let hasIllegalChild = false;

  const handleChild = (child: React.ReactElement) => {
    if (!child) return;
    if (child.type === ToolGroup) {
      groups.push(child);
    } else if (child.type === HiddenToolGroup) {
      hiddenGroups.push(child);
    } else if (child.type === React.Fragment) {
      React.Children.forEach(child.props.children, handleChild);
    } else {
      hasIllegalChild = true;
    }
  };
  React.Children.forEach(children as React.ReactElement, handleChild);

  warning(!hasIllegalChild, 'Children of DoubleLineToolbar must be ToolGroup or HiddenToolGroup');

  if (!((groups.length || hiddenGroups.length) > 0)) {
    throw new Error('DoubleLineToolbar should has more than one child which type is ToolGroup or HiddenToolGroup');
  }
  return { groups, hiddenGroups };
};

const toolsMoreOverlayStyle: React.CSSProperties = {
  padding: '0 10px',
};

export class DoubleLineToolbar extends Component<IDoubleLineToolbarProps, IDoubleLineToolbarState> {
  // eslint-disable-next-line react/static-property-placement
  static defaultProps = {
    autoFolding: true,
    alignType: 'center',
  };

  resizeObserver: ResizeObserver;

  // Slot的宽度缓存
  slotsWidthList: number[] = [];

  refToolbar = React.createRef<HTMLElement>();

  refToolContent = React.createRef<HTMLElement>();

  refShow = React.createRef<HTMLDivElement>();

  refMore = React.createRef<HTMLElement>();

  recomputeLayout = debounce(() => {
    this.calculateWidth(false);
  }, 50);

  constructor(props: IDoubleLineToolbarProps) {
    super(props);
    this.state = {
      hiddenPivot: INIT_PIVOT,
      groups: [],
      hiddenGroups: [],
      // eslint-disable-next-line react/no-unused-state
      prevChildren: null, // 用于状态比对
      needCalculating: false,
      morePanelVisible: false,
    };
    this.resizeObserver = new ResizeObserver(this.handleResize);
  }

  static getDerivedStateFromProps = (
    props: IDoubleLineToolbarProps, state: IDoubleLineToolbarState,
  ) => {
    const { prevChildren } = state;

    const { children, autoFolding } = props;
    const { groups, hiddenGroups } = getChildren(children!);

    if (!autoFolding) {
      return {
        groups,
        hiddenGroups,
        prevChildren: children,
        hiddenPivot: INIT_PIVOT,
        needCalculating: false,
      };
    }
    /**
     * 分两种情况
     * 1: 布局变换, 则重新计算布局
     * 2: 布局没变化, 但是children变换(children的props变化), 需要重新渲染
     */
    if (children !== prevChildren) {
      // TODO 如果对应自组件长度变化, 则认为布局变化
      if (groups.length !== state.groups.length
        || hiddenGroups.length !== state.hiddenGroups.length) {
        return {
          groups,
          hiddenGroups,
          prevChildren: children,
          hiddenPivot: INIT_PIVOT,
          needCalculating: true,
        };
      }
      // 布局未变化, 则沿用上次计算结果
      return {
        groups,
        hiddenGroups,
        prevChildren: children,
        needCalculating: false,
      };
    }

    return null;
  };

  componentDidMount() {
    const { autoFolding } = this.props;
    if (!autoFolding) return;

    setTimeout(() => this.calculateWidth(true));
    if (this.refToolContent.current && this.resizeObserver) {
      this.resizeObserver.observe(this.refToolContent.current);
    }
    if (this.refShow.current) {
      this.refShow.current.addEventListener('mousedown', this.mouseDownAtShow);
    }
  }

  componentDidUpdate(prevProps: IDoubleLineToolbarProps) {
    const { needCalculating } = this.state;
    if (prevProps.children !== this.props.children && needCalculating) {
      this.calculateWidth(true);
    }
  }

  componentWillUnmount() {
    const { autoFolding } = this.props;
    if (!autoFolding) return;

    if (this.resizeObserver) {
      this.resizeObserver.disconnect();
    }
    if (this.refShow.current) {
      this.refShow.current.removeEventListener('mousedown', this.mouseDownAtShow);
    }
  }

  mouseDownAtShow = (e: MouseEvent) => {
    const whiteTags = ['input', 'textarea'];
    const tagName = ((e.target as HTMLElement).tagName || '').toLowerCase();
    if (!whiteTags.includes(tagName)) {
      e.preventDefault();
    }
  };

  /**
   * 重新计算宽度来布局
   */
  calculateWidth = (isFirst = false) => {
    if (!this.refShow.current || !this.refShow.current.children) return;
    const { children } = this.refShow.current;
    const parentElement = this.refShow.current.parentElement as HTMLElement;
    const { offsetWidth: containerWidth } = parentElement;

    let pivot = INIT_PIVOT;
    // 更多(...)的宽度
    let sum = 32;
    if (this.refMore.current) {
      sum = this.refMore.current.offsetWidth;
    }

    // 非第一次计算(如resize)，从缓存中取已计算好的数据
    if (!isFirst && this.slotsWidthList && this.slotsWidthList.length > 0) {
      for (let index = 0; index < this.slotsWidthList.length; index += 1) {
        sum += this.slotsWidthList[index];
        if (sum > containerWidth) {
          pivot = index;
          break;
        }
      }
      this.setState({
        hiddenPivot: pivot,
      });
      return;
    }
    // 第一渲染或children发生变化，重新计算宽度
    for (let index = 0; index < children.length; index += 1) {
      const child = children[index] as HTMLElement;
      if (isFirst) {
        this.slotsWidthList.push(child.offsetWidth);
      }
      sum += child.offsetWidth;
      if (sum > containerWidth && pivot <= INIT_PIVOT) {
        pivot = index;
      }
    }
    this.setState({
      hiddenPivot: pivot,
    });
  };

  getResizeContainer = (): HTMLElement => this.refToolbar.current as HTMLElement;

  /**
 * resize回调事件
 */
  handleResize = (): void => {
    this.recomputeLayout();
  };

  handleClickMorePanel = (e: React.MouseEvent) => {
    const { autoCloseMorePanel } = this.props;
    // 阻止冒泡
    e.stopPropagation();
    if (autoCloseMorePanel) {
      this.setState({ morePanelVisible: false });
    }
  };

  handleMorePanelVisibleChange = (e: boolean) => {
    this.setState({ morePanelVisible: e });
  };

  // render hidden slots
  renderMore(hiddenSlots: React.ReactNodeArray): React.ReactNode {
    const { zIndex } = this.props;
    const { morePanelVisible } = this.state;

    if (hiddenSlots.length === 0) {
      return null;
    }

    const DropContent = (
      <ToolsShow onClick={this.handleClickMorePanel}>
        {hiddenSlots}
      </ToolsShow>
    );

    return (
      <ToolsMore ref={this.refMore} isSingleLine={false}>
        <TranslateProvider>
          {(t) => (
            <Dropdown
              trigger="click"
              overlay={DropContent}
              placement="bottomRight"
              zIndex={zIndex}
              overlayStyle={toolsMoreOverlayStyle}
              visible={morePanelVisible}
              onVisibleChange={this.handleMorePanelVisibleChange}
            >
              <ToolBox
                layout={EToolBoxLayout.Vertical}
                name={t('we_ui_double_line_toolbar_more')}
              >
                <Button
                  bordered={false}
                  shape="rect"
                  icon={<MoreNormal />}
                />
              </ToolBox>
            </Dropdown>
          )}
        </TranslateProvider>
      </ToolsMore>
    );
  }

  render() {
    const { style, autoFolding, alignType, className } = this.props;
    const { hiddenPivot, hiddenGroups, groups } = this.state;

    let computedShowSlots = groups;
    let computedHiddenSlots = hiddenGroups;
    // 如果为初始值, 说明没有计算, 直接略过
    if (hiddenPivot > INIT_PIVOT) {
      computedHiddenSlots = hiddenGroups.concat(computedShowSlots.slice(hiddenPivot));
      computedShowSlots = computedShowSlots.slice(0, hiddenPivot);
    }

    const styles: React.CSSProperties = {
      width: autoFolding ? '0' : '100%',
    };

    return (
      <ToolbarWrapper
        style={{ ...style }}
        ref={this.refToolbar}
        className={className}
      >
        <Content ref={this.refToolContent} style={styles} className={alignType} >
          <ToolsShow ref={this.refShow}>{computedShowSlots}</ToolsShow>
          {this.renderMore(computedHiddenSlots)}
        </Content>
      </ToolbarWrapper>
    );
  }
}
