/* eslint-disable max-lines-per-function */
import React from 'react';
import tinycolor from 'tinycolor2';
import { classnames, isWhite, isTransparent } from '~/utils';
import {
  ColorBoxItem,
  ClearItem,
  ColorBlock,
} from './styled';

interface IColorItemProps {
  /**
   * 当前颜色值
   */
  colorValue: string;
  /**
   * 清楚色
   */
  clearColor?: string;
  /**
   * 索引
   */
  index: number;
  /**
   * 当前色块是否选中
   */
  active: boolean;
  /**
   * 颜色选中的回调
   */
  onColorPick: (color: string) => void;
  /**
   * 色块hover出现的字
   */
  colorBlockText?: string;
}

// 勾选的填充色缓存
const fillColorCache: { [color: string]: string } = {}; // for:性能优化

export interface IColorResult {
  // 颜色的16进展表示
  hex: string;
  // hsl颜色空间
  hsl: { h: number; s: number; l: number; a?: number };
  // rgba颜色空间
  rgb: { r: number; g: number; b: number; a?: number };
}

// 颜色对象（包含多种颜色空间的类型）
export interface IColorObj extends IColorResult {
  // hsv颜色空间
  hsv: { h: number; s: number; v: number };
  // 原始颜色值（转换之前的数据）
  source: string;
}

// 渲染每个色块
export const ColorBlockItem: React.FC<IColorItemProps> = React.memo((props) => {
  const { colorBlockText } = props;
  /**
   * 将颜色值转换成颜色对象
   */
  const toColorObj = (colorStr: string, oldHue?: number): IColorObj => {
    const tinyColor = tinycolor(colorStr);
    const hsl = tinyColor.toHsl();
    const hsv = tinyColor.toHsv();
    const rgb = tinyColor.toRgb();
    const hex = tinyColor.toHex();
    if (hsl.s === 0) {
      hsl.h = oldHue || 0;
      hsv.h = oldHue || 0;
    }
    const transparent = (hex === '000000' && rgb.a === 0);

    return {
      hsl,
      hex: transparent ? 'transparent' : `#${hex}`,
      rgb,
      hsv,
      source: colorStr,
    };
  };

  /**
   * 获取对比色，即：色块选中时打勾的颜色，要和色块本身的背景色形成反差
   * @param color 字符串表示的颜色值
   */
  const getContrastingColor = (color: string):string => {
    if (!color) {
      return '#FFFFFF';
    }

    let result = fillColorCache[color];
    if (result) {
      return result;
    }

    const colorObj: IColorObj = toColorObj(color);
    if (colorObj.hex === 'transparent') {
      result = 'rgba(0,0,0,0.4)';
    } else {
      const yiq = (colorObj.rgb.r * 299 + colorObj.rgb.g * 587 + colorObj.rgb.b * 114) / 1000;
      result = yiq >= 210 ? '#8C8C8C' : '#FFFFFF';
    }

    fillColorCache[color] = result;
    return result;
  };

  const { colorValue, clearColor, index, active, onColorPick } = props;
  const [isHover, setHover] = React.useState<boolean>(false);
  const key = `${index}-${colorValue}`;

  // 是否为清空色块，加上红色斜杠
  const isClear = typeof clearColor === 'string'
    ? clearColor.toLocaleLowerCase() === colorValue.toLocaleLowerCase()
    : false;
  if (isClear) {
    return (
      <ClearItem
        key={key}
        onClick={() => onColorPick(colorValue)}
        onMouseEnter={() => setHover(true)}
        onMouseLeave={() => setHover(false)}
        className={classnames({ isHover })}
      >
        <ColorBlock
          color={colorValue}
          border
          className={classnames({ isHover })}
        />
      </ClearItem>
    );
  }

  // 颜色值为FAFAFA或FFFFFF时， 需要设置边框
  // const border = ['#FAFAFA', '#FFFFFF'].includes(colorValue); // Todo：通过计算来设置
  const border = isWhite(colorValue) || isTransparent(colorValue);

  // 选中的样式
  const checkStyle = {
    fill: active ? getContrastingColor(colorValue) : '#fff',
    display: active ? 'block' : 'none',
  };

  return (
    <ColorBoxItem
      key={key}
      onClick={() => onColorPick(colorValue)}
      onMouseEnter={() => setHover(true)}
      onMouseLeave={() => setHover(false)}
    >
      <ColorBlock
        color={colorValue}
        border={border || isClear}
        className={classnames({ isHover })}
      >
        <svg style={checkStyle} viewBox="0 0 18 18">
          <path d="M21,7L9,19L3.5,13.5L4.91,12.09L9,16.17L19.59,5.59L21,7Z" />
        </svg>
        {
          colorBlockText && !active && isHover && (
            <span className="colorBlockText" style={{ color: getContrastingColor(colorValue) }}>
              {colorBlockText}
            </span>
          )
        }
      </ColorBlock>
    </ColorBoxItem>
  );
});
