export interface ITableStyleProps {
  /**
   * 尺寸
   */
  size?: 'normal' | 'small';
  /**
   * 变化时回调函数
   */
  onChange?: (e: string) => void;
  /**
   * TableStyle容器类名
   */
  className?: string;
  /**
   * ID
   */
  id?: string;
  /**
   * Switch容器样式
   */
  style?: React.CSSProperties;
  /**
 * 受控模式
 */
  value?: string;
  /**
 * 传值为非受控模式
 */
  defaultValue?: string;
  /**
* 传值为每页的样式数目
*/
  pageSize?: number;
  /**
* 传值为菜单页的每行的样式数目
*/
  menuPageSize?: number;
  /**
* 样式图片的配置
*/
  styleList?: Array<{ name: string, url: string }>;
}

function rotateArrow(angle: number): React.CSSProperties {
  return { flex: 'none', transform: `rotate(${angle}deg)`, width: 8, height: 8 };
}
