function convert2D(arr: Array<{ name: string, url: string }>, pageSize: number) {
  const res = [];
  for (let i = 0; i < arr.length; i += pageSize) {
    const temp = arr.slice(i, i + pageSize);
    res.push(temp);
  }
  return res;
}
let currentImgPadding = imgPadding;
const TableStyle: React.FC<ITableStyleProps> = React.memo(((props: ITableStyleProps) => {
  const {
    onChange,
    size = 'normal',
    styleList = STYLE_LIST,
    pageSize = 4,
    menuPageSize = 7,
  } = props;
  currentImgPadding = size === 'normal' ? imgPadding : imgPadding - 1;
  const currentMode = size === 'small' ? smallMode : normalMode;
  const wrapperWidth = (btnWidth * 2) + (currentMode.upper + currentImgPadding * 2 + imgMargin * 2 + imgBorder * 2) * pageSize;
  styleList2D = useMemo(
    () => {
      return convert2D(styleList, pageSize);
    },
    [pageSize, styleList],
  );
  menuStyleList2D = useMemo(
    () => {
      return convert2D(styleList, menuPageSize);
    },
    [menuPageSize, styleList],
  );
  const maxPage = styleList2D.length - 1;
  const [value, setValue] = useControlledValue(props.value, (props.defaultValue || styleList[0].name));

  const handleClick = function (item: string) {
    if (item !== value && onChange) {
      onChange(item);
    }
    setValue(item);
    setOverlayVisible(false);
    const idx = styleList.findIndex((ele) => {
      return ele.name === item;
    });
    const rowIndex = Math.floor(idx / pageSize);
    if (rowIndex !== undefined) {
      switchPage(rowIndex);
    }
  };
  const [currentTab, setCurrentTab] = useState(0);
  const [showArrowDown, setShowArrowDown] = useState(false);
  const [overlayVisible, setOverlayVisible] = useState(false);
  const switchPage = (nextPage: number) => {
    if (nextPage >= 0 && nextPage <= maxPage) {
      setCurrentTab(nextPage);
    }
  };
  const getOverlay = () => {
    return (
      <OverlayWrapper>
        {
          menuStyleList2D.map((row, rIndex) => {
            return (
              <TableStyleSet key={`footer-${rIndex}`}>
                {
                  (row.map((item, cIndex) => {
                    return (
                      <TableStyleItemWrap key={`footer-${item.name}`} onClick={() => handleClick(item.name)} footer mode={currentMode} className={item.name === value ? 'actived' : ''}>
                        <TableStyleItem src={menuStyleList2D[rIndex][cIndex].url} />
                      </TableStyleItemWrap>
                    );
                  }))
                }
              </TableStyleSet>
            );
          })
        }
      </OverlayWrapper>
    );
  };
  return (
    <TableStyleWrapper
      onMouseEnter={() => setShowArrowDown(true)}
      onMouseLeave={() => setShowArrowDown(false)}
      width={wrapperWidth}
      mode={size}
    >
      <TableStyleUpper>
        <IconWrapper onClick={() => switchPage(currentTab - 1)} data-testid="arrow-increase" >
          <ArrowUpTriangleNormal style={rotateArrow(-90)} className={classnames({ disabled: currentTab === 0 })} />
        </IconWrapper>
        <Tabs activeKey={String(currentTab)} hasTabBar={false} style={{ flex: 1 }}>
          {
            styleList2D.map((row, rowIndex) => {
              return (
                <TabPane key={String(rowIndex)} tab={String(rowIndex)} >
                  <TableStyleSet>
                    {
                      (row.map((item, colIndex) => {
                        return (
                          <TableStyleItemWrap key={item.name} onClick={() => handleClick(item.name)} mode={currentMode} size={size} className={item.name === value ? 'actived' : ''}>
                            <TableStyleItem src={styleList2D[rowIndex][colIndex].url} />
                          </TableStyleItemWrap>
                        );
                      }))
                    }
                  </TableStyleSet>
                </TabPane>
              );
            })
          }
        </Tabs>
        <IconWrapper onClick={() => switchPage(currentTab + 1)} >
          <ArrowUpTriangleNormal style={rotateArrow(90)} className={classnames({ disabled: currentTab === maxPage })} />
        </IconWrapper>
      </TableStyleUpper>
      <TableStyleDown>
        <Dropdown overlay={getOverlay()} onVisibleChange={vi => setOverlayVisible(vi)} placement="bottom" visible={overlayVisible}>

          <IconWrapper isDown onClick={() => setOverlayVisible(!overlayVisible)} width="26px" active={overlayVisible}>
            <ArrowDownTriangleNormal style={(showArrowDown || overlayVisible) ? { width: 8, height: 8 } : { visibility: 'hidden', width: 8, height: 8 }} />
          </IconWrapper>
        </Dropdown>
      </TableStyleDown>
    </TableStyleWrapper>
  );
}));

TableStyle.displayName = 'TableStyle';

TableStyle.defaultProps = {
  pageSize: 4,
  size: 'normal',
  menuPageSize: 7,
};
export { TableStyle };
