import React, { useMemo, useCallback, useRef, PropsWithChildren } from 'react';
import { ArrowDownTriangleNormal } from '~/components/icons';
import { Dropdown } from '~/components/dropdown';
import { classnames } from '~/utils';
import { KEY_CODES } from '~/utils/consts';
import { useTranslate } from '~/locale/locales';
import useKeyboards, { IKeyboardPair } from '~/hooks/useKeyboards';
import { ISelectValue, ISelectProps, IState, IClickOptionEvent } from './interface';
import { SelectOption } from './SelectOption';
import { SelectOptionGroup } from './SelectOptionGroup';
import { SelectWrap, TriggerWrap, TriggerContent, EmptyWrap, OverlayWrap } from './styled';
import { SelectButton } from './SelectButton';
import { createSelectContext } from './SelectContext';
import { useFlush, useControlledValue } from '~/hooks';
import { isSelectOption, isSelectOptionGroup } from './utils';
import { IInputHandle } from '../input/Input';

// eslint-disable-next-line max-lines-per-function
const Select = <VT extends ISelectValue = ISelectValue>(
  props: PropsWithChildren<ISelectProps<VT>>,
  ref: React.RefObject<HTMLDivElement>,
) => {
  const {
    children,
    getContainer,
    style,
    className,
    overlayStyle,
    renderTrigger,
    bordered,
    onChange,
    disabled,
    onVisibleChange,
    optionWrapperStyle,
    selectable,
    locale,
    size,
    zIndex,
    showArrow,
    placement,
    overlayClassName,
    mode,
    rules,
    autoClose,
    testid,
    title,
    errored,
    ...restProps
  } = props;
  // value 受控
  const [value, setValue] = useControlledValue(props.value, props.defaultValue);
  // visible 受控
  const [visible, setVisible] = useControlledValue(props.visible, props.defaultVisible);

  // 初始化的时候收集一次 SelectOption 节点，选中的节点
  const OptionChildrenRecord = useMemo(() => {
    const result = new Map<VT, React.ReactNode>();
    function iterator(nodes: React.ReactElement[]) {
      React.Children.forEach(nodes, (node) => {
        if (isSelectOptionGroup(node)) {
          iterator(node.props.children);
        } else if (isSelectOption(node)) {
          result.set(node.props.value, node.props.children);
        }
      });
    }
    iterator(children);
    return result;
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  // 状态
  const state = useRef<IState<VT>>({
    OptionChildrenRecord,
    OptionDisabledRecord: new Map<VT, boolean>(),
    keyword: '',
    showOptionValues: [],
    isInputFocused: false,
    keyboardAt: null,
  }).current;

  // 是否正在搜索
  const isSearching = state.keyword.length > 0 && (mode === 'search');

  // Input 实例
  const inputRef = useRef<IInputHandle>(null);

  // 强制刷新函数
  const flush = useFlush();

  // 国际化
  const t = useTranslate(locale);

  /**
   * 可见性变化处理函数
   *
   * @param {boolean} e 可见性
   * @return {void}
   */
  const handleVisibleChange = useCallback((e: boolean) => {
    if (e !== visible) {
      if (onVisibleChange) {
        onVisibleChange(e);
      }
      setVisible(e);
    }
    // 下拉选择消失时，手动触发blur事件
    if (!e && inputRef && inputRef.current) {
      inputRef.current.blur();
    }
  }, [visible, onVisibleChange, setVisible]);

  /**
   * 值变化处理函数
   *
   * @param {ValueType} e 变化值
   * @return {void}
   */
  const handleValueChange = useCallback((e: VT): void => {
    if (onChange && (e !== value || !selectable)) {
      let error;
      for (const rule of (rules || [])) {
        if (typeof rule !== 'object') return;
        const { validator } = rule;
        if (typeof validator === 'function' && !validator(e)) {
          error = { violatedRule: rule, prevVal: value };
          break;
        }
      }

      onChange(e, error);
    }

    if (e !== value) {
      setValue(e);
    }
  }, [value, selectable, onChange, setValue, rules]);

  /**
   * 点击 Option 选项
   *
   * @param {IClickOptionEvent<VT>} event { value, disabled }
   * @return {void}
   */
  const handleClickOption = useCallback((event: IClickOptionEvent<VT>): void => {
    if (event.disabled) {
      if (autoClose) {
        handleVisibleChange(false);
      }
      return;
    }
    // 如果 selectable 为 false，那么一直可以触发 onChange
    if (event.value !== undefined && (!selectable || value !== event.value)) {
      handleValueChange(event.value);
    }
    if (autoClose) {
      handleVisibleChange(false);
    }
  }, [value, selectable, handleVisibleChange, handleValueChange, autoClose]);

  /**
   * Option 被移除
   *
   * @param {ValueType} key
   * @return {void}
   */
  const handleRemoveOption = useCallback((key: VT): void => {
    state.OptionChildrenRecord.delete(key);
    flush();
  }, [state, flush]);

  /**
   * Option 被添加或者被更新
   *
   * @param {ValueType} key
   * @return {void}
   */
  const handleUpdateOption = useCallback((key: VT, newChildren: React.ReactNode): void => {
    const oldChildren = state.OptionChildrenRecord.get(key);
    if (oldChildren !== newChildren) {
      state.OptionChildrenRecord.set(key, newChildren);
      flush();
    }
  }, [state, flush]);

  /**
   * Option disabled更新
   *
   * @param {ValueType} key
   * @param {boolean}
   * @return {void}
   */
  const handleCacheOptionDisabled = useCallback((key: VT, newDisabled: boolean): void => {
    state.OptionDisabledRecord.set(key, newDisabled);
  }, [state]);

  /**
   * Option disabled删除
   *
   * @param {ValueType} key
   * @return {void}
   */
  const handleUncacheOptionDisabled = useCallback((key: VT): void => {
    state.OptionDisabledRecord.delete(key);
  }, [state]);

  /**
   * 点开菜单输入框聚焦的时候，点击我们不希望生效，不然会收起弹层
   *
   * @return {void}
   */
  const handleClickTrigger = useCallback(() => {
    if ((visible && state.isInputFocused) || disabled) {
      return false;
    }
  }, [visible, state.isInputFocused, disabled]);

  /**
   * 当展开菜单的时候，点击trigger我们不希望Input失焦
   */
  const handleMouseDownTrigger = useCallback((e: React.MouseEvent<HTMLElement>) => {
    if (visible && state.isInputFocused) {
      e.preventDefault();
    }
  }, [visible, state.isInputFocused]);

  /**
   * 输入框失焦
   *
   * @param {React.FocusEvent<HTMLInputElement>} e
   * @return {void}
   */
  const handleInputBlur = useCallback((e: React.FocusEvent<HTMLInputElement>): void => {
    const inputValue = (state.keyword || e.target.value).trim();
    state.keyword = '';
    state.showOptionValues = [];
    state.isInputFocused = false;
    const values = Array.from(state.OptionChildrenRecord.keys());
    if (mode === 'search') {
      const index = values.findIndex(v => `${v}` === inputValue);
      if (index !== -1) {
        handleValueChange(values[index]);
      }
    } else if (mode === 'input') {
      // input 模式的 ValueType 只支持 string | number
      let processedInputValue: ISelectValue;
      if (typeof values[0] === 'number') {
        processedInputValue = Number(inputValue);
      } else {
        processedInputValue = inputValue;
      }
      handleValueChange(processedInputValue as VT);
    }

    handleVisibleChange(false);
  }, [state, handleValueChange, handleVisibleChange, mode]);

  /**
   * 输入框聚焦
   *
   * @return {void}
   */
  const handleInputFocus = useCallback((): void => {
    state.keyword = String(value);
    state.isInputFocused = true;
    state.showOptionValues = Array.from(state.OptionChildrenRecord.keys());
    flush();
    if (inputRef.current) {
      inputRef.current.select();
    }
  }, [state, flush, value]);

  /**
   * 输入框回车
   *
   * @return {void}
   */
  const handleInputEnter = useCallback((): void => {
    if (state.keyboardAt === null && inputRef.current) {
      inputRef.current.blur();
    }
  }, [state]);

  /**
   * 输入值变化
   *
   * @param {React.ChangeEvent<HTMLInputElement>}
   * @return {void}
   */
  const handleInputChange = useCallback((e: React.ChangeEvent<HTMLInputElement>): void => {
    state.keyword = e.target.value;
    state.keyboardAt = null;
    state.showOptionValues = Array.from(state.OptionChildrenRecord.keys()).filter((v) => {
      return `${v}`.includes(e.target.value.trim());
    });
    flush();
  }, [state, flush]);

  /**
   * 选项高亮移动
   *
   * @param {1 | -1} step
   * @return {void}
   */
  const handleChangeKeyboardAt = useCallback((step: 1 | -1) => {
    const values = isSearching
      ? state.showOptionValues
      : Array.from(state.OptionChildrenRecord.keys());
    let index = state.keyboardAt ? values.indexOf(state.keyboardAt) : -1;
    // 需要跳过disabled的选项
    do {
      index += step;
    } while (
      index < values.length
      && !!state.OptionDisabledRecord.get(values[index])
    );
    if (index >= 0 && index <= values.length - 1) {
      if (values[index] !== state.keyboardAt) {
        state.keyboardAt = values[index];
        flush();
      }
    }
  }, [state, flush, isSearching]);

  /**
   * 按下向下键，聚焦时触发选项高亮
   *
   * @return {void}
   */
  const handlePressDown = useCallback((): void => {
    handleChangeKeyboardAt(1);
  }, [handleChangeKeyboardAt]);

  /**
   * 按下向上键，聚焦时触发选项高亮
   *
   * @return {void}
   */
  const handlePressUp = useCallback((): void => {
    handleChangeKeyboardAt(-1);
  }, [handleChangeKeyboardAt]);

  /**
   * 按下Enter键，选择高亮选项
   */
  const handlePressEnter = useCallback((): void => {
    if (state.keyboardAt !== null) {
      handleValueChange(state.keyboardAt);
    }
    handleVisibleChange(false);
  }, [handleValueChange, handleVisibleChange, state.keyboardAt]);


  // 键盘事件定义
  const keyboards: IKeyboardPair[] = useMemo(() => {
    return [
      [[KEY_CODES.DOWN], handlePressDown],
      [[KEY_CODES.UP], handlePressUp],
      [[KEY_CODES.ENTER], handlePressEnter],
    ];
  }, [handlePressDown, handlePressUp, handlePressEnter]);

  // 绑定键盘事件
  const keyboardHandlers = useKeyboards({ keyboards, on: visible });

  // context
  const context = useMemo(() => {
    return {
      value: value!,
      selectable: !!selectable,
      showOptionValues: state.showOptionValues,
      isSearching,
      keyboardAt: state.keyboardAt,
      optionWrapperStyle,
      onClickOption: handleClickOption,
      onUpdateOption: handleUpdateOption,
      onRemoveOption: handleRemoveOption,
      onCacheOptionDisabled: handleCacheOptionDisabled,
      onUncacheOptionDisabled: handleUncacheOptionDisabled,
    };
  }, [
    value,
    selectable,
    isSearching,
    state.keyboardAt,
    state.showOptionValues,
    optionWrapperStyle,
    handleClickOption,
    handleUpdateOption,
    handleRemoveOption,
    handleCacheOptionDisabled,
    handleUncacheOptionDisabled,
  ]);

  // 获取选中的 Option 的子节点
  let SelectedOptionChildren = null;
  if (value !== null && value !== undefined) {
    SelectedOptionChildren = state.OptionChildrenRecord.get(value) || null;
  }

  const SelectContext = createSelectContext<VT>();
  const searchNoMatch = isSearching && state.showOptionValues.length === 0;

  return (
    <SelectContext.Provider value={context}>
      <Dropdown
        title={title}
        trigger="click"
        visible={visible}
        onVisibleChange={handleVisibleChange}
        getContainer={getContainer}
        overlayStyle={overlayStyle}
        overlayClassName={overlayClassName}
        zIndex={zIndex}
        placement={placement}
        testid={testid}
        overlay={(
          <OverlayWrap data-testid={testid && `overlay-${testid}`}>
            <div
              style={{
                display: searchNoMatch ? 'none' : 'block',
              }}
            >
              {children}
            </div>
            {
              searchNoMatch && (
                <EmptyWrap>
                  <span className="message">{t('we_ui_search_no_data')}</span>
                </EmptyWrap>
              )
            }
          </OverlayWrap>
        )}
      >
        <SelectWrap
          showArrow={showArrow}
          ref={ref}
          tabIndex={disabled ? undefined : '0'}
          style={style}
          className={classnames(className, size, {
            bordered,
            focused: state.isInputFocused,
            disabled,
            actived: visible,
            errored: !disabled && errored,
          })}
          {...keyboardHandlers}
          {...restProps}
          onClick={handleClickTrigger}
          onMouseDown={handleMouseDownTrigger}
          data-testid={testid ? `SelectWrap-${testid}` : undefined}
        >
          <TriggerWrap>
            <TriggerContent>
              <SelectButton<VT>
                value={value}
                locale={locale}
                renderTrigger={renderTrigger}
                mode={mode}
                visible={visible}
                isInputFocused={state.isInputFocused}
                keyword={state.keyword}
                onBlur={handleInputBlur}
                onFocus={handleInputFocus}
                onEnter={handleInputEnter}
                onChange={handleInputChange}
                inputRef={inputRef}
                testid={testid}
              >
                {(SelectedOptionChildren || (<span>{value}</span>)) as React.ReactNode}
              </SelectButton>
            </TriggerContent>
            <ArrowDownTriangleNormal
              data-testid={testid && `select-arrow-${testid}`}
              className={classnames('select-arrow', {
                opened: visible,
                hidden: !showArrow,
              })}
            />
          </TriggerWrap>
        </SelectWrap>
      </Dropdown>
    </SelectContext.Provider>
  );
};

// 为了 hack VT的传递、Select.Option、Select.OptionGroup的定义
const SelectMemo = (
  React.memo(React.forwardRef(Select as any)) as unknown
) as (
  typeof Select & {
    Option: typeof SelectOption,
    OptionGroup: typeof SelectOptionGroup,
  }
);

(SelectMemo as any).defaultProps = {
  defaultVisible: false,
  disabled: false,
  mode: 'default',
  selectable: true,
  size: 'normal',
  showArrow: true,
  autoClose: true,
};

(SelectMemo as any).displayName = 'Select';

SelectMemo.Option = SelectOption;
SelectMemo.OptionGroup = SelectOptionGroup;

export { SelectMemo as Select };
