/* eslint-disable max-lines-per-function */
import React, { useCallback, useState } from 'react';
import useControlledValue from '~/hooks/useControlledValue';
import { useTranslate } from '~/locale/locales';
import { SelectNormal, XDownNormal, XUpNormal } from '../icons';
import { SplitButton, ButtonSize } from '../button';
import { Tooltip } from '../tooltip';
import { OverlayWrap, OverlayItem } from './styled';
import { TOptionTitle } from '../select/interface';
import { getTitleContent, getTitlePlacement } from '../select/SelectOption';

type ScriptType = 'super' | 'sub';

export interface IScriptSelectProps {
  /**
   * 默认的上标或者下标
   */
  defaultValue?: ScriptType;
  /**
   * 上标或者下标
   */
  value?: ScriptType;
  /**
   * 设置国际化语言
   */
  locale?: string;
  /**
   * 样式
   */
  style?: React.CSSProperties;
  /**
   * 类名
   */
  className?: string;
  /**
   * 是否禁用
   */
  disabled?: boolean;
  /**
   * 点击的回调函数
   */
  onClick?: (value: ScriptType, event?: React.MouseEvent) => void;
  /**
   * tooltip提示
   */
  title?: string | React.ReactNode;
  /**
   * 选项的tooltip提示
   */
  titles?: Record<string, TOptionTitle>;
  /**
   * button 的大小
   */
  size?: ButtonSize;
}

const ScriptSelect: React.FC<IScriptSelectProps> = (props) => {
  const { locale, style, onClick, className, title, titles, disabled, size } = props;
  const [value, setValue] = useControlledValue(props.value, props.defaultValue);
  const [active, setActive] = useState(false);
  const t = useTranslate(locale);

  /**
   * 点击上下标操作
   */
  const handleClickLeft = useCallback((event: React.MouseEvent) => {
    if (onClick) {
      onClick(value!, event);
    }
  }, [value, onClick]);

  const isSuper = value === 'super';
  const superText = t('we_ui_script_superscript'); // 上标文案
  const subText = t('we_ui_script_subscript'); // 下标文案

  const handleChooseScript = useCallback((script: ScriptType): void => {
    if (onClick) {
      onClick(script);
    }
    setValue(script);
  }, [onClick, setValue]);

  const handleChooseSuper = useCallback(() => {
    if (value !== 'super') {
      handleChooseScript('super');
      setActive(false);
    }
  }, [value, handleChooseScript]);

  const handleChooseSub = useCallback(() => {
    if (value !== 'sub') {
      handleChooseScript('sub');
      setActive(false);
    }
  }, [value, handleChooseScript]);

  const chooses = [
    {
      title: superText,
      value: 'super',
      Icon: XUpNormal,
      onClick: handleChooseSuper,
    },
    {
      title: subText,
      value: 'sub',
      Icon: XDownNormal,
      onClick: handleChooseSub,
    },
  ];

  /**
   * 下拉选项
   */
  const DropContent = (
    <OverlayWrap>
      {
        chooses.map((item) => (
          <Tooltip
            title={getTitleContent(titles![item.value])}
            placement={getTitlePlacement(titles![item.value])}
            key={item.value}
          >
            <OverlayItem
              className={value === item.value ? 'selected' : ''}
              onClick={item.onClick}
              data-testid={`option-${item.value}`}
            >
              <item.Icon className="script-icon" />
              <span>{item.title}</span>
              {
                value === item.value && <SelectNormal className="check-icon" />
              }
            </OverlayItem>
          </Tooltip>
        ))
      }
    </OverlayWrap>
  );

  return (
    <SplitButton
      overlay={DropContent}
      onClickLeft={handleClickLeft}
      onActiveChange={setActive}
      style={style}
      size={size}
      active={active}
      className={className}
      disabled={disabled}
      title={title || (isSuper ? superText : subText)}
    >
      {isSuper ? <XUpNormal /> : <XDownNormal />}
    </SplitButton>
  );
};

ScriptSelect.defaultProps = {
  defaultValue: 'super',
  titles: {},
};

export default ScriptSelect;
