import styled from 'styled-components';
import { Ellipsis, resetComponent } from '~/utils/styled.mixin';
import { Button } from '../button';
import { withTheme } from '../theme';

export const AlertRoot = withTheme(styled.div`
  ${resetComponent}
  width: 100%;
  overflow: hidden;

  &.hidden {
    display: none;
  }

  .type-icon {
    font-size: ${({ theme }) => theme.ui.fontSizeLarge};
    margin-right: ${({ theme }) => theme.ui.marginMedium};
  }

  .close-icon {
    margin-top: 2px;
    color: ${({ theme }) => theme.ui.colorText3};
    cursor: pointer;
  }

  &.close-mode {
    .close-icon {
      margin-top: 2px;
      margin-left: ${({ theme }) => theme.ui.margin};
    }
  }

  &.action-mode {
    .close-icon {
      display: none;
    }
  }


  &.info {
    border-color: ${({ theme }) => theme.ui.primaryColor};
    background-color: ${({ theme }) => theme.ui.primaryColor2};
    .type-icon {
      color: ${({ theme }) => theme.ui.primaryColor};
    }
  }

  &.error {
    border-color: ${({ theme }) => theme.ui.errorColor};
    background-color: ${({ theme }) => theme.ui.errorColor2};
    .type-icon {
      color: ${({ theme }) => theme.ui.errorColor};
    }
  }

  &.warning {
    border-color: ${({ theme }) => theme.ui.warningColor};
    background-color: ${({ theme }) => theme.ui.warningColor2};
    .type-icon {
      color: ${({ theme }) => theme.ui.warningColor};
    }
  }
`);

export const AlertWrap = withTheme(styled.div<any>`
  display: flex;
  align-items: flex-start;
  padding: ${({ theme }) => theme.ui.alertVerticalPadding} ${({ theme }) => theme.ui.alertHorizontalPadding};
  line-height: 1.6;
  font-size: ${({ theme }) => theme.ui.fontSizeSmall};
  color: ${({ theme }) => theme.ui.colorText5};
`);

export const MessageWrap = withTheme(styled.div`
  flex: 1 auto;
  width: 0;
  display: flex;
  align-items: center;
  flex-direction: column;
`);

export const MessageContent = withTheme(styled.div<any>`
  display: flex;
  align-items: center;
  height: ${({ theme }) => theme.ui.elementHeightSmall};
  width: 100%;
  font-size: ${({ size, theme }) => (size === 'large' ? theme.ui.fontSize : theme.ui.fontSizeSmall)};
`);

export const MessageText = withTheme(styled.div`
  ${Ellipsis}
  flex-shrink: 1;
`);


export const DescriptionContent = withTheme(styled.div<any>`
  display: flex;
  align-items: center;
  width: 100%;
  height: ${({ theme }) => theme.ui.elementHeightSmall};
  font-size: ${({ theme }) => theme.ui.fontSizeSmall};
`);

export const DescriptionText = withTheme(styled.div`
  ${Ellipsis}
`);


export const TextButton = styled(Button)`
  font-size: 12px;
  cursor: pointer;
  padding: 0 4px 0 4px;
  display: flex;
  align-items: center;
  justify-content: center;
`;

export const IconWrap = withTheme(styled.div`
  height: ${({ theme }) => theme.ui.elementHeightSmall};
  display: flex;
  align-items: center;
`);

export const ActionWrap = withTheme(styled(TextButton)`
  height: ${({ theme }) => theme.ui.elementHeightSmall};
  color: ${({ theme }) => theme.ui.linkHoverColor};
  margin-left: ${({ theme }) => theme.ui.margin};
  margin-right: ${({ theme }) => theme.ui.margin};
  cursor: pointer;
  &:hover {
    color: ${({ theme }) => theme.ui.linkPressColor};
  }
`);
export const CloseButton = withTheme(styled(TextButton)`
  color: ${({ theme }) => theme.ui.colorText3};
  width: 24px;
  height: ${({ theme }) => theme.ui.elementHeightSmall};
  margin-left: 0;
  color: #0089ff;
  &:hover,
  &:focus {
    color: ${({ theme }) => theme.ui.colorText1};
  }

  &.active {
    color: ${({ theme }) => theme.ui.colorText1};
  }
`);















