/* eslint-disable arrow-body-style */
/* eslint-disable @typescript-eslint/explicit-function-return-type */
/* eslint-disable @typescript-eslint/no-explicit-any */
import styled from 'styled-components';
import { resetComponent } from '~/utils/styled.mixin';
import { withTheme } from '../theme';

export const CheckboxInner: any = withTheme(styled.span<any>`
  ${resetComponent}
  display: inline-block;
  vertical-align: middle;
  width: 16px;
  height: 16px;
  border: 1px solid ${({ theme }) => theme.ui.borderColor};
  background-color: ${({ theme }) => theme.ui.colorGray1};
  transition: all .24s ease-in;
  border-radius: 3px;
  &.disabled {
    background-color: ${({ theme }) => theme.ui.disabledBackgroundColor};
  }

  &:after {
    position: absolute;
    box-sizing: border-box;
    top: 51%;
    left: 28%;
    display: table;
    width: 5.71428571px;
    height: 9.14285714px;
    border: 2px solid ${({ theme }) => theme.ui.colorGray1};;
    border-top: 0;
    border-left: 0;
    transform: rotate(45deg) scale(0) translate(-50%,-50%);
    opacity: 0;
    transition: all .1s cubic-bezier(.71,-.46,.88,.6),opacity .1s;
    content: ' ';
  }
`);

export const CheckboxWrap = withTheme(styled.label<any>`
  ${resetComponent}
  cursor: ${({ disabled }) => (disabled ? 'not-allowed' : 'pointer')};

  ${({ disabled, theme }) => {
    return disabled ? '' : `
      &:hover {
        ${CheckboxInner} {
          border-color: ${theme.ui.primaryColor};
        }
      }
    `;
  }};

  ${({ checked, theme: { ui }, disabled }) => {
    return checked ? `
      ${CheckboxInner} {
        border-color: ${disabled ? ui.borderColor : ui.primaryColor};
        background-color: ${disabled ? ui.disabledBackgroundColor : ui.primaryColor};
        &:after {
          position: absolute;
          display: table;
          border: 2px solid ${disabled ? ui.disabledColor : ui.colorGray1};
          border-top: 0;
          border-left: 0;
          -webkit-transform: rotate(45deg) scale(1) translate(-50%,-50%);
          transform: rotate(45deg) scale(1) translate(-50%,-50%);
          opacity: 1;
          -webkit-transition: all .2s cubic-bezier(.12,.4,.29,1.46) .1s;
          transition: all .2s cubic-bezier(.12,.4,.29,1.46) .1s;
          content: ' ';
        }
      }
    ` : '';
  }}
`);

export const CheckboxText = withTheme(styled.span<any>`
  ${resetComponent}
  display: inline-block;
  vertical-align: middle;
  padding: 0 ${({ theme }) => theme.ui.padding};
`);

export const CheckboxContent = withTheme(styled.span<any>`
  ${resetComponent}
  position: relative;
  input {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1;
    width: 0;
    height: 0;
    cursor: pointer;
    opacity: 0;

    &:focus ~ ${CheckboxInner} {
      border-color: ${({ theme }: any) => theme.ui.primaryColor};
    }
  }
`);

export const CheckboxGroupWrap = withTheme(styled.div<any>`
  ${resetComponent}
`);
