import React, { useState, useCallback, useMemo } from 'react';
import { SplitButton, ButtonSize } from '../button';
import { IncreaseindentNormal, DecreaseindentNormal } from '../icons';
import styled from 'styled-components';
import { withTheme } from '../theme';
import { TOptionTitle } from '../select/interface';
import { Tooltip } from '../tooltip';
import { getTitlePlacement, getTitleContent } from '../select/SelectOption';
import { useTranslate } from '~/locale/locales';

export interface IIndentButtonProps {
  /**
   * 类名
   */
  className?: string;
  /**
   * 样式
   */
  style?: React.CSSProperties;
  /**
   * 是否禁用
   */
  disabled?: boolean;
  /**
   * 点击选项的回调函数
   */
  onSelect?: (e: 'increase' | 'decrease') => void;
  /**
   * 点击左侧的回调函数
   */
  onClick?: () => void;
  /**
   * tooltip提示
   */
  title?: string | React.ReactNode;
  /**
   * 选项的tooltip提示
   */
  titles?: Record<string, TOptionTitle>;
  /*
   * 国际化语言
   */
  locale?: string;
  /**
   * button 的大小
   */
  size?: ButtonSize;
}

const OverlayWrap = withTheme(styled.div`
  padding: ${({ theme }) => theme.ui.padding} 0;
`);

const OverlayItem = withTheme(styled.div`
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: ${({ theme }) => theme.ui.paddingSmall} 20px;
  margin: 0 ${({ theme }) => theme.ui.marginMedium};
  cursor: pointer;
  transition: all .1s ease-in-out;
  font-size: ${({ theme }) => theme.ui.fontSizeSmall};
  border-radius: ${({ theme }) => theme.ui.radiusLarge};
  height: 32px;
  box-sizing: border-box;

  &:hover {
    background-color: ${({ theme }) => theme.ui.backgroundColor};
  }

  .indent-icon {
    margin-right: 8px;
    color: ${({ theme }) => theme.ui.commonBaseColor2};
  }

`);

const TextSpan = styled.span``;

interface IOptions {
  title: string;
  Icon: React.ComponentType<any>;
  value: 'increase' | 'decrease';
}

export const IndentButton: React.FC<IIndentButtonProps> = (props) => {
  const { style, className, disabled, onSelect, onClick, title, titles, locale, size } = props;
  const [active, setActive] = useState(false);
  const t = useTranslate(locale);

  const options: IOptions[] = [
    {
      title: t('we_ui_indent_button_increase_indent'),
      Icon: IncreaseindentNormal,
      value: 'increase',
    },
    {
      title: t('we_ui_indent_button_decrease_indent'),
      Icon: DecreaseindentNormal,
      value: 'decrease',
    },
  ];

  const handleClickOption = useCallback((e: 'increase' | 'decrease') => {
    if (onSelect) onSelect(e);
  }, [onSelect]);

  const handleClickLeft = useCallback(() => {
    if (onClick) onClick();
  }, [onClick]);

  const getOverlay = useMemo(() => (
    <OverlayWrap>
      {options.map((item) => (
        <Tooltip
          key={item.value}
          title={getTitleContent(titles![item.value])}
          placement={getTitlePlacement(titles![item.value])}
        >
          <OverlayItem
            onClick={() => handleClickOption(item.value)}
            data-testid={`option-${item.value}`}
          >
            <item.Icon className="indent-icon" />
            <TextSpan>{item.title}</TextSpan>
          </OverlayItem>
        </Tooltip>
      ))}
    </OverlayWrap>
  ), [options, handleClickOption, titles]);

  return (
    <SplitButton
      overlay={getOverlay}
      onClickLeft={handleClickLeft}
      active={active}
      onActiveChange={setActive}
      className={className}
      disabled={disabled}
      style={style}
      testid="IndentButton"
      title={title}
      size={size}
    >
      <IncreaseindentNormal />
    </SplitButton>
  );
};

IndentButton.defaultProps = {
  titles: {},
};
