import React from 'react';
import warning from 'warning';
import { ToolBox, ToolButton } from './ToolBox';
import { ToolSubGroupWrap } from './styled';

export interface IToolLineProps {
  /**
  * 包裹的组件
  */
  children: any;
  /**
   * 是否为单行工具栏
   */
  isSingleLine?: boolean;
  /**
   * ToolSubGroupWrap 的样式
   */
  style?: React.CSSProperties;
}

export class ToolLine extends React.PureComponent<IToolLineProps> {
  static defaultProps = {
    isSingleLine: true,
  };

  constructor(props: IToolLineProps) {
    super(props);
    let hasIllegalChild = false;
    React.Children.forEach(props.children, (child) => {
      if (child.type !== ToolBox && child.type !== ToolButton) {
        hasIllegalChild = true;
      }
    });
    warning(!hasIllegalChild, 'Children of ToolLine must be ToolBox or ToolButton');
  }

  render() {
    const { children, isSingleLine, style } = this.props;
    return (
      <ToolSubGroupWrap isSingleLine={isSingleLine} style={style} >
        {children}
      </ToolSubGroupWrap>
    );
  }
}

export type ToolLineArray = Array<React.ReactElement<IToolLineProps>>;

export class HiddenToolLine extends ToolLine {
}
