/* eslint-disable max-lines-per-function */
import React, { useState, useCallback, useMemo } from 'react';
import { SplitButton, ISplitButtonProps } from '../button';
import { MergeNormal, MergeCellsNormal, SelectNormal } from '../icons';
import styled from 'styled-components';
import { withTheme } from '../theme';
import { useTranslate } from '~/locale/locales';
import { useControlledValue } from '~/hooks';
import { classnames } from '~/utils';
import { getTitlePlacement, getTitleContent } from '../select/SelectOption';
import { TOptionTitle } from '../select/interface';
import { Tooltip } from '../tooltip';
import { Ellipsis } from '~/utils/styled.mixin';

const TriggerWrap = styled.div`
  display: flex;
  align-items: center;
  .title {
    width: 70px;
    margin-left: 6px;
    ${Ellipsis};
  }
`;

export type CellMergeStatus = 'unmerged' | 'merged';

export interface IMergeCellButton extends Omit<Partial<ISplitButtonProps>, 'onClick'> {
  /**
   * 合并状态（受控）
   */
  status?: CellMergeStatus;
  /**
   * 默认的合并状态（非受控）
   */
  defaultStatus?: CellMergeStatus;
  /**
   * 点击选项的回调函数，回调的入参为选择的选项对应状态
   */
  onSelect?: (e: CellMergeStatus) => void;
  /**
   * 点击左侧的回调函数，回调的入参为单元格 **当前** 的状态
   */
  onClick?: (e: CellMergeStatus) => void;
  /**
   * tooltip提示
   */
  title?: string | React.ReactNode;
  /**
   * 选项的tooltip提示
   */
  titles?: Record<string, TOptionTitle>;
  /**
   * 国际化语言
   */
  locale?: string;
  /**
   * 是否显示字
   */
  showTitle?: boolean;
  /**
   * 是否禁用
   */
  disabled?: boolean;
}

const OverlayWrap = withTheme(styled.div`
  padding: ${({ theme }) => theme.ui.padding} 0;
`);

const OverlayItem = withTheme(styled.div`
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: ${({ theme }) => theme.ui.paddingSmall} 20px;
  margin: 0 ${({ theme }) => theme.ui.marginMedium};
  cursor: pointer;
  transition: all .1s ease-in-out;
  font-size: ${({ theme }) => theme.ui.fontSizeSmall};
  border-radius: ${({ theme }) => theme.ui.radiusLarge};
  height: 32px;
  box-sizing: border-box;

  &:hover {
    background-color: ${({ theme }) => theme.ui.backgroundColor};
  }

  &.disabled {
    color: ${({ theme }) => theme.ui.disabledColor};
    background-color: transparent;
    cursor: not-allowed;  }

  // .merge-icon {
  //   margin-right: 8px;
  //   color: ${({ theme }) => theme.ui.commonBaseColor2};
  // }

  .check-icon {
    opacity: 0;
    font-size: 12px;
    color: #3296fa;
    position: absolute;
    left: 4px;
    top: 50%;
    transform: translateY(-50%);  }

  .check-icon.actived {
    opacity: 1;
  }

`);

const TextSpan = styled.span``;

interface IOptions {
  title: string;
  Icon: React.ComponentType<any>;
  value: CellMergeStatus;
  handleClick: () => void;
}

export const MergeCellButton: React.FC<IMergeCellButton> = (props) => {
  const {
    className,
    onSelect,
    onClick,
    locale,
    defaultStatus = 'unmerged',
    title,
    titles = {},
    showTitle,
    disabled,
    ...restProps
  } = props;
  const [overlayActive, setOverlayActive] = useState(false);
  const [status, setStatus] = useControlledValue(props.status, defaultStatus);
  const t = useTranslate(locale);

  const toggle = (curr: CellMergeStatus | undefined) => ((curr === 'unmerged') ? 'merged' : 'unmerged');

  const handleClickMerged = () => {
    if (onSelect) {
      onSelect('merged');
    }
    setStatus(toggle);
    setOverlayActive(false);
  };

  const handleClickUnMerged = () => {
    if (onSelect) {
      onSelect('unmerged');
    }
    setStatus('unmerged');
    setOverlayActive(false);
  };

  const options: IOptions[] = [
    {
      title: t('we_ui_merge_cell_button_merge_cell'),
      Icon: MergeNormal,
      value: 'merged',
      handleClick: handleClickMerged,
    },
    {
      title: t('we_ui_merge_cell_button_split_cell'),
      Icon: MergeCellsNormal,
      value: 'unmerged',
      handleClick: handleClickUnMerged,
    },
  ];

  const anotherOption = options.find(o => o.value !== status);

  const handleClickLeft = useCallback(() => {
    if (onClick) {
      onClick(status!);
    }
    setStatus(curr => ((curr === 'merged') ? 'unmerged' : 'merged'));
  }, [onClick, status, setStatus]);

  const isMerged = status === 'merged';
  const getOverlay = useMemo(() => {
    return (
      <OverlayWrap>
        {options.map((item) => (
          <Tooltip
            key={item.value}
            title={getTitleContent(titles![item.value])}
            placement={getTitlePlacement(titles![item.value])}
          >
            <OverlayItem
              onClick={item.handleClick}
              data-testid={`option-${item.value}`}
              className={classnames({ disabled: status === 'unmerged' && item.value === 'unmerged' })}
            >
              {/* <item.Icon className="merge-icon" /> */}
              <TextSpan>{item.title}</TextSpan>
              {
              item.value === 'merged' && <SelectNormal className={classnames('check-icon', { actived: isMerged })} />
            }
            </OverlayItem>
          </Tooltip>
        ))}
      </OverlayWrap>
    );
  }, [options, status, isMerged, titles]);

  return (
    <SplitButton
      {...restProps}
      overlay={getOverlay}
      onClickLeft={handleClickLeft}
      active={overlayActive}
      onActiveChange={setOverlayActive}
      className={classnames(className, { leftSelected: isMerged })}
      testid="MergeCellButton"
      title={title}
      disabled={disabled}
    >
      <TriggerWrap className="we-ui-merge-cell-button-trigger">
        <MergeNormal />
        {anotherOption && showTitle && <span className="title">{anotherOption.title}</span>}
      </TriggerWrap>
    </SplitButton>
  );
};

