import React, { useCallback, useEffect, useRef } from 'react';
import { supportRef, composeRef } from '~/utils/ref';
import { findDOMNode } from '~/utils/dom';
import { ITooltipProps } from './interface';
import useSingleton from './useSingleton';

export const Tooltip: React.FC<ITooltipProps> = React.memo((props) => {
  const { zIndex, children, placement, title, disabled } = props;

  const triggerRef = useRef<React.ReactInstance | HTMLElement | null>(null);

  const child = React.Children.only(children) as React.ReactElement;

  const { onMouseEnter, onMouseLeave } = child.props;

  const singleton = useSingleton();

  useEffect(() => {
    if (singleton) {
      singleton.update({
        title,
        placement,
        zIndex,
        disabled: !!disabled,
      });
    }
  }, [title, placement, zIndex, singleton, disabled]);

  /**
   * 鼠标进入trigger区域
   *
   * @param {React.MouseEvent<HTMLElement>} e
   * @return {void}
   */
  const handleMouseEnter = useCallback((e: React.MouseEvent<HTMLElement>) => {
    if (onMouseEnter) {
      onMouseEnter(e);
    }
    if (singleton) {
      const triggerNode: HTMLElement | undefined = triggerRef.current
        // eslint-disable-next-line react/no-find-dom-node
        ? findDOMNode(triggerRef.current)
        : undefined;

      singleton.enter({
        title,
        placement,
        zIndex,
        triggerNode,
        disabled: !!disabled,
      });
    }
  }, [onMouseEnter, title, placement, zIndex, singleton, disabled]);

  /**
   * 鼠标离开trigger区域
   *
   * @param {React.MouseEvent<HTMLElement>} e
   * @return {void}
   */
  const handleMouseLeave = useCallback((e: React.MouseEvent<HTMLElement>) => {
    if (onMouseLeave) {
      onMouseLeave(e);
    }
    if (singleton) {
      singleton.leave();
    }
  }, [onMouseLeave, singleton]);

  const cloneProps: any = {};

  if (supportRef(child)) {
    cloneProps.ref = composeRef(triggerRef, (child as any).ref);
  }

  cloneProps.onMouseEnter = handleMouseEnter;
  cloneProps.onMouseLeave = handleMouseLeave;

  return title ? React.cloneElement(child, cloneProps) : child;
});

Tooltip.defaultProps = {
  placement: 'bottom',
  zIndex: 2000,
};
